class TransactionsListWidget extends React.Component {
  constructor(props) {
    super(props)
    this.state = {}
  }


  componentDidMount(){
  }

  formatNumber(num) {
    let newFor = num.toFixed(2);
    return (newFor.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,'));
}

  render() {

    if(this.props.dashboardTranscations && this.props.dashboardTranscations.length == 0) {

      return(
        <div className="dash-no-records">
          No Records Found
        </div>
      )

    } else {
      return (
        
        <Carousel
          className="dash-slide-transaction"
          interval={false}
          indicators={false}
          fade={true}
          controls={this.props.dashboardTranscations && this.props.dashboardTranscations.length <=1 ? false : true}
        >


{this.props.dashboardTranscations && this.props.dashboardTranscations.map(tran => {
  return <Carousel.Item onClick={() => { this.props.history.push("/transactions/detail/" + tran._id) }} key={tran._id} >
            <Card>
              <div className="dash-card-img">

              {tran.property.image.length <= 0 ?
                  <Card.Img variant="top" src={placeholderImage} />

                  :
                  <Card.Img variant="top" src={UrlConstants.AdminCreateTransactionUrl+"/upload?id="+ tran.ownerId + "&img="+ tran.property.image[0]} />

                  // <ImageLoader src={UrlConstants.AdminCreateTransactionUrl+"/upload?id="+ tran.ownerId + "&img="+ tran.property.image[0]}>
                  //     <img />
                  //     <img src={placeholderImage} />
                  //     <Preloader />
                  // </ImageLoader>
              }

                
              </div>
              <Card.Body className="p-3">
                <h4 className="font-20 mb-0 color-blue-3">
                { tran.property.street + (tran.property.street1 != '' ? (', ' + tran.property.street1 + ''): '') + " " +  tran.property.city}
                </h4>
                <h5 className="font-18 color-blue-3">{ (tran.property.state ? (tran.property.state + " ") : '') + tran.property.zipcode}</h5>
                <Card.Title className="font-24 font-600">
                  ${this.formatNumber(tran.agreedOpenPrice)}
                </Card.Title>
                <Row>
                  <Col>
                    <div style={{ lineHeight: '1.1em' }}>
                      <span class="color-grey-2 font-14 font-500">
                        Status:&nbsp;
                      </span>
                      <span class="color-light-blue-1 font-14 font-600">
                      {tran.status}
                      </span>
                    </div>
                    <div style={{ lineHeight: '1.1em' }}>
                      <span class="color-grey-2 font-14 font-500">
                        Updated on:&nbsp;
                      </span>
                      <span class="color-grey-1 font-14 font-500">
                        { moment(tran.modifiedDate).format('MM/DD/YYYY, hh:mm a') }
                      </span>
                    </div>
                  </Col>
                  <Col align="right" className="col-auto">
                    <div className="trans-progress-circle">
                      <span className="circle-val">20%</span>
                      {/* Foreground path */}
                      <CircularProgressbar
                        className="single-circle circle1"
                        value={10}
                        styles={buildStyles({
                          trailColor: 'transparent',
                          strokeLinecap: 'butt',
                          pathColor: '#45a751'
                        })}
                      />
                      {/* Foreground path */}
                      <CircularProgressbar
                        className="single-circle circle2"
                        value={20}
                        styles={buildStyles({
                          trailColor: 'transparent',
                          strokeLinecap: 'butt',
                          pathColor: '#e88741'
                        })}
                      />
                      {/* Foreground path */}
                      <CircularProgressbar
                        className="single-circle circle3"
                        value={100}
                        styles={buildStyles({
                          trailColor: 'transparent',
                          strokeLinecap: 'butt',
                          pathColor: '#ebebeb'
                        })}
                      />
                    </div>
                  </Col>
                </Row>
              </Card.Body>
            </Card>
    </Carousel.Item>
}) }

         

        </Carousel>
  
)
    }


    
  }
}

