import React from "react";

import ReactGantt from 'gantt-for-react';
import TaskDetail from './task-detail';

import $ from 'jquery'
import moment from 'moment';

import { Button, Col, Dropdown, Modal, Row } from "react-bootstrap";
import noRecord from './../../../assets/img/no-record.png';

import { connect } from 'react-redux';
import { getAllTasksAction, getTaskDetailsAction, updateTaskAction, updateDepedentTaskAction } from './../../../actions/TransactionActions'


class TimelineComponent extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      viewMode: 'Day',
      // tasks: this.getTasks(),
      editTask: "",
      tasks: [],
      showLoader: false,
      //editTask : "",
      taskId: '',
      OpenTaskDetail: false,
      moveConfirmationModal: false,
      affectedTasks: [],
      moveTaskPostData: {},
      clickedTaskId: '',
      reloadPage: false
    };
    this.onDateChange = this.onDateChange.bind(this)
    this.onTaskClick = this.onTaskClick.bind(this)
    this.customPopupHtml = this.customPopupHtml.bind(this)
  }

  componentWillMount() {
    // window.setInterval(function () {
    //   this.setState({
    //     viewMode: ['Quarter Day', 'Half Day', 'Day', 'Week', 'Month'][parseInt(Math.random() * 5 + 1) - 1],
    //     tasks: this.getTasks().slice(0, parseInt(Math.random() * 4 + 1))
    //   });
    // }.bind(this), 5000)


    this.setState({ showLoader: true })

    this.props.getAllTasksAction(this.props.transactionId).then(res => {

      if (this.props.tasks !== undefined && this.props.tasks !== null) {

        let taskData = this.props.tasks.data;

        this.setState({
          tasks: this.formatTasks(taskData),
          originalTasks: taskData,
          showLoader: false
        });
      }

      setTimeout(function () {
        $(".gannt-timeline.body .bar-milestone").each(function () {
          let getTextWidth = $(this)[0].getBoundingClientRect().width;
          // console.log('getTextWidth', getTextWidth);
          $(this).find('.bar').width(getTextWidth + 10);
        });
      }, 50);

    });

  }

  componentDidMount() {
    setTimeout(function () {
      $(".gannt-timeline.body .bar-milestone").each(function () {
        let getTextWidth = $(this)[0].getBoundingClientRect().width;
        // console.log('getTextWidth', getTextWidth);
        $(this).find('.bar').width(getTextWidth + 10);
      });
    }, 500);
  }

  formatTasks(allTask) {
    let tasks = allTask.map(function (task, i) {

      let d = new Date(task.eventStartDate)
      let sDate = d.setDate(d.getDate() - 1);

      let taskStsclass = "bar-milestone-scheduled";

      if (task.status == 'SCHEDULED') {
        taskStsclass = "bar-milestone-scheduled";
      }

      if (task.status == 'COMPLETED') {
        taskStsclass = "bar-milestone-completed";
      }

      if (task.overDue) {
        taskStsclass = "bar-milestone-overdue";
      }

      return {
        start: new Date(task.eventEndDate),
        end: new Date(task.eventEndDate),
        name: task.title,
        id: task._id,
        progress: 100,
        taskData: task,
        dependencies: task.predecessor,
        custom_class: "bar-milestone " + taskStsclass
      }
    });
    return tasks;
  }


  getTasks = () => {
    let names = [
      ["TO DO for Agent, TC and/or Admin", [0, 7]],
      ["Auto email to Septic:", [1, 4]],
      ["Radon Inspector has to upload document", [3, 6]],
      ["Auto Text to Buyer’s 11:30 a.m.", [7, 7]],
      ["After SEPTIC Inspector has been picked: EMAIL to Client:", [8, 9]],
      ["Text to Inspector:", [10, 10]]
    ];

    let tasks = names.map(function (name, i) {
      let today = new Date();
      let start = new Date(today.getFullYear(), today.getMonth(), today.getDate());
      let end = new Date(today.getFullYear(), today.getMonth(), today.getDate());
      start.setDate(today.getDate() + name[1][0]);
      end.setDate(today.getDate() + name[1][1]);
      return {
        start: start,
        end: end,
        name: name[0],
        id: "Task " + i,
        progress: 100
      }
    });
    tasks[1].dependencies = "Task 0";
    tasks[2].dependencies = "Task 1, Task 0";
    tasks[3].dependencies = "Task 2";
    tasks[5].dependencies = "Task 4";

    tasks[0].custom_class = "bar-milestone";
    tasks[0].progress = 100;

    // console.log("tasks", tasks)
    return tasks;
  };

  templatesScroll() {
    let getBodyScroll = $('.gannt-timeline.body .gantt-container').scrollLeft();
    // console.log(getBodyScroll);
    $('.gannt-timeline.head .gantt-container').scrollLeft(getBodyScroll);
  }

  customPopupHtml(task) {

    // this.setState({ editTask: "open", taskId : task.id});

    setTimeout(function () {
      $(".gannt-timeline.body .bar-milestone").each(function () {
        let getTextWidth = $(this)[0].getBoundingClientRect().width;
        // console.log('getTextWidth', getTextWidth);
        $(this).find('.bar').width(getTextWidth + 10);
      });
    }, 20);

    // const end_date = task._end.format('MMM D');


  }

  onDateChange(task, start, end) {

    // console.log("task --- ",task)

    // let originalTask = task.taskData;
    // //date
    // let ed = moment(end).format('YYYY-MM-DD');
    // let et = moment(originalTask.eventEndDate).format("HH:mm");
    // let finalDate = moment(ed + ' ' + et);

    // originalTask.eventEndDate =  new Date(finalDate.format('YYYY-MM-DD HH:mm'));
    // originalTask.eventType = originalTask.type

    // console.log("updated",originalTask)

    // this.props.updateTaskAction(originalTask, task.id).then(res => {
    //     let affectedTasks = res.data || [];
    //   console.log("affectedTasks",affectedTasks.length)
    //     // if(affectedTasks.length > 0) {
    //     //   this.setState({
    //     //     affectedTasks : affectedTasks,
    //     //     moveConfirmationModal: true,
    //     //     moveTaskPostData : originalTask
    //     //   });
    //     // }
    //     // else {
    //     //   this.updateDependency(originalTask, task.id)
    //     // }        
    // });

  }

  onTaskClick(task) {

    this.setState({ taskId: "" });
    this.setState({ taskId: task.id, editTask: "open" })
  }

  deleteTask(id) {
    this.props.deleteTaskAction(id).then(res => {
      this.getAllTasks('delete');
    })
  }

  updateTasks(name) {
    if (name == 'update') {
      this.getAllTasks();
      this.setState({ editTask: "", taskId: '' })
    }
    setTimeout(function () {
      $(".gannt-timeline.body .bar-milestone").each(function () {
        let getTextWidth = $(this)[0].getBoundingClientRect().width;
        // console.log('getTextWidth', getTextWidth);
        $(this).find('.bar').width(getTextWidth + 10);
      });
    }, 20);
  }


  getAllTasks() {
    this.props.getAllTasksAction(this.props.transactionId).then(res => {

      if (this.props.tasks !== undefined && this.props.tasks !== null) {

        let taskData = this.props.tasks.data;

        this.setState({
          tasks: this.formatTasks(taskData),
          originalTasks: taskData,
          showLoader: false,
          editTask: "",
          taskId: ''
        });

        setTimeout(function () {
          $(".gannt-timeline.body .bar-milestone").each(function () {
            let getTextWidth = $(this)[0].getBoundingClientRect().width;
            // console.log('getTextWidth', getTextWidth);
            $(this).find('.bar').width(getTextWidth + 10);
          });
        }, 20);
      }
    });

  }


  confirmMove() {
    this.setState({ moveConfirmationModal: false, showLoader: true });
    this.updateDependency(this.state.moveTaskPostData, this.state.moveTaskPostData._id);
  }

  updateDependency(taskdata, id) {
    // alert('hello')
    this.props.updateDepedentTaskAction(id, taskdata).then((res) => {
      // alert("in")
      // this.getAllTasks();
      this.setState({ showLoader: false });
    });
  }


  render() {


    // console.log("tasks", this.state.tasks)

    return (
      <div className="gannt-timeline-wrapper">


        {this.state.showLoader ?
          <div className="page-loader">
            <div className="page-loader-inner">
              <div className="spinner-border" role="status">
                <span className="sr-only">Loading...</span>
              </div>
            </div>
          </div>
          :

          <div>

          {this.state.tasks.length <= 0 ? 
          <div className="no-transaction no-record">
              <div className="no-transaction-inner">
                  <img src={noRecord} alt="no record"/>
                  <p>No task found</p>
              </div>
          </div>
        
        :

          <div>

            {this.state.tasks.length > 0 &&
              <div className="gannt-timeline head">
                <ReactGantt tasks={this.state.tasks}
                  viewMode={this.state.viewMode}
                  customPopupHtml={this.customPopupHtml}
                  scrollOffsets={this.state.scrollOffsets}
                />
              </div>
            }

            {this.state.tasks.length > 0 &&
              <div>
                <div className="gannt-timeline body" onScroll={() => this.templatesScroll()}>
                  <ReactGantt tasks={this.state.tasks}
                    viewMode={this.state.viewMode}
                    customPopupHtml={this.customPopupHtml}
                    scrollOffsets={this.state.scrollOffsets}
                    onDateChange={this.onDateChange}
                    onClick={this.onTaskClick}
                  />
                </div>

                <div className={this.state.editTask + "  task-single calendar-add-task"}>

                  {/* Task Detail in Mobile */}
                  {this.state.taskId != "" &&
                    <TaskDetail taskId={this.state.taskId}
                      deleteTask={(id) => { this.deleteTask(id) }}
                      onUpdate={(e) => this.updateTasks(e)}
                      transactionData={this.props.transactionData}
                      reloadPage={() => this.getAllTasks()}
                      fromTimeline={true}
                      closeDetailsModal={() => this.setState({ editTask: "" })}
                    />
                  }

                </div>
              </div>
            }

          </div>
        
        }
        </div>
        }


        <Modal
          show={this.state.moveConfirmationModal}
          // onHide={() => this.setState({ moveConfirmationModal : false})}
          dialogClassName="modal-90w"
          aria-labelledby="example-custom-modal-styling-title"
          className="modal-med modal-400 modal-delete"
          centered
          backdrop={false}
        >
          <Modal.Body className="pb-0 text-center">
            <p>Are you sure you move this task?</p>
            here is the list of task that will be affected :
								{this.state.affectedTasks.map((e, i) => {
              if (e) {
                return <li key={e.title} > Day - {e.day} {e.title}</li>
              }
            })}
          </Modal.Body>
          <Modal.Footer>
            <Button variant='primary' className="btn-blue btn-outlined w-140" onClick={() => { this.setState({ moveConfirmationModal: false }) }}>Cancel</Button>
            <Button variant='primary' className="btn-blue w-140" onClick={() => { this.confirmMove() }}>Yes</Button>
          </Modal.Footer>
        </Modal>

      </div>
    );
  }
}


// export default TimelineComponent;


const mapStateToProps = state => {
  return {
    tasks: state.reducer.tasks,
  };
};

export default connect(mapStateToProps, {
  getAllTasksAction,
  getTaskDetailsAction,
  updateTaskAction,
  updateDepedentTaskAction
})(TimelineComponent);
