    updateAssigneeOptions(con, setFieldValue) {
        let assigneeOptions = [];
        this.state.variablesRefs.forEach(v => {
            if ((v.contingency === 'all' || v.contingency === con) && v.account === 'user') {
                assigneeOptions.push({ label: v.name, value: v.name });
            }
        })
        this.setState({
            assigneeOptions
        }, () => {
            setFieldValue('assignee', '')
        })
    }

    getAllTasks() {
        this.getVariables();
        this.props.getTaskDetailsAction(this.props.taskId).then(res => {
            if (res) {
                let assigneeOptions = [];
                this.state.variablesRefs.forEach(v => {
                    if ((v.contingency === 'all' || v.contingency === res.contingency) && v.account === 'user') {
                        assigneeOptions.push({ label: v.name, value: v.name });
                    }
                })
                this.setState({
                    assigneeOptions
                })
                var comments = res.comments || []
                this.setState({ comments });
                var commStr = '';
                res.communicationTo.forEach((e, i, v) => {
                    if (e.indexOf('@') == 0) {
                        commStr += "{(" + e + ')}' + (i < v.length - 1 ? ',' : '')
                    } else {
                        commStr += e + (i < v.length - 1 ? ',' : '')
                    }
                })
                // (res.communicationTo[0] + '').split(',').forEach((e, i, v) => {
                //     commStr += "{(" + e + ')}' + (i < v.length - 1 ? ',' : '')
                // })
                res.communicationTo[0] = commStr
                res.eventEndDate = new Date(res.eventEndDate)

                this.props.getActualValuesAction(this.props.taskId).then(res1 => {
                    var commStr1 = '';
                    res1.communicationTo.forEach((e, i, v) => {
                        // if(e.indexOf('@') == 0){
                        //     commStr += "{(" + e + ')}'+ (i < v.length - 1 ? ',' : '')
                        // }else {
                        if (e != '') {
                            commStr1 += e + (i < v.length - 1 ? ', ' : '')
                        }
                        // }
                    })
                    res1.communicationTo[0] = commStr1;
                    this.setState({ actualValues: res1, showLoader: false });
                })
                this.setState({ taskData: res, showLoader: false }, () => {
                    this.getEditorContent(this.state.taskData)
                });
                if (res.predecessorData.length > 0) {
                    this.setState({ dependencyIsActive: true });
                }
                this.showTaskType((res.type + '').toUpperCase())
                this.showText((res.communicationType + '').toUpperCase())
            } else {
                this.setState({ showLoader: false })
            }
        })
    }

