    getVariables() {
        this.setState({ showLoader: true })
        this.props.adminGetVarriableRefAction().then(res => {
            console.log(res)
            this.setState({ showLoader: false })
            var emailRefVar = [];
            var textRefVar = [];
            var AllRefVar = [];
            var AllRefVarForMapping = [];
            var AllRefVarForEditor = [];
            var variablesRefs = this.props.variablesRefs || []

            for (var i = 0, len = variablesRefs.length; i < len; i++) {

                AllRefVar.push({ id: variablesRefs[i].name, display: variablesRefs[i].name })

                AllRefVarForMapping.push(variablesRefs[i].name)

                AllRefVarForEditor.push({
                    text: variablesRefs[i].name,
                    value: variablesRefs[i].name.replace("@", ''),
                    url: ''
                });


                if (variablesRefs[i].communicationtype === "EMAIL") {
                    emailRefVar.push({ id: variablesRefs[i].name, display: variablesRefs[i].name })
                }
                if (variablesRefs[i].communicationtype === "TEXT") {
                    // textRefVar.push(nextProps.variablesRefs[i])
                    textRefVar.push({ id: variablesRefs[i].name, display: variablesRefs[i].name })
                }
            }
            this.setState({ variablesRefs, emailRefVar: emailRefVar, textRefVar: textRefVar, AllRefVar: AllRefVar, AllRefVarForEditor: AllRefVarForEditor, AllRefVarForMapping: AllRefVarForMapping })
        });
    }

    getEditorContent(values) {
        var emailTextValForEditor = EditorState.createEmpty();
        // var actualEmailTextValForEditor = EditorState.createEmpty();
        var detailsValForEditor = EditorState.createEmpty();
        let contentBlock = htmlToDraft(values.description && (values.description.length > 0 && values.description[0]));
        if (contentBlock) {
            const contentState = ContentState.createFromBlockArray(contentBlock.contentBlocks);
            emailTextValForEditor = EditorState.createWithContent(contentState);
        }

        if (values.type.toUpperCase() === "INTERACTIVE") {
            let contentBlock = htmlToDraft(values.description && (values.description.length > 0 && values.description[0]));
            if (contentBlock) {
                const contentState = ContentState.createFromBlockArray(contentBlock.contentBlocks);
                detailsValForEditor = EditorState.createWithContent(contentState);
            }
        }


        // let contentBlock1 = htmlToDraft(this.state.actualValues.description && (this.state.actualValues.description.length > 0 && this.state.actualValues.description[0]));
        // if (contentBlock1) {
        //     const contentState = ContentState.createFromBlockArray(contentBlock.contentBlocks);
        //     actualEmailTextValForEditor = EditorState.createWithContent(contentState);
        // }
        this.setState({ emailTextValForEditor, detailsValForEditor });
    }

    updateTask(values, setFieldValue, field, value, edit) {

        this.myRef.current.setSubmitting();
        this.submitMyForm();

        var val = values;
        val['valueChanged'] = field
        if (field == 'communicationTo' || field == 'communicationSubject') {
            // if ((value[0].match(/@/g) || []).length < 2) {
            setFieldValue(field, value, true);
            val[field] = value;

            // }
            if (edit == 'save') {
                if (Object.keys(this.myRef.current.state.errors).length <= 0) {
                    this.handleSubmit(val);
                }
            }
        } else if (field == 'description') {
            if (edit == 'save') {
                if (Object.keys(this.myRef.current.state.errors).length <= 0) {
                    this.handleSubmit(val);
                }
            }
        } else if (field == 'status') {
            val[field] = value;
            if (this.myRef.current.state.values.document == true) {
                if (this.myRef.current.state.values.documents.length > 0) {
                    setFieldValue(field, value, true);
                    if (Object.keys(this.myRef.current.state.errors).length <= 0) {
                        this.handleSubmit(val);
                    }
                }
            } else {
                setFieldValue(field, value, true);
                if (Object.keys(this.myRef.current.state.errors).length <= 0) {
                    this.handleSubmit(val);
                }
            }
        } else {
            val[field] = value;
            setFieldValue(field, value, true);
            if (Object.keys(this.myRef.current.state.errors).length <= 0) {
                this.handleSubmit(val);
            }
        }


    }

