    handleSubmit(values) {
        var val = values;
        // delete val['document']
        val.eventType = val.type;
        // var newStr = myStr.replace(/\{\(/g, "");
        // newStr = newStr.replace(/\}/g, "");
        // newStr = newStr.replace(/\)\}/g, "");
        var commToVal = [];
        if (val.communicationTo) {
            (val.communicationTo[0] + '').split(',').forEach(e => {
                var data = e.replace(/\{\(/g, "").replace(/\)\}/g, "") || '';
                commToVal.push(data.trim())
            })
        }
        var commSubjectVal = [];
        if (val.communicationSubject) {
            commSubjectVal.push((val.communicationSubject[0] + '').replace(/\{\(/g, "").replace(/\)\}/g, "") || '')
        }
        val.communicationTo = commToVal;
        val.communicationSubject = commSubjectVal;
        val['time'] = (moment(val.eventEndDate).hour() * 60 + moment(val.eventEndDate).minute())
        // this.setState({ showLoader: true })

        delete val['eventLogs']
        delete val['commentsLogs']
        this.props.updateTaskAction(val, this.state.taskData._id).then(res => {
            if (res != undefined && res.length > 0) {
                var affectedTasks = res || [];
                this.setState({
                    moveConfirmationModal: true,
                    affectedTasks,
                    moveTaskPostData: val
                })
            } else {
                if (res != undefined && res.hasOwnProperty('message')) {
                    this.setState({ moveErrorModal: true, moveErrorMessage: res.message })
                } else {
                    this.setState({ showLoader: true })
                    this.props.updateDepedentTaskAction(this.state.taskData._id, val).then((res) => {
                        this.setState({ showLoader: false });
                        this.getAllTasks();
                        // this.setState({ showLoader: false })
                        this.props.onUpdate("update")
                    });
                }

            }
            //  else{
            // 	this.setState({showLoader: true})
            // 	this.props.adminUpdateDependentTasksAction(postData, "both", 0, data.eventtemplateId, data.commId, type).then(res=>{
            // 		this.setState({showLoader: false})
            // 		this.changeCampaign(this.state.currentCampian._id);
            // 	});
            // }
            // this.getAllTasks();
            // this.setState({ showLoader: false })
            // this.props.onUpdate("update")
        })


    }

    handleDateChange(date, type, setFieldValue, values) {
        var dateStr = moment().format('YYYY-MM-DD');
        if (date) {
            dateStr = moment(date).format('YYYY-MM-DD');
            var timeStr = moment(date).format('HH:mm');
        } else {
            dateStr = moment().format('YYYY-MM-DD');
            timeStr = moment().format('HH:mm');
        }



        if (type == 'time') {
            dateStr = moment(values.eventEndDate).format('YYYY-MM-DD')
        }

        var finalDate = moment(dateStr + ' ' + timeStr);

        // setFieldValue('eventEndDate', new Date(finalDate.format('YYYY-MM-DD HH:mm')))
        this.updateTask(values, setFieldValue, 'eventEndDate', new Date(finalDate.format('YYYY-MM-DD HH:mm')));
    }

