    showPreviewModal(name) {
        if (name) {
            var url = '/#'
            fetch(UrlConstants.AdminCreateTransactionUrl + '/' + name + '?id=' + this.props.transactionData._id, { method: 'GET' }).then(response => response.json()).then(res => {
                url = res.url
                this.setState({ showPreview: true, docName: name, previewURL: url })
            });
        }

    }
    getType(name) {
        var type = '';
        var ext = ((name.match(/(\.\w+$)/igm) || [])[0] + '').toLowerCase();
        if (ext == ".jpg" || ext == ".jpeg" || ext == ".png" || ext == ".gif") {
            type = 'image'
        } else if (name.match(/.(pdf)$/i)) {
            type = 'pdf'
        }
        return type
    }

