//All action about user defined 

import * as types from './actions';

import APIUtil from '../config/APIUtil';
import UrlConstants from '../config/UrlConstants';

var _ = require('lodash');


/**
 * @desc Transactions - get All
 */
export function getVendorTransactionsAction() {  

    let vendorId = localStorage.getItem("ID")
   
    let url = UrlConstants.vendorTransactionsUrl;
    url = url.replace("{id}",vendorId);

    return function (dispatch){
      return APIUtil.getMethod(url, true).then(response => {
          // dispatch
          if(response.status == 200) {
              dispatch( {
                  type: types.GET_ALL_VENDOR_TRANSACTIONS,
                  vendorTransactions: response.data
              });
          }
          else {
              dispatch( {
                  type: types.GET_ALL_VENDOR_TRANSACTIONS,
                  vendorTransactions: []
              });
          }
      });
    }  
}

export function getVendorTasksAction(transactionId) {  

    let vendorId = localStorage.getItem("ID")
    let url = UrlConstants.vendorTasksUrl;
    url = url.replace("{transactionId}",transactionId);
    url = url.replace("{vendorId}",vendorId);

    return function (dispatch){
      return APIUtil.getMethod(url, true).then(response => {
          // dispatch
          if(response.status == 200) {
              dispatch( {
                  type: types.GET_ALL_VENDOR_TASKS,
                  vendorTasks: response.data
              });
          }
          else {
              dispatch( {
                  type: types.GET_ALL_VENDOR_TASKS,
                  vendorTasks: []
              });
          }
      });
    }  
}
