//All action about user defined 

import * as types from './actions';

import APIUtil from '../config/APIUtil';
import UrlConstants from '../config/UrlConstants';
var _ = require('lodash');
/*
 * action creators
 */
/**
 * 
 * @desc Darshboard transactions data api call
 */
export function transactionsAction() {

    return function (dispatch){
        return APIUtil.getMethod(UrlConstants.DashboardTransactionsUrl, true).then(response => {
            if(response.status === 200) {
                dispatch( {
                    type: types.DASHBOARD_TRANSACTIONS,
                    dashboardTranscations : response.data.transactions,
                    countTransactions : response.data.totalTransactions, 
                });
            }
            else {
                dispatch( {
                    type: types.DASHBOARD_TRANSACTIONS,
                    dashboardTranscations : [],
                    countTransactions: []
                });
            }
            return response;          
        });
    } 
}
/**
 * @desc Dashboard Upcoming Tasks - get All
 */
export function getDashboardTasksWidgetAction() {

    let url = UrlConstants.DashboardwidgetFourUrl;

    return function (dispatch){
      return APIUtil.getMethod(url, true).then(response => {
          // dispatch
          if(response.status == 200) {
              dispatch( {
                  type: types.GET_UPCOMING_TASKS_DASHBOARD,
                  upcomingTasksData: response.data
              });
          }
          else {
              dispatch( {
                  type: types.GET_UPCOMING_TASKS_DASHBOARD,
                  upcomingTasksData: []
              });
          }
          return response.data || {}
      });
    }  
}

/**
 * @desc Dashboard - get Transactions values
 */
export function getTransactionValuesWidgetDataAction() {

    let url = UrlConstants.TransactionValuesWidgetDataUrl;

    return function (dispatch){
      return APIUtil.getMethod(url, true).then(response => {
          // dispatch
          if(response.status == 200) {
              dispatch( {
                  type: types.DASHBOARD_TRANSACTIONS_VALUE,
                  valuesData: response.data
              });
          }
          else {
              dispatch( {
                  type: types.DASHBOARD_TRANSACTIONS_VALUE,
                  valuesData: []
              });
          }
          return response.data || {};
      });
    }  
}
