//All action about user defined 

import * as types from './actions';

import APIUtil from '../config/APIUtil';
import UrlConstants from '../config/UrlConstants';
import { relativeTimeRounding } from 'moment';

/*
 * action creators
 */

/**
 *  
 * @desc List Buyer Agents API call
 */
export function getBuyerAgentMasterTemplateAction() {  
    return function (dispatch){
      return APIUtil.getMethod(UrlConstants.buyerAgentMasterTemplate,true).then(response => {
        
          if(response.status === 200) {
              dispatch( {
                    type: types.BUYER_GET_MASTER_TEMPLATE_DATA,
                    buyerMasterTemplates: response.data.data,
                    totalBuyerTemplates : response.data.total
              });
          }
          else {
              dispatch( {
                  type: types.BUYER_GET_MASTER_TEMPLATE_DATA,
                  buyerMasterTemplates: {},
                  totalBuyerTemplates : 0
              });
          }
      });
    }  
}
/**
 * 
 * @desc Update master template data API call
 */
export function updateTaskAction(data,model,campaignId, eventTempId, commuId, type) {  
    return function (dispatch){
      return APIUtil.putMethod(UrlConstants.buyerAgentMasterTemplate + "?id="+ eventTempId + model , data ,true).then(response => {
        
          if(response.status === 200) {
              dispatch( {
                    type: types.BUYER_UPDATE_MASTER_TEMPLATE_DATA,
                    isMasterTemplateUpdatedBuyer : true,
                    updatedEventDataBuyer : response.data
              });
              return response.data
          }
          else {
              dispatch( {
                  type: types.BUYER_UPDATE_MASTER_TEMPLATE_DATA,
                  isMasterTemplateUpdatedBuyer : false,
                  updatedEventDataBuyer : []
              });
              return response.data
          }
      });
    }  
}

