/**
 * 
 * @desc Delete Communication template
 */
export function deleteTaskAction(eventtemplateId) {  
    return function (dispatch){

        var url = UrlConstants.buyerAgentMasterTemplate+ '?id='+ eventtemplateId;
        return APIUtil.deleteMethod(url,true).then(response => {
          if(response.status === 200) {
              dispatch( {
                    type: types.BUYER_DELETE_TASK,
                    isMasterTemplateTaskDeleted: true,
              });
          }
          else {
              dispatch( {
                  type: types.BUYER_DELETE_TASK,
                  isMasterTemplateTaskDeleted: false,
              });
          }
        });
    }  
}


/**
 * 
 * @desc Get list of variables
 */
export function getTasksByCampaignIdAction(id) {  

    // let url = UrlConstants.AdminGetCampDataByIdMaterTemplateUrl;
    // url = url.replace("{campId}",id);
    return function (dispatch){
      return APIUtil.getMethod(UrlConstants.buyerAgentMasterTemplate+"?campaign="+ id, true).then(response => {
          if(response.status === 200) {
              dispatch( {
                    type: types.MASTER_TEMPLATE_GET_CAMPAIGN_DATA_BY_ID_BUYER,
                    BuyerCampDataById : response.data.data,
              });
          }
          else {
              dispatch( {
                  type: types.MASTER_TEMPLATE_GET_CAMPAIGN_DATA_BY_ID_BUYER,
                  BuyerCampDataById : []
              });
          }
          return response.data;
      }, error => {
          return error;
      });
    }  
}


