/**
 * 
 * @desc Delete Communication template
 */
export function removeDependencyAction(id, removeId) {  
    return function (dispatch){

        var url = UrlConstants.removeDependencyBuyer.replace("{id}",id).replace("{removeId}",removeId);

        return APIUtil.deleteMethod(url,true).then(response => {
          if(response.status === 200) {
              dispatch( {
                    type: types.REMOVE_DEPENDENCY_BUYER
              });
          }
          else {
              dispatch( {
                  type: types.REMOVE_DEPENDENCY_BUYER,
              });
          }
        });
    }  
}