//All action about Documents

import * as types from './actions';

import APIUtil from '../config/APIUtil';
import UrlConstants from '../config/UrlConstants';

var _ = require('lodash');

/**
 *
 * @desc List Registration requests API call
 */

export function listDocAction(transactionId) {
	var url = UrlConstants.docsUrl.replace('{transactionId}', transactionId);
	if(localStorage.getItem('usertype') == 'VENDOR'){
		url = url + '&vendorId=' + localStorage.getItem('ID') + "&type=vendor"  
	} else {
		url = url + "&type=buyer"
	}

	return function(dispatch) {
		return APIUtil.getMethod(url, true).then(response => {
			// dispatch
			// console.log("sdaf",response)
			if (response.status === 200) {
				dispatch({
					type: types.GET_DOCS,
					docs: response.data.data,
					totalDocs: response.data.total
				});
				return response.data.data;
			} else {
				dispatch({
					type: types.GET_DOCS,
					docs: {}
				});
				return {};
			}
		});
	};
}

/**
 *
 * @desc add doc
 */
export function addDocAction(data) {
	return function(dispatch) {
		return APIUtil.postMethod(UrlConstants.addDocUrl, data, true).then(
			response => {
				if (response._id !== '') {
					dispatch({
						type: types.ADD_DOC,
						isDocAdded: true
					});
				} else {
					dispatch({
						type: types.ADD_DOC,
						isDocAdded: false
					});
				}
				return response;
			}
		);
	};
}


/**
 *
 * @desc update doc
 */
export function updateDocAction(data, id) {
	return function(dispatch) {
		return APIUtil.putMethod(UrlConstants.addDocUrl+'?id='+id, data, true).then(
			response => {
				if (response._id !== '') {
					dispatch({
						type: types.UPDATE_DOC,
						isDocAdded: true
					});
				} else {
					dispatch({
						type: types.UPDATE_DOC,
						isDocAdded: false
					});
				}
				return response;
			}
		);
	};
}

