/**
 * @desc Transactions - upload property image
 */
export function uploadDocumentAction(data, id) {  
    var url = UrlConstants.uploadDocUrl.replace('{transactionId}', id);
    return function (dispatch){
      return APIUtil.postFormDataMethod(url, data, true).then(response => {

          // dispatch
          if(response.status == 200) {
              dispatch( {
                  type: types.UPLOAD_DOC,
                  uploadSuccess: true 
			  });
			  return true;
          }
          else {
              dispatch( {
                  type: types.UPLOAD_DOC,
                  uploadSuccess: false
			  });
			  return false;
          }
      });
    }  
}

