import React from 'react';

// import pages
import HeaderLogin from "./../HeaderLogin/HeaderLogin.jsx";

// Bootstrap components
import { Card } from "react-bootstrap";

// icons
import { CheckCircle } from '@material-ui/icons';

class ThankYou extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      
    };
  }

  checkLoggedIn() {
    
    var userType = localStorage.getItem('usertype');
    if (userType !== null && userType.toUpperCase() === 'ADMIN') {
      this.props.history.push("/RegistrationRequests")
    } else {
      this.props.history.push("/transactions")
    }
  }

  render() {

    if (localStorage.getItem('accessToken')) {
      this.checkLoggedIn();
    }
    
    return (
      <div>
        <HeaderLogin/>
        <div className="thank-you-msg">
          <Card>
            <span><CheckCircle/></span>
            <div>Thanks for registering with us, Our team will review your application and you will be notified once your account is Approved.</div>
          </Card>
        </div>
      </div>
    );
  }

}


export default ThankYou;
