import React from "react";

// Bootstrap components 
import { Form, Row, Col } from "react-bootstrap";

import PropTypes from 'prop-types';
import validation from 'react-validation-mixin';
import strategy from 'joi-validation-strategy';
import Joi from 'joi';

import { StatesList } from './../../../Helper/StatesList'
import InputMask from 'react-input-mask';

class CompanyInfo extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
        name : props.getUserData().companyInformation.name,
        website : props.getUserData().companyInformation.website,
        address : props.getUserData().companyInformation.address,
        zipcode :  props.getUserData().companyInformation.zipcode,
        city : props.getUserData().companyInformation.city,
        state : props.getUserData().companyInformation.state,
        phoneNumber : props.getUserData().companyInformation.phoneNumber,
        faxNumber : props.getUserData().companyInformation.faxNumber,
        typeOfBusiness : props.getUserData().companyInformation.typeOfBusiness,
        companySize : props.getUserData().companyInformation.companySize,
    };


    this.validatorTypes = function() {
      var base = Joi.object().keys({         
          name : Joi.string().required().label("Company Name"),
          website : Joi.string().uri().label("Company Website"),
          address : Joi.string().required().label("Company Address"),
          zipcode : Joi.string().regex(new RegExp("^[0-9]{5}(?:-[0-9]{4})?$")).required(), //Joi.string().regex(new RegExp("^[0-9]{5}(?:-[0-9]{4})?$")).required(),
          city : Joi.string().required().label("City"),
          state : Joi.string().required().label("State"),
          phoneNumber : Joi.string().regex(new RegExp("^[0-9]{3}-[0-9]{3}-[0-9]{4}?$")).required().label("Company Phone No")
      });
      return base;
    };

  this.getValidatorData = this.getValidatorData.bind(this);
  this.renderHelpText = this.renderHelpText.bind(this);
  this.isValidated = this.isValidated.bind(this);

  }


  getValidatorData() {
    return {
      name : this.props.getUserData().companyInformation.name,
      website : this.props.getUserData().companyInformation.website,
      address : this.props.getUserData().companyInformation.address,
      zipcode : this.state.zipcode,
      city : this.props.getUserData().companyInformation.city,
      state : this.props.getUserData().companyInformation.state,
      phoneNumber : this.props.getUserData().companyInformation.phoneNumber,
      faxNumber : this.props.getUserData().companyInformation.faxNumber,
      typeOfBusiness : this.props.getUserData().companyInformation.typeOfBusiness,
      companySize : this.props.getUserData().companyInformation.companySize,
    }
};
isValidated() {
  
    return new Promise((resolve, reject) => {
      this.props.validate((error) => {
        //  console.log("er",error)
        if (error) {
          reject(); // form contains errors
          return false;
        }
        resolve();
        return true;
      });
    });
  }

  renderHelpText(message, id) {
      return (<div className="val-err-tooltip" key={id}><span>{message}</span></div>);
  }

  onChange (event) {
     
      if(event.target.name === "zipcode") {
        this.setState({zipcode : event.target.value});
        this.props.updateUserData("zipcode", event.target.value);
      } else {
        this.setState({[event.target.name] : event.target.value});
        this.props.updateUserData(event.target.name, event.target.value);
      }
     
  }

  getErrorMsg(field, errsList){
    // console.log('errsList',errsList)
    var err = '';
    if(field === "name" || field === 'address' || field === 'city' || field === 'state') {
        if(errsList[field] !== undefined && errsList[field] !== null && errsList[field] !== "") {
            if(field === "name") {
                err = 'Please enter Company Name'; 
            }
            if(field === "address") {
                err = 'Please enter Company Address';
            }
            if(field === "city") {
                err = 'Please enter City';
            }
            if(field === "state") {
                err = 'Please select State';
            }
        }
    }
    if(field === 'zipcode') {            
      if(errsList[field] !== undefined && errsList[field] !== null && errsList[field] !== "") {
          if(errsList[field].length === 2){
              err = 'Please enter Zip code';
          }
          if(errsList[field].length === 1){
              err = 'Please enter valid Zip code';
          }
      }
    }

    if(field === 'phoneNumber') {            
      if(errsList[field] !== undefined && errsList[field] !== null && errsList[field] !== "") {
          if(errsList[field].length === 2){
              err = 'Please enter Phone No';
          }
          if(errsList[field].length === 1){
              err = 'Phone No Must be of 10 digits';
          }
      }
    }

    if(field === 'website') {            
      if(errsList[field] !== undefined && errsList[field] !== null && errsList[field] !== "") {
          if(errsList[field].length === 1){
              err =  'Company Website must be a valid url';
          }
          if(errsList[field].length === 2){
              // err = 'Please enter Company Website';
              delete  errsList["website"];
          }
          
      }
    }
   
        return err;
  }

  beforeMaskedValueChange = (newState, oldState, userInput) => {
    var { value } = newState;
    var selection = newState.selection;
    var cursorPosition = selection ? selection.start : null;
 
    // keep minus if entered by user
    if (value.endsWith('-') && userInput !== '-' && !this.state.value.endsWith('-')) {
      if (cursorPosition === value.length) {
        cursorPosition--;
        selection = { start: cursorPosition, end: cursorPosition };
      }
      value = value.slice(0, -1);
    }
 
    return {
      value,
      selection
    };
  }

  render() {

    var stateDropDownValue = [];
    for (var key in StatesList) {        
        var opt = <option key={key} value={key}>{StatesList[key]}</option>;
        stateDropDownValue.push(opt);
    }  
    const zipdata = (this.state.zipcode.length <= 5?"99999":"99999-9999");
    return (
      <div className="step-content">
        <h4 className="title-blue mb-3">Company Information</h4>
        <Form className="form-sm">

          <Row>
            <Col xs={12}>
              <Form.Group controlId="">
                <Form.Label>Company Name<span className="text-danger">*</span></Form.Label>
                <Form.Control maxLength="50" defaultValue = {this.state.name}
                  onChange={this.onChange.bind(this)} name="name" type="text" size='lg' onBlur={this.props.handleValidation('name')} />

<Form.Text className="text-danger">
 {this.renderHelpText(this.getErrorMsg('name', this.props.errors))}
    </Form.Text>
              </Form.Group>
            </Col>
          </Row>

          <Row>
            <Col xs={12}>
              <Form.Group controlId="">
                <Form.Label>Company Website</Form.Label>
                <Form.Control maxLength="50" defaultValue = {this.state.website}
                  onChange={this.onChange.bind(this)} name="website" type="text" size='lg' />
                  <Form.Text className="text-danger">
 {this.renderHelpText(this.getErrorMsg('website', this.props.errors))}
    </Form.Text>
              </Form.Group>
            </Col>
          </Row>

          <Row>
            <Col xs={12}>
              <Form.Group controlId="">
                <Form.Label>Company Address<span className="text-danger">*</span></Form.Label>
                <Form.Control defaultValue = {this.state.address}
                  onChange={this.onChange.bind(this)} name="address" type="text" size='lg' onBlur={this.props.handleValidation('address')} />

<Form.Text className="text-danger">
 {this.renderHelpText(this.getErrorMsg('address', this.props.errors))}
    </Form.Text>
              </Form.Group>
            </Col>
          </Row>

          <Row>
            <Col xs={12} sm={4}>
              <Form.Group controlId="">
                <Form.Label>Zip Code<span className="text-danger">*</span></Form.Label>

                
                {/* <InputMask 
                    mask="99999-9999" 
                    maskChar=''
                    // className={`${classes.textField} c-textfield`}
                    formControlProps={{
                    fullWidth: true,
                  }}
                    onChange={this.onChange.bind(this)}
                    onBlur={this.props.handleValidation('zipcode')}
                    value={this.state.zipcode}
                    name="zipcode"
                    beforeMaskedValueChange={this.beforeMaskedValueChange} 
                    // error={ Boolean(this.props.errors.primaryAddress && this.props.errors.primaryAddress[0].postalcode)}
                  >
                  {(inputProps) =>
                  <Form.Control   {...inputProps}/>
                  // <TextField {...inputProps}/>
                  }
              </InputMask> */}

                <Form.Control maxLength="14" defaultValue = {this.state.zipcode}
                  onChange={this.onChange.bind(this)} name="zipcode" type="text" size='lg' onBlur={this.props.handleValidation('zipcode')} />

                  <Form.Text className="text-danger">
                  {this.renderHelpText(this.getErrorMsg('zipcode', this.props.errors))}
                  </Form.Text>


              </Form.Group>
            </Col>
            <Col xs={12} sm={4}>
              <Form.Group controlId="">
                <Form.Label>City<span className="text-danger">*</span></Form.Label>
                <Form.Control defaultValue = {this.state.city}
                  onChange={this.onChange.bind(this)} name="city" type="text" size='lg' onBlur={this.props.handleValidation('city')} />

<Form.Text className="text-danger">
 {this.renderHelpText(this.getErrorMsg('city', this.props.errors))}
    </Form.Text>

              </Form.Group>
            </Col>
            <Col xs={12} sm={4}>
              <Form.Group controlId="">
                <Form.Label>State<span className="text-danger">*</span></Form.Label>
                <div className="c-select">
                  <select name="state" id="" className="form-control form-control-lg" defaultValue={this.state.state} onChange={this.onChange.bind(this)} >
                    <option value="">Select</option>
                    {stateDropDownValue}
                  </select>
                </div>

                <Form.Text className="text-danger">
 {this.renderHelpText(this.getErrorMsg('state', this.props.errors))}
    </Form.Text>
              </Form.Group>
            </Col>
          </Row>

          <Row>
            <Col xs={12} sm={6}>
              <Form.Group controlId="">
                <Form.Label>Company Phone No.<span className="text-danger">*</span></Form.Label>


                <InputMask 
                    mask="999-999-9999" 
                    maskChar=''
                    formControlProps={{
                        fullWidth: true,
                    }} 
                    className="form-control" 
                    value = {this.state.phoneNumber}
                    name="phoneNumber" 
                    onChange={this.onChange.bind(this)}
                    // onChange={(e)=> { this.setMobile(e.target.value,'mobileNumber', setFieldValue)}}
                    >
                    {(inputProps) =>
                    <Form.Control   {...inputProps}/>
                    }
                </InputMask> 

                {/* <Form.Control maxLength="10" minLength="10" defaultValue = {this.state.phoneNumber}
                  onChange={this.onChange.bind(this)} name="phoneNumber" type="number" size='lg' onBlur={this.props.handleValidation('phoneNumber')} /> */}

<Form.Text className="text-danger">
 {this.renderHelpText(this.getErrorMsg('phoneNumber', this.props.errors))}
    </Form.Text>
              </Form.Group>
            </Col>
            <Col xs={12} sm={6}>
              <Form.Group controlId="">
                <Form.Label>Company Fax </Form.Label>
                <Form.Control maxLength="14" defaultValue = {this.state.faxNumber}
                  onChange={this.onChange.bind(this)} name="faxNumber" type="text" size='lg' />
              </Form.Group>
            </Col>
          </Row>

          <Row>
            <Col xs={12} sm={6}>
              <Form.Group controlId="">
                <Form.Label>Type of Business</Form.Label>
                <div className="c-select">
                  <select name="typeOfBusiness" defaultValue={this.state.typeOfBusiness} onChange={this.onChange.bind(this)}  id="" className="form-control form-control-lg">
                    <option value="">Select</option>
                    <option value="Brokerage">Brokerage</option>
                  </select>
                </div>
              </Form.Group>
            </Col>
            <Col xs={12} sm={6}>
              <Form.Group controlId="">
                <Form.Label>Company Size</Form.Label>
                <div className="c-select">
                  <select name="companySize" defaultValue={this.state.companySize} onChange={this.onChange.bind(this)}  id="" className="form-control form-control-lg">
                    <option value="">Select</option>
                    <option value="1-10">Small (1-10)</option>
                    <option value="11-24">Medium (11-24)</option>
                    <option value="24+">Large (24+)</option>
                  </select>
                </div>
              </Form.Group>
            </Col>
          </Row>

        </Form>
      </div >
    );
  }

}


//export default CompanyInfo;


CompanyInfo.propTypes = {
  errors: PropTypes.object,
  validate: PropTypes.func,
  isValid: PropTypes.func,
  handleValidation: PropTypes.func,
  getValidationMessages: PropTypes.func,
  clearValidations: PropTypes.func,
  getUserData: PropTypes.func,
  updateUserData: PropTypes.func
};

export default validation(strategy)(CompanyInfo);
