import React from 'react';

// Bootstrap components
import { Button, Modal } from "react-bootstrap";
import { addContactAction, getContactByEmailAction, getUserRolesAction, addUserRolesAction } from '../../actions/UserActions';
import { connect } from 'react-redux';
import * as Yup from "yup";
import { Field, Form, Formik, ErrorMessage } from 'formik';
import { CodeSharp } from '@material-ui/icons';
import InputMask from 'react-input-mask';
import Axios from 'axios';

class CreateContact extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      page: 0,
      isEmailValid: false,
      isRoleExist: false,
      unauthorized: false,
      contact: {
        role: [""], // required
        usertype: "VENDOR", // required
        firstname: "",
        lastname: "",
        mobileNumber: '',
        email: ''
      },
      saveType: '',
    };
  }

  componentWillMount() {
    this.props.getUserRolesAction();
  }

  next(email, role, errors, setFieldError) {

    if (email != '' && errors['email'] == undefined) {
      this.props.getContactByEmailAction(email).then(res => {
        if (res == "Unauthorized") {
          this.setState({
            unauthorized: true
          })
        } else {
          if (Object.keys(res).length > 0) {
            var contact = {
              role: res.role ? res.role[0] : '',
              usertype: "VENDOR",
              firstname: res.firstname,
              lastname: res.lastname,
              mobileNumber: res.mobileNumber,
              email: email
            }
            this.setState({
              isEmailValid: true,
              unauthorized: false,
              isRoleExist: res.role ? true : false,
              contact
            })
          }
          this.setState({ page: 1, })
        }
      })
    }
  }

  addContact(values) {

    if (values.role == 'other') {
      this.props.addUserRolesAction(values.other.toUpperCase()).then(res => {
        values['roles'] = [res._id]
        values.mobileNumber = Number(values.mobileNumber)
        this.props.addContactAction(values).then(res => {
          this.props.onClose();
        });
      })
    } else {
      values['roles'] = [values.role]
      values.mobileNumber = Number(values.mobileNumber)
      this.props.addContactAction(values).then(res => {
        this.props.onClose();
      });
    }




  }

  setFieldValue(name, e) {
    var contact = this.state.contact
    contact[name] = e
    this.setState({
      contact
    })
  }

  setMobile(value, field, setFieldValue){
    var mob =  value.replace(/-/g,'')
    setFieldValue(field, mob)
  }

  render() {
    console.log(this.props.roles)
    return (
      <div>
        <Modal.Body>
          <Formik
            validateOnBlur={true}
            initialValues={this.state.contact}
            enableReinitialize={true}
            validationSchema={Yup.object().shape({
              firstname: Yup.string().trim().required('First Name is required.'),
              lastname: Yup.string().trim().required('Last Name is required.'),
              role: Yup.string().trim().required('Vendor Type is required.'),
              email: Yup.string().trim().email('Please enter valid Email address.').required('Email ID is required'),
              mobileNumber: Yup.string().nullable()
              .required('Please Enter Mobile Number.').test('mobileNumber','Please Enter Valid Mobile Number',(value)=>{
                if((value || '').length == 10){
                  return true
                }
                return false
              }),
            })}
            onSubmit={(values, errors) => {
              if (this.state.saveType != 'next') {
                this.addContact(values)
              }
            }}
          >
            {({
              handleChange,
              values,
              errors,
              setFieldValue,
              setFieldError
            }) => (
                <Form className="form-sm form-admin">
                  {this.state.page == 0 &&
                    <>
                      <div className="form-group">
                        <label>Email ID<span className="text-danger">*</span></label>
                        <Field type="text" className="form-control" name="email" value={values.email} onChange={(e) => { setFieldValue('email', e.target.value); this.setState({ unauthorized: false }); console.log(e.target.value) }} />
                        <ErrorMessage name="email" component="div" className="invalid-feedback" style={{ display: "block" }} />
                      </div>
                      {/* <div className="form-group">
                        <label>Select Vendor Type<span className="text-danger">*</span></label>
                        <div className="c-select">
                          <select className="form-control" name="role" value={values.role} onChange={(e)=> { setFieldValue("role", e.target.value)}}>
                            <option value='' >Select</option>
                            <option value='HOME INSPECTOR'>Home Inspector</option>
                            <option value='RADON INSPECTOR'>Radon Inspector</option>
                            <option value='WOOD INSPECTOR'>Wood Inspector</option>
                            <option value='other'>Other</option>
                          </select>
                        </div>
                        <ErrorMessage name="role" component="div" className="invalid-feedback" style={{display:"block"}} />
                      </div>
                      <div className="form-group">
                        <Field type="text" name="other"  className="form-control" placeholder='Enter Custom type' hidden={values.role && (values.role == 'other' ? false : true)} handleChange={handleChange}/>
                      </div> */}
                      {this.state.unauthorized && <label className="invalid-feedback" style={{ display: "block" }} >User already exists as buyer agent.</label>}
                      <Button variant="primary" size="sm" className="btn-blue w-140" type="submit" onClick={() => { this.setState({ saveType: 'next' }, () => { this.next(values.email, values.role, errors) }) }}>Next</Button>
                    </>
                  }
                  {this.state.page == 1 &&
                    <>
                      {/* {this.state.isEmailValid &&  <> */}
                      <div className="form-group">
                        <label>Email ID</label>
                        <Field type="email" className="form-control" name="email" disabled value={values.email} handleChange={handleChange} />
                        <ErrorMessage name="email" component="div" className="invalid-feedback" style={{ display: "block" }} />
                      </div>
                      <div className="form-group">
                        <label>Select Vendor Type</label>
                        <div className="c-select">
                          <select className="form-control" name="role" value={values.role} onChange={(e) => { setFieldValue("role", e.target.value) }}>
                            <option value='' >Select</option>
                            {this.props.roles &&
                              this.props.roles.map(role => {
                                return <option value={role._id}>{role.name}</option>
                              })
                            }
                            <option value='other'>Other</option>
                          </select>
                        </div>
                      </div>
                      {(values.role == 'other') &&
                        <div className="form-group">
                          <Field type="text" name="other" className="form-control" placeholder='Enter Custom type' required handleChange={handleChange} />
                        </div>}
                      <div className="form-group">
                        <label>First Name<span className="text-danger">*</span></label>
                        <Field type="text" name="firstname" className="form-control" value={values.firstname} disabled={this.state.isEmailValid} handleChange={handleChange} />
                        {this.state.saveType != 'next' && <ErrorMessage name="firstname" component="div" className="invalid-feedback" style={{ display: "block" }} />}
                      </div>
                      <div className="form-group">
                        <label>Last Name<span className="text-danger">*</span></label>
                        <Field type="text" name="lastname" className="form-control" value={values.lastname} disabled={this.state.isEmailValid} handleChange={handleChange} />
                        {this.state.saveType != 'next' && <ErrorMessage name="lastname" component="div" className="invalid-feedback" style={{ display: "block" }} />}
                      </div>
                      <div className="form-group" hidden={this.state.isEmailValid}>
                        <label>Phone No.<span className="text-danger">*</span></label>
                        <InputMask 
                            mask="999-999-9999" 
                            maskChar=''
                            formControlProps={{
                              fullWidth: true,
                            }} 
                            className="form-control" 
                            value={values.mobileNumber} 
                            name="mobileNumber" 
                            disabled={this.state.isEmailValid}
                            onChange={(e)=> { this.setMobile(e.target.value,'mobileNumber', setFieldValue)}}
                          >
                          {(inputProps) =>
                            <Field   {...inputProps}/>
                          }
                        </InputMask>  
                        {/* <Field type="number" name="mobileNumber" className="form-control" value={values.mobileNumber} disabled={this.state.isEmailValid} handleChange={handleChange} /> */}
                        {this.state.saveType != 'next' && <ErrorMessage name="mobileNumber" component="div" className="invalid-feedback" style={{ display: "block" }} />}
                      </div>

                      {this.state.isRoleExist && <Button variant="primary" size="sm" className="btn-blue w-140" onClick={() => { this.props.onClose() }}>{this.state.isEmailValid ? 'Close' : 'Add'}</Button>}
                      {!this.state.isRoleExist && <Button variant="primary" size="sm" className="btn-blue w-140" type="submit" onClick={() => { this.setState({ saveType: '' }) }}>{this.state.isEmailValid ? 'Save' : 'Add'}</Button>}

                    </>
                  }
                </Form>)}
          </Formik>
        </Modal.Body>
        {/* <Modal.Footer>
          
          {this.state.page == 1 &&
            <Button variant="primary" size="sm" className="btn-blue w-140" type="submit" >{this.state.isEmailValid ? 'Save' : 'Add'}</Button>
          }
          <Button variant="primary" size="sm" className="btn-outlined btn-blue w-140">Save & Create New</Button>
        </Modal.Footer> */}
      </div>
    );
  }

}


const mapStateToProps = state => {
  return {
    contactResponse: state.reducer.contactResponse,
    contact: state.reducer.contact,
    roles: state.reducer.roles
  };
};


export default connect(mapStateToProps, { addContactAction, getContactByEmailAction, getUserRolesAction, addUserRolesAction })(CreateContact);
