// a little function to help us with reordering the result
const reorder = (list, startIndex, endIndex) => {
  const result = Array.from(list);
  const [removed] = result.splice(startIndex, 1);
  result.splice(endIndex, 0, removed);

  return result;
};

const grid = 3;

const getItemStyle = (isDragging, draggableStyle) => ({
  // some basic styles to make the items look a bit nicer
  userSelect: "none",
  // padding: grid * 2,
  // margin: `0 0 ${grid}px 0`,

  // change background colour if dragging
  background: isDragging ? "#f5f9fc" : "",

  // styles we need to apply on draggables
  ...draggableStyle
});

const getListStyle = isDraggingOver => ({
  background: isDraggingOver ? "#fff" : "",
  // padding: grid,
  // width: 250
});

class Contingencies extends Component {
  constructor(props) {
    super(props);

    this.state = {
      propsConti : this.props.contingencies,
      show:false,
      startDate: moment(),
      closeDate: moment().subtract(-29, 'days'),
      contStartDate: moment(),
      contDueDate: moment().subtract(-29, 'days'),
      activeContigency: true,
      inactiveContiClass: '',
      items: getItems(3),
      addVendor: false,
      isvendorClick:false,
      allContingencies : [],
      campContingencies :[],
      allContingenciesFromAPI :[],
      trasactioncontigency : [],
      vendors : [],
      isUpdateconti:false
      // transcationAllData : this.props.getTransactionData()
    };
    this.onDragEnd = this.onDragEnd.bind(this);
  }

  onDragEnd(result) {
    // dropped outside the list
    if (!result.destination) {
      return;
    }

    const items = reorder(
      this.state.items,
      result.source.index,
      result.destination.index
    );

    this.setState({
      items
    });
  }

    setStartDate(event, picker) {
    this.setState({
      startDate: picker.startDate,
    });
  }

  setEndDate(event, picker) {
    this.setState({
      closeDate: picker.startDate,
    });
  }

  setModalOpen(){
    this.setState({show:!this.state.show});
  }

  // contigency dates
  contStartDate(event, picker) {
    this.setState({
      contStartDate: picker.startDate,
    });
  }

  contDueDate(event, picker) {
    this.setState({
      contDueDate: picker.startDate,
    });
  }

  activeContigency(i,index,item){

    let currentContingencie = this.state.allContingencies;

    let isActive = true;
    if(i == 1){
      isActive = false;
    }

    for(let j=0;j<currentContingencie.length;j++) {
      if(currentContingencie[j].key === item.key) {
        currentContingencie[j].isActive = isActive;
      }
    }

    this.setState({allContingencies : currentContingencie});

    let contiObj = {};
    for(let i=0; i<currentContingencie.length;i++) {
      if(currentContingencie[i].isActive === true) {
        contiObj[currentContingencie[i].key] = (currentContingencie[i].val !== "") ? currentContingencie[i].val : currentContingencie[i].key
      }
    }

    this.props.setFieldValue('contingencies',contiObj)
  
  }

  addVendor(){   
    this.setState({addVendor:!this.state.addVendor,isUpdateconti : true});
  }

  addCloseVendor(){
    this.setState({isvendorClick:false,addVendor:!this.state.addVendor});
    this.getAllContacts()
  }


  getAllContacts(){
  
    this.props.getContactsActions().then(res=>{
      var vendors = {}
      var response = this.props.contacts;
      this.props.getUserRolesAction().then(cats => {

        if(cats){
          vendors = _.groupBy(response, function(b) { return b.roles ? b.roles[0] : ''})
          var newCategories = {};
          Object.keys(vendors).forEach((cat)=>{

            let name = "";
            for(let i=0;i<cats.length;i++) {
              if(cats[i]._id === cat) {
                name = cats[i].contingency;
              }
            }
            var data = { data: vendors[cat], open: true}
            if(name != "") {
              newCategories[name] = data
            } else {
              newCategories[cat] = data
            }
            
          })
          
          this.setState({
            vendors : newCategories,
          })
        } else {
          this.setState({
            vendors : []
          })
        } 

      })
    }, error=>{
      this.setState({
        vendors : []
      })
    });
  }

componentDidMount() {
  // this.getAllContacts();

}

componentWillMount() {

      let contingencies = [];
      for (var key in this.props.contingencies) {
        if (this.props.contingencies.hasOwnProperty(key)) {
          contingencies.push({"key":key, "val":this.props.contingencies[key],
           'isActive' : true});
        }     
      }
      this.props.getAllContigencyAction().then(res=>{
        res.forEach(ele=>{
          var found = false;
          for (var key in this.props.contingencies) {
            if (ele.name ==  key) {
              found = true;
            }     
          }
          if(!found){
            contingencies.push({"key": ele.name, "val":ele.date, 'isActive' : false});
          }
        })
        this.setState({allContingencies : contingencies,allContingenciesFromAPI : res});

      });
     

  }

  componentWillReceiveProps(nextProps) {
  
      let contingencies = [];
      
        for (var key in nextProps.contingencies) {
            if (nextProps.contingencies.hasOwnProperty(key)) {
              contingencies.push({"key":key, "val":nextProps.contingencies[key], 
              'isActive' : true});
            }
        }

        if(contingencies.length > 0) {
         
        this.state.allContingenciesFromAPI.forEach(ele=>{
        
          var found = false;
          for (var key in nextProps.contingencies) {
            if (ele.name ==  key) {
              found = true;
            }
          }
          if(!found){
              contingencies.push({"key": ele.name, "val":ele.date, 'isActive' : false });
          }
        });

        this.setState({allContingencies : contingencies,isvendorClick:false});
      }
      else {
        this.state.allContingenciesFromAPI.forEach(ele=>{
        
              contingencies.push({"key": ele.name, "val":ele.date, 'isActive' : true });
        });

        this.setState({allContingencies : contingencies,isvendorClick:false});
      }
    
  }
  


  addCustomContingency(e){
    let newConti = '';
    if (e.key === 'Enter' && e.shiftKey === true) {
      newConti = e.target.value;
    }
    else if (e.key === 'Enter' && e.shiftKey === false) {
      newConti = e.target.value;
    }    

    if(newConti !== "" && newConti !== null) {
        let currentContingencie = this.state.allContingencies;
        currentContingencie.push({"key":newConti, "val" : "", isActive : true});
        this.setState({allContingencies : currentContingencie});

        let contiObj = {};
        for(let i=0; i<currentContingencie.length;i++) {
          if(currentContingencie[i].isActive === true) {
            contiObj[currentContingencie[i].key] = currentContingencie[i].val 
          }
        }
        if(this.props.isMasterTemplate) {
          this.props.setFieldValue('contingencies',contiObj)
        }

        e.target.value = "";
    }
  }

  enterCustomContingency(e){
    let newConti = '';
    let contiVal = $('.set-contingencies-footer .title input').val();
    newConti = contiVal    

    if(newConti !== "" && newConti !== null) {
        let currentContingencie = this.state.allContingencies;
        currentContingencie.push({"key":newConti, "val" : "", isActive : true});
        this.setState({allContingencies : currentContingencie});

        let contiObj = {};
        for(let i=0; i<currentContingencie.length;i++) {
          if(currentContingencie[i].isActive === true) {
            contiObj[currentContingencie[i].key] = currentContingencie[i].val 
          }
        }
        if(this.props.isMasterTemplate) {
          this.props.setFieldValue('contingencies',contiObj)
        }

        $('.set-contingencies-footer .title input').val('');
    }
  }


  addCustomTranContingency(e) {
   
    let newConti = '';
    if (e.key === 'Enter' && e.shiftKey === true) {
      e.preventDefault();
      newConti = e.target.value;
    }
    else if (e.key === 'Enter' && e.shiftKey === false) {
      e.preventDefault();
      newConti = e.target.value;
    }    

    if(newConti !== "" && newConti !== null) {
        let currentContingencie = this.state.allContingencies;
        currentContingencie.push({"key":newConti, "val" : "", isActive : true});
        this.setState({allContingencies : currentContingencie});

        let contiObj = {};
        for(let i=0; i<currentContingencie.length;i++) {
          if(currentContingencie[i].isActive === true) {
            contiObj[currentContingencie[i].key] = currentContingencie[i].val 
          }
        }       
        e.target.value = "";
    }
    return false;

  }

  renderMasterTemplateCamp() {
    
    console.log("this.props.contingencies",this.props.contingencies)
    console.log("this.state.allContingencies",this.state.allContingencies)

    let contiAll = this.state.allContingencies

    contiAll =  _.sortBy(contiAll, "key")

    return(

      <div className="set-contingencies">
      <div className="set-contingencies-header">
        <ul className="set-contingencies-row">
          <li className="id"></li>
          <li className="title"></li>
          <li className="status">active /inactive</li>
        </ul>
      </div>
      <div className="set-contingencies-body">
        <DragDropContext onDragEnd={this.onDragEnd}>
         
          <Droppable droppableId="droppable123">
            {(provided, snapshot) => (
              <div
                {...provided.droppableProps}
                ref={provided.innerRef}
                style={getListStyle(snapshot.isDraggingOver)}
              >
                {contiAll.map((item, index) => (
                  <Draggable 
                  key={index} 
                  draggableId={"draggableId" + index} 
                  index={index}
                  >
                    {(provided, snapshot) => (
                      <div
                        ref={provided.innerRef}
                        {...provided.draggableProps}
                        {...provided.dragHandleProps}
                        style={getItemStyle(
                          snapshot.isDragging,
                          provided.draggableProps.style
                        )}
                      >
                        <ul className= {(item.isActive) ? " set-contingencies-row" : "inactive set-contingencies-row"}>
              <li className="id">
                <DragIndicator/>
              </li>
              <li className="title">{item.key}</li>
              
              <li className="status">
                <Button variant="transparent">
                  
                    {(item.isActive)?
                      <CheckCircle className="check" onClick={() => this.activeContigency(1,index,item)}/>
                      :
                      <CheckCircleOutline className="check-out" onClick={() => this.activeContigency(0,index,item)}/>
                    }

                </Button>
              </li>
            </ul>
                      </div>
                    )}
                  </Draggable>
                ))}
                {provided.placeholder}
              </div>
            )}
          </Droppable>
        
        </DragDropContext>

      </div>
      
      <div className="set-contingencies-footer">
        <ul className="set-contingencies-row">
            <li className="id">
              
            </li>
            <li className="title">
              <input type="text" onKeyDown={(e)=>this.addCustomContingency(e)} className="form-control form-control-sm" placeholder="Add custom contingency"/>
            </li>
            <li className="status">
              <Button variant="transparent">
                <AddCircleOutline className="add" onClick={(e)=>this.enterCustomContingency(e)}/>
              </Button>
            </li>
          </ul>
      </div>
    </div>

    )
  }

  listVendorsByConti(conti,item,index) {
    
    let vendors = this.state.vendors;
    let filteredVendors = []

    // let contiAll =  this.state.allContingencies;

    if(conti === "Home Inspection" || conti === "Radon Inspection" ||
     conti === "Septic Inspection" || conti === "Wood Destroying Inspection") {

      if(vendors[conti] !== undefined && vendors[conti] !== null) {      
        let vendorsData = (vendors[conti].data !== undefined && vendors[conti].data !== null) ? vendors[conti].data : [];
        for (var i = 0, len = vendorsData.length; i < len; i++) {

          if(vendorsData[i].default) {
            // contiAll[index].assignee = vendorsData[i]._id
           // defaultVal = vendorsData[i]._id
          }

          if(this.props.contactResponse !== undefined && this.props.contactResponse !== null) {
            let venData = this.props.contactResponse.data
            if(vendorsData[i]._id === venData._id) {
              // contiAll[index].assignee = vendorsData[i]._id
            }            
          }
          
          filteredVendors.push(<option value={vendorsData[i]._id} key={vendorsData[i]._id}>{vendorsData[i].firstname + " "+ vendorsData[i].lastname }</option>);
        }
      }
    }
    else {
      for (var key in vendors) {
        let vendorsData = vendors[key].data;
        for (var i = 0, len = vendorsData.length; i < len; i++) {
          filteredVendors.push(<option value={vendorsData[i]._id} key={vendorsData[i]._id}>{vendorsData[i].firstname + " "+ vendorsData[i].lastname }</option>);
        }
      }
    }
    
    //this.setState({isvendorClick: true, allContingencies : contiAll})
    
    return (
      filteredVendors
    );

  }

  // Normally you would want to split things out into separate components.
  // But in this example everything is just done in one place for simplicity
  render() {

    return (
     <div>
        <Row className="mt-2 align-items-center conti-title">
          <Col className="col-auto pr-0">
              <h2 className="color-blue-3 font-20 mb-0">Set Contingencies</h2>
          </Col>
          <Col align="right" className="pl-0">
              <Button variant="primary" className="btn-orange text-uppercase add-vendor-btn" onClick={() => { this.setState({isvendorClick:true}); this.addVendor();}}>add new vendor</Button>
          </Col>
        </Row>

        {this.renderMasterTemplateCamp()}

        <Modal
            show={this.state.addVendor}
            onHide={() => this.addVendor()}
            dialogClassName="modal-90w"
            aria-labelledby="example-custom-modal-styling-title"
            className="modal-med"
          >
            <Modal.Header closeButton>
              <Modal.Title id="example-custom-modal-styling-title">
                  Create New Contact
              </Modal.Title>
            </Modal.Header>
            <AddContact onClose={() => {this.setState({isvendorClick:false});this.addCloseVendor()}}/>
          </Modal>
      </div>
      
    );
  }
}

const mapStateToProps = state => {
	return {
    allContingencies : state.reducer.allContingencies,
    contactResponse: state.reducer.contactResponse,
    contacts: state.reducer.contacts,
    roles: state.reducer.roles,
	};
};

export default connect(mapStateToProps, {
    getAllContigencyAction,
    getContactsActions,
    getUserRolesAction})
  (Contingencies);

