import React from 'react';

// Bootstrap components
import { Row, Col, Button, Dropdown, Modal } from 'react-bootstrap';

// icons
import { MoreVert, Close, Archive } from '@material-ui/icons';
import folderIcon from './../../assets/img/folder.svg';

import pdfFile from './../../assets/img/pdf-test.pdf';
import moment from 'moment';
import noRecord from './../../assets/img/no-record.png';
import UrlConstants from '../../config/UrlConstants';
import { archiveDocAction, updateDocAction } from './../../actions/DocActions';
import {  updateTaskAction,getTaskDetailsAction } from './../../actions/TransactionActions';
import { connect } from 'react-redux';
var _ = require('lodash')

class DocumentsDetail extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			showArchieve: false,
			show: false,
      showPreview: false,
	  previewURL: '',
	  docName:'',
	  userRole : localStorage.getItem('usertype').toUpperCase()
		};
	}

	// archieve accordion
	showArchieve() {
		this.setState({ showArchieve: !this.state.showArchieve });
	}

	// add folder modal
	setModalOpen() {
		this.setState({ show: !this.state.show });
	}

	// modal preview
	ModalPreview(name, transactionId) {
		if(name){
			var url = '/#'
			fetch(UrlConstants.AdminCreateTransactionUrl + '/'+ name +'?id='+ transactionId, {method: 'GET'}).then(response => response.json()).then(res=>{
				url = res.url
				this.setState({ showPreview: !this.state.showPreview, previewURL : url, docName: name });
			});
		}
  	}

	getType(name){
		var type = '';
		if(name){
			var ext = ((name.match(/(\.\w+$)/igm)|| [])[0]+'').toLowerCase();
			if(ext == ".jpg" || ext == ".jpeg" || ext == ".png" || ext == ".gif"){
				type = 'image'
			} else if(name.match(/.(pdf)$/i)){
				type = 'pdf'
			}
		}
		
		return type
	}
	archiveDocument(doc){
		
		this.props.archiveDocAction(doc._id).then(res => {
			// var values = {};
			// this.props.getTaskDetailsAction(doc.transactionEventId).then(res => {
			// 		values = res;//res.documentId
				
			// 	values['documentId'] = values.documentId.filter((e)=>{
			// 		return e.document != doc.document
			// 	})
			// 	values.eventType = values.type;
			// 	this.props.updateTaskAction(values, doc.transactionEventId).then(res => {
			// 	})	
			// })
			this.props.archiveDocument(res);
		});
	}

	render() {
	var files = [];
	var { folderName, transactionId, documents } = this.props;
	console.log(documents)
	documents = _.sortBy(documents, 'document');
	
	var docURL = UrlConstants.viewDocUrl.replace('{transactionId}', transactionId);
		return (
			<React.Fragment>
				<Row className="mx-0 align-items-center folder-detail-title">
					<Col className="col-auto pr-0">
						<img
							src={folderIcon}
							alt="folder"
							style={{ width: '30px' }}
						/>
					</Col>
					<Col>
						<h4 className="color-blue-3 font-18 mb-0">
							{folderName}
						</h4>
					</Col>
					{/* <Col align="right" className="col-auto px-0">
						<Button variant="transparent" className="p-0">
							<Archive className="color-grey-1"/>
						</Button>
					</Col> */}
					<Col align="right" className="col-auto">
					{(this.state.userRole === "VENDOR") 
						? "" :
						
						<Button
							variant="primary"
							size="xs"
							hidden={this.props.hideNewDocument} 
							className="btn-outlined btn-blue-light h-auto font-14 browse-btn"
						>
							<input
								type="file"
								id="browseDoc"
								multiple
								value={files}
								accept=".pdf,.doc,.docx,.xml,application/msword,application/vnd.openxmlformats-officedocument.wordprocessingml.document,.jpg,.png,.gif,.jpeg"
								onChange={e => {
									this.props.uploadNewDocument(
										e.target.files
									);
									files = []
								}}
							/>
							
							<label htmlFor="browseDoc">+ New Document</label>
						</Button>
					}
					</Col>
				</Row>
				<div className="folder-detail">
					{documents.map((doc, i) => {
						return (
							<Row
								key={'doc' + i}
								className="mx-0 folder-detail-row"
							>
								<Col className="col-auto pr-0">
									<input
											type="checkbox"
											id={'check' + i}
											name=""
										/>
								</Col>
								{ (this.getType(doc.document) == 'image' || this.getType(doc.document) == 'pdf') ?
								<Col className="pl-2" onClick={() => this.ModalPreview(doc.document, transactionId) }>
									<div className="custom-control custom-checkbox font-15 color-black font-500 mr-0 pl-0">
										{/* <input
											type="checkbox"
											className="custom-control-input"
											id={'check' + i}
											name=""
										/> */}
										<label
											className="mb-0"
										>
													{doc.document}
										</label>
									</div>
									<p className="color-grey-1 font-14 mb-0">
										Updated{' '}
										{moment(doc.modifiedDate).format(
											'MM/DD/YYYY'
										)}
									</p>
								</Col>
								:
								<Col className="pl-2">
									<div className="custom-control custom-checkbox font-15 color-black font-500 mr-0 pl-0">
										{/* <input
											type="checkbox"
											className="custom-control-input"
											id={'check' + i}
											name=""
										/> */}
										<label
											className="mb-0"
										>
											
												<a href={ docURL +  doc.document } download>{doc.document}</a>
										</label>
									</div>
									<p className="color-grey-1 font-14 mb-0">
										Updated{' '}
										{moment(doc.modifiedDate).format(
											'MM/DD/YYYY'
										)}
									</p>
								</Col>
								}
								<Col className="col-auto">
									<Dropdown
										alignRight
										className="dropdown-action"
									>
										<Dropdown.Toggle
											variant="transparent"
											id="dropdown-basic"
											className="p-0"
										>
											<MoreVert />
										</Dropdown.Toggle>

										<Dropdown.Menu>
											{/* <Dropdown.Item>Open</Dropdown.Item>
											<Dropdown.Item>Replace</Dropdown.Item> */}
											<Dropdown.Item href={ docURL +  doc.document } download>Download</Dropdown.Item>
											{ (this.getType(doc.document) == 'image' || this.getType(doc.document) == 'pdf') &&
												<Dropdown.Item
												onClick={() =>
													this.ModalPreview(doc.document, transactionId)
												}
												>
												Preview
												</Dropdown.Item>
											}
											{/* <Dropdown.Item className="color-dark-orange-3" onClick={()=>{this.deleteDocument()}}>
												Remove/Delete
											</Dropdown.Item> */}
											{(localStorage.getItem('usertype') != 'VENDOR' && doc.isArchive == false && doc.isDelete == false) &&
												<Dropdown.Item className="color-dark-orange-3" onClick={()=>{this.archiveDocument(doc)}}>
													Archive document
												</Dropdown.Item>
											}
										</Dropdown.Menu>
									</Dropdown>
								</Col>
							</Row>
						);
					})}
					{documents.length <= 0 && (
						<div className="no-transaction no-record">
							<div className="no-transaction-inner">
								<img src={noRecord} alt="no record" />
								<p>No Documents</p>
							</div>
						</div>
					)}
				</div>

				<Modal
					show={this.state.showPreview}
					onHide={() => {this.setState({
						showPreview :false
					})}}
					dialogClassName="modal-90w"
					aria-labelledby="example-custom-modal-styling-title"
					className="modal-med modal-preview"
				>
					<Modal.Header className="d-block">
						<Row className="align-items-center">
							<Col xs="9">
								<div className="modal-title">
									Document Preview
								</div>
							</Col>
							<Col xs="3" className="text-right">
								<Button
									className="close-btn"
									variant="transparent"
									onClick={() => {this.setState({
										showPreview :false
									})}}
								>
									<Close />
								</Button>
							</Col>
						</Row>
					</Modal.Header>
					<Modal.Body>
					{this.getType(this.state.docName || '') == 'image' ?
						<img src={UrlConstants.AdminCreateTransactionUrl+"/upload?id="+ transactionId + "&img="+ this.state.docName}></img> :
						<object
							data={this.state.previewURL || '#'}
							// type="application/pdf"
							width="100%"
							height="100%"
						>
							{/* alt : <a href="test.pdf"/> */}
						</object>
					}
					</Modal.Body>
				</Modal>
			</React.Fragment>
		);
	}
}

const mapStateToProps = state => {
	return {
		docs: state.reducer.docs || [],
	};
};

export default connect(mapStateToProps, { archiveDocAction, updateDocAction, updateTaskAction, getTaskDetailsAction })(DocumentsDetail);