import Axios from "axios";

import UrlConstants from './UrlConstants'


class APIUtil {

    externalAPI(url) {
        return fetch(url, {
            method: 'GET'
        })
            .then(response => response.json()
                .catch((error) => { throw error }))
    }

    getMethod(url, auth) {
        var headersSet = {
            "Accept": "application/json",
            "Content-Type": "application/json",           
        };
        if(auth) {
            var accessToken = localStorage.getItem("accessToken");
            headersSet = {
                "Accept": "application/json",
                "Content-Type": "application/json",
                "x-auth-token" : accessToken,
            };
        }

        return Axios({
            method: 'get',
            url: url,
            headers: headersSet
        }).then(response => response)
        .catch(error =>{ 

            if (error.response) {
                if(error.response.data === "Invalid token.") {
                    this.inValidToken();
                    // return false;      
                    // return error.response          
                } 
                else {
                    return error.response
                }
            } 
            
            else {
                return {"status":"404"}
            }
        })

    }

    inValidToken() {
        localStorage.removeItem("accessToken");
        localStorage.removeItem("usertype");
        window.location.href = UrlConstants.SiteUrl + 'login';
        window.location.reload()
        // browserHistory.push("/login")
    }

    postMethod(url, data, auth) {
        var headersSet = {
            "Accept": "application/json",
            "Content-Type": "application/json",
        };
        if(auth) {
            var accessToken = localStorage.getItem("accessToken");
            headersSet = {
                "Accept": "application/json",
                "Content-Type": "application/json",
                "x-auth-token" : accessToken,
            };
        }

        return Axios({
            method: 'post',
            url: url,
            headers: headersSet,
            data : data
        }).then(response => response)
        .catch(error =>{ 
            if (error.response) {
                if(error.response.data === "Invalid token.") {
                    this.inValidToken();                    
                } 
                else {
                    return error.response
                }
            } else {
                return {"status":"404"}
            }
        })

    }

    putMethod(url, data, auth) {
        var headersSet = {
            "Accept": "application/json",
            "Content-Type": "application/json",           
        };
        if(auth) {
            var accessToken = localStorage.getItem("accessToken");
            headersSet = {
                "Accept": "application/json",
                "Content-Type": "application/json",
                "x-auth-token" : accessToken,
            };
        }

        return Axios({
            method: 'PUT',
            url: url,
            headers: headersSet,
            data : data
        }).then(response => response)
        .catch(error =>{ 
            if (error.response) {
                if(error.response.data === "Invalid token.") {
                    this.inValidToken();                    
                } 
                else {
                    return error.response
                }
            } else {
                return {"status":"404"}
            }
        })

    }

    deleteMethod(url,auth,data) {
        var headersSet = {
            "Accept": "application/json",
            "Content-Type": "application/json",           
        };
        if(auth) {
            var accessToken = localStorage.getItem("accessToken");
            headersSet = {
                "Accept": "application/json",
                "Content-Type": "application/json",
                "x-auth-token" : accessToken,
            };
        }

        return Axios({
            method: 'DELETE',
            url: url,
            data: data ? data : null,
            headers: headersSet
        }).then(response => response)
        .catch(error =>{ 
            if (error.response) {
                if(error.response.data === "Invalid token.") {
                    this.inValidToken();                    
                } 
                else {
                    return error.response
                }
            } else {
                return {"status":"404"}
            }
        })
    }

    postFile(url, data) {
        var formData = new FormData();
        formData.append("File", data)
        return fetch(url, {
            method: 'POST',
            body: formData
        }).then(response => response.json())
            .catch((error) => { throw error })
    }

    putFormData(url, formData) {
        return fetch(url, {
            method: 'PUT',
            body: formData
        }).then(response => response.json())
            .catch((error) => { throw error })
    }

    postFormDataMethod(url, data, auth) {
        var headersSet = {
            "Accept": "application/json",
            'Content-Type': 'multipart/form-data',
        };
        if(auth) {
            var accessToken = localStorage.getItem("accessToken");
            headersSet = {
                "Accept": "application/json",
                'Content-Type': 'multipart/form-data',
                "x-auth-token" : accessToken,
            };
        }

        return Axios({
            method: 'post',
            url: url,
            headers: headersSet,
            data : data
        }).then(response => response)
        .catch(error =>{ 
            if (error.response) {
                if(error.response.data === "Invalid token.") {
                    this.inValidToken();                    
                } 
                else {
                    return error.response
                }
            } else {
                return {"status":"404"}
            }
        })

    }
    putMultiFiles(url, data) {
        // var formData = new FormData();
        // formData.append("File", data)
         return fetch(url, {
             method: 'PUT',
             body: data
         }).then(response => response.json())
             .catch((error) => { throw error })
     }

    getImage(url, auth) {
        var headersSet = {
            "Accept": "application/json",
            "Content-Type": "application/json",           
        };
        if(auth) {
            var accessToken = localStorage.getItem("accessToken");
            headersSet = {
                "Accept": "application/json",
                "Content-Type": "application/json",
                "x-auth-token" : accessToken,
            };
        }

        return Axios({
            method: 'get',
            url: url,
            headers: headersSet
        }).then(response => response)
        .catch(error =>{ 
            if (error.response) {
                if(error.response.data === "Invalid token.") {
                    this.inValidToken();                    
                } 
                else {
                    return error.response
                }
            } else {
                return {"status":"404"}
            }
        })
    }

    checkStatus(response) {
        // raises an error in case response status is not a success
        if (response.Error) {
            if (response.ErrText === "Valid session required") {
                localStorage.removeItem('session_id');
                // history.push('/login');
                return response
            }
            else {
                return response
            }
        }
        else {
            return response
        }
    }

}
export default (new APIUtil);