componentWillMount() {

    if(this.props.isMasterTemplate) {
      let contingencies = [];
      for (var key in this.props.contingencies) {
        if (this.props.contingencies.hasOwnProperty(key)) {
          contingencies.push({"key":key, "val":this.props.contingencies[key],
           'isActive' : true});
        }     
      }
      this.props.getAllContigencyAction().then(res=>{
        res.forEach(ele=>{
          var found = false;
          for (var key in this.props.contingencies) {
            if (ele.name ==  key) {
              found = true;
            }     
          }
          if(!found){
            contingencies.push({"key": ele.name, "val":ele.date, 'isActive' : false});
          }
        })
        this.setState({allContingencies : contingencies,allContingenciesFromAPI : res});

      });
      this.setState({allContingencies : contingencies});

    }
    else {

      let contingencies = [];
      if(this.props.isUpdateTrans) {

        let transcationAllData = this.props.getTransactionData();
          for (var key in this.props.campContingencies) {

            let v = this.props.campContingencies[key];
      
            if(v.name === "HOA Docs" && !transcationAllData.hoa && !this.state.isHoa) {
              contingencies.push({"key": v.name, "val":v.name, 'isActive' : false,
                'start' : (v.startDate != "") ? moment(v.startDate).format('YYYY-MM-DD') : this.state.contStartDate.format('YYYY-MM-DD'), 
                'end' : (v.endDate != "") ? moment(v.endDate).format('YYYY-MM-DD') : this.state.contDueDate.format('YYYY-MM-DD'), 
                'assignee' : "", //v.assignee,
                defaultAssignee:  (v.assignee !== "" && v.assignee !== undefined) ? false: true});
            }
            else {
              contingencies.push({"key": v.name, "val":v.name, 'isActive' : true,
                'start' : (v.startDate != "") ? moment(v.startDate).format('YYYY-MM-DD') : this.state.contStartDate.format('YYYY-MM-DD'), 
                'end' : (v.endDate != "") ? moment(v.endDate).format('YYYY-MM-DD') : this.state.contDueDate.format('YYYY-MM-DD'), 'assignee' : v.assignee,
                defaultAssignee: (v.assignee !== "" && v.assignee !== undefined) ? false: true});
            }      
          }

          this.props.getAllContigencyAction().then(res=>{         
            this.setState({allContingenciesFromAPI : res});

          }); 

          this.props.updateTransactionData("contingencies",contingencies)     


    }
    else {
        let contingencies = [];
          for (var key in this.props.campContingencies) {
            if (this.props.campContingencies.hasOwnProperty(key)) {            
              contingencies.push({
                "key":key, "val":this.props.campContingencies[key], 
                'isActive' : true,
                'start' : this.state.contStartDate.format('YYYY-MM-DD'), 
                'end' : this.state.contDueDate.format('YYYY-MM-DD'), 'assignee' : ""});
            }     
          }
          this.props.getAllContigencyAction().then(res=>{         
            this.setState({allContingenciesFromAPI : res});

          });  
          this.props.updateTransactionData("contingencies",contingencies)     
        }
       
    }

  }
  
