  listVendorsByConti(conti,item,index, contingencies) {
    
    let vendors = this.state.vendors;
    let filteredVendors = []
    
    if(conti === "Home Inspection" || conti === "Radon Inspection" || 
    conti === "Septic Inspection" || conti === "Wood Destroying Inspection") {

      if(vendors[conti] !== undefined && vendors[conti] !== null) {      
        let vendorsData = (vendors[conti].data !== undefined && vendors[conti].data !== null) ? vendors[conti].data : [];
        for (var i = 0, len = vendorsData.length; i < len; i++) {
  
          if(vendorsData[i].default && item.defaultAssignee && item.isActive && !this.props.isUpdateTrans) {
           item.assignee = vendorsData[i].contactId
           item.vendorName = vendorsData[i].firstname +''+ vendorsData[i].lastname
          }
  
          // if(this.props.contactResponse !== undefined && this.props.contactResponse !== null) {
          //   let venData = this.props.contactResponse.data
          //   if(vendorsData[i]._id === venData._id && !this.props.isUpdateTrans && (item.assignee == "" || item.assignee === undefined)) {
          //    item.assignee = vendorsData[i].contactId
          //    item.isActive = true; 
          //   }            
          // }
          
          filteredVendors.push(<option value={vendorsData[i].contactId} key={vendorsData[i].contactId}>{vendorsData[i].firstname + " "+ vendorsData[i].lastname }</option>);
        }
      }
      

    }

    else {

      if(vendors["all"] !== undefined && vendors["all"] !== null) { 
        let vendorsData = (vendors["all"].data !== undefined && vendors["all"].data !== null) ? vendors["all"].data : [];
        for (var i = 0, len = vendorsData.length; i < len; i++) {
          filteredVendors.push(<option value={vendorsData[i].contactId} key={vendorsData[i].contactId}>{vendorsData[i].firstname + " "+ vendorsData[i].lastname }</option>);
        }
      }
    }
    // console.log("item",this.state.buyerVendors)
    if( this.state.userRole == 'BUYER'){
      return (
        <div>
        {(item.isActive)?
         <>
         {this.state.buyerVendors && this.state.buyerVendors.map( (ven, ind) => {
          if(ven._id == item.assignee ){
            return(
              <Link to={'/UserProfile/'+ven.contactId} className="text-capitalize font-500 color-dark-blue c-pointer font-14" >
                {ven.firstname + ' ' + ven.lastname}
              </Link>
            )
          }
         })
         }        
         </>
         :""
         }
        </div>
      );
    } else{
      return (
    
        <div className="c-select">    
          <select name="assignee"
          disabled={(this.state.userRole == "BUYER") ? true : false}
          onChange={(e)=>this.setAssignee(e,item,index,contingencies)} 
          className="form-control form-control-sm" value={item.assignee}>
            <option value="">Select Vendor</option>
            {filteredVendors}         
          </select>
        </div>
        );
    }


  }


