//All action about user defined 

import * as types from './actions';

import APIUtil from './../config/APIUtil';
import UrlConstants from './../config/UrlConstants';

var _ = require('lodash');


/**
 * @desc Transactions - get All
 */
export function getAllTransactionsAction() {  

    return function (dispatch){
      return APIUtil.getMethod(UrlConstants.AdminCreateTransactionUrl, true).then(response => {

          // dispatch
          if(response.status == 200) {
              dispatch( {
                  type: types.GET_ALL_TRANSACTIONS,
                  transactions: response.data
              });
          }
          else {
              dispatch( {
                  type: types.GET_ALL_TRANSACTIONS,
                  transactions: []
              });
          }
      });
    }  
}

