/**
 * @desc Transaction - get by ID
 */
export function getTransactionDataAction(id) {  

    let url = UrlConstants.GetTransactionUrl;
    url = url.replace("{transactionsId}",id);

    return function (dispatch){
      return APIUtil.getMethod(url, true).then(response => {

          // dispatch
          if(response.status == 200) {
              dispatch( {
                  type: types.GET_TRANSACTION,
                  transactionData: response.data.data[0]
              });
              return response.data.data[0]
          }
          else {
              dispatch( {
                  type: types.GET_TRANSACTION,
                  transactionData: []
              });

              return {}
          }
      });
    }  
}


/**
 * @desc Transaction - update by ID
 */
export function updateTransactionDataAction(id,data) {  

    let url = UrlConstants.UpdateTransactionUrl;
    url = url.replace("{transactionsId}",id);

    return function (dispatch){
      return APIUtil.putMethod(url,data, true).then(response => {

          // dispatch
          if(response.status == 200) {
              dispatch( {
                  type: types.UPDATE_TRANSACTION,
                  updatedTransData: response.data
              });
          }
          else {
              dispatch( {
                  type: types.UPDATE_TRANSACTION,
                  updatedTransData: []
              });
          }
          return response.data;
      });
    }  
}

