/**
 * 
 * @desc Delete Task/Event
 */
export function deleteTaskAction(id) {

    return function (dispatch){
        return APIUtil.deleteMethod(UrlConstants.transactionEvent+"?id="+id, true).then(response => {
            if(response.status === 200) {                
                dispatch( {
                    type: types.DELETE_TASK,
                });
            } 
            else { 
                dispatch( {
                    type: types.DELETE_TASK
                });
            }
            return response
        });
    } 
}


/**
 * 
 * @desc User - Update Task
 */
export function updateTaskAction(data, id) {

    return function (dispatch){
        return APIUtil.putMethod(UrlConstants.transactionEvent+"?id="+id, data, true).then(response => { 
            if(response._id !== '') {      
                dispatch( {
                    type: types.UPDATE_TASK,
                });
            }
            else {
                dispatch( {
                    type: types.UPDATE_TASK
                });
            }    
            if(response){
                return response.data;      
            } else {
                return []
            }
            
        });
    } 
}
