/**
 * @desc User - Add Comment
 */
export function addCommentAction(data, eid) {
    return function (dispatch){
        return APIUtil.postMethod(UrlConstants.comments+eid, data, true).then(response => {
          if(response.status == 200) {
            dispatch( {
                type: types.ADD_COMMENT,
                uploadSuccess: true
            });
        }
        else {
            dispatch( {
                type: types.ADD_COMMENT,
                uploadSuccess: false
            });
        }
            return response;
        });
    }
}



/**
 * @desc Transactions - status update
 */
export function changeTransactionStatusAction(transactionId,data) {  

    let url = UrlConstants.transactionStatusUrl;
    url = url.replace("{transactionId}",transactionId);
   
    return function (dispatch){
      return APIUtil.putMethod(url,data, true).then(response => {
          // dispatch
          if(response.status == 200) {
              dispatch( {
                  type: types.TRANSACTION_STATUS,
                  transactionStatus: response.data
              });
          }
          else {
              dispatch( {
                  type: types.TRANSACTION_STATUS,
                  transactionStatus: []
              });
          }
      });
    }  
}


