	/**
	 * A semi-generic way to handle multiple lists. Matches
	 * the IDs of the droppable container to the names of the
	 * source arrays stored in the state.
	 */
	id2List = {
		droppable: 'items',
		droppable2: 'selected',
		droppable0: 'sd'
	};

	getList = id => this.state.tasksList[id] || [];

	getStyle(style, snapshot) {
		if (!snapshot.isDropAnimating) {
			return style;
		}
		const { moveTo, curve, duration } = snapshot.dropAnimation;
		// move to the right spot
		const translate = `translate(${moveTo.x}px, ${moveTo.y}px)`;
		// add a bit of turn for fun
		const scale = 'scale(1.05)';

		// patching the existing style
		return {
			...style,
			//   animation: 'ScaleAnimation 1s normal forwards ease-in-out',
			background: '#ff8',
			borderColor: '#e6e67a',
			transform: `${translate} ${scale}`,
			// slowing down the drop because we can
			transition: `all ${curve} ${duration + 1}s`,
		};
	}

	onDragEnd = result => {
		const { source, destination } = result;

		// dropped outside the list
		if (!destination) {
			return;
		}

		if (source.droppableId === destination.droppableId) {
			const items = reorder(
				this.getList(source.droppableId),
				source.index,
				destination.index
			);

			let state = { items };

			if (source.droppableId === 'droppable2') {
				state = { selected: items };
			}

			this.setState(state);
		} else {
			const result1 = move(
				this.getList(source.droppableId),
				this.getList(destination.droppableId),
				source,
				destination
			);
			var tasksList = this.state.tasksList;
			tasksList[source.droppableId] = result1[source.droppableId];
			tasksList[destination.droppableId] = result1[destination.droppableId];
			tasksList[destination.droppableId][0].day = destination.droppableId.replace('droppable', '')
			this.getDataToPost(tasksList[destination.droppableId][0])
			this.updateTaskData(tasksList[destination.droppableId][0], 'drag');
			// this.setState({
			// 	tasksList
			// });

		}
	};

