	// Edit Campaign modal
	editCampaign(camp) {

		this.setState({ editCampaign: !this.state.editCampaign });
		if (camp !== "") {
			this.setState({
				initialValueEditCampaign: {
					"campainId": camp._id,
					"campainName": camp.name,
					"campaignDays": camp.campaignDays,
					"campaignDesc": camp.description,
					"contingencies": camp.contingencies,
				},
				isCampUpdate: true,
				isTemplateOptSelected: true,
			});
		} else {

			let contigencies = {}
			for (let i = 0; i < this.state.allContingencies.length; i++) {
				contigencies[this.state.allContingencies[i].name] = this.state.allContingencies[i].name;
			}
			this.setState({
				initialValueEditCampaign: {
					"campainName": "",
					"campaignDays": "",
					"campaignDesc": "",
					"contingencies": contigencies
				},
				isCampUpdate: false,
				isTemplateOptSelected: false,
			});
		}
	}

	getContingenciesByCampIdForClone(id) {
		let contingenciesListAr = this.state.campaignstyles;
		let data = [];
		for (let i = 0; i < contingenciesListAr.length; i++) {
			if (contingenciesListAr[i]._id === id) {
				data = contingenciesListAr[i].contingencies;
			}
		}
		return data;
	}

	//Add new campaign data - API call
	createCampaign(data) {
		this.setState({ showLoader: true })
		let contingencies = data.contingencies;
		let postData;
		if (this.state.isTemplateOptSelected && this.state.isCampFromClone) {
			postData = {
				"campaign": data.selectedCampaign,
				"name": data.campainName,
				"description": data.campaignDesc,
				"campaignDays": data.campaignDays,
				"contingencies": contingencies
			}
			// call api for copy
			this.props.adminCopyCampaignMaterTemplateAction(postData).then(res => {
				let newcampaign = this.state.campaignstyles;
				newcampaign.push(this.props.addedCampaign)
				this.setState({ campaignstyles: newcampaign });
				this.changeCampaign(this.props.addedCampaign._id);
				this.setState({ showLoader: false })
			});

		}
		else {
			postData = {
				"name": data.campainName,
				"description": data.campaignDesc,
				"campaignDays": data.campaignDays,
				"contingencies": contingencies
			}
			//call api for create
			this.props.createCampaignAction(postData).then(res => {
				let newcampaign = this.state.campaignstyles;
				newcampaign.push(this.props.newCampaign)
				this.setState({ campaignstyles: newcampaign });
				this.changeCampaign(this.props.newCampaign._id);
				this.setState({ showLoader: false })
			});
		}

		this.setState({ editCampaign: !this.state.editCampaign });
	}

	// On change options
	selectCampaignCreate(e) {
		if (e.target.value == 0) {
			this.setState({ isTemplateOptSelected: true, isCampFromClone: true })
		} else {
			this.setState({ isTemplateOptSelected: true, isCampFromClone: false })
		}
	}

	existingCampaignList() {

		let campaignList = [];

		for (var i = 0, len = this.state.campaignstyles.length; i < len; i++) {
			let camp = this.state.campaignstyles[i];
			let data = <option value={camp._id} key={"camp-" + camp._id}>{camp.name}</option>
			campaignList.push(data);
		}
		return (campaignList);
	}

