	changeDay(type) {
		if (type == 'prev') {
			$('.go-to-dropdown').val($('.go-to-dropdown').val() - 1 >= 0 ? $('.go-to-dropdown').val() - 1 : 0);
		} else {
			$('.go-to-dropdown').val(Number($('.go-to-dropdown').val()) < this.state.campaignDaysCnt - 1 ? (Number($('.go-to-dropdown').val()) + 1) : this.state.campaignDaysCnt - 1);
		}
		this.goToDate();
	}

	getMinutes(date) {
		return (moment(date).hour() * 60 + moment(date).minute())
	}

	updateAssigneeOptions(con, setFieldValue) {
		let assigneeOptions = [];
		this.state.variablesRefs.forEach(v => {
			if ((v.contingency === 'all' || v.contingency === con) && v.account === 'user') {
				assigneeOptions.push({ label: v.name, value: v.name });
			}
		})
		this.setState({
			assigneeOptions
		}, () => {
			setFieldValue('assignee', '')
		})
	}


	clearFilter() {
		this.setState({
			filterContingency: null,
			filterType: null,
			tasksList: this.state.originalTaskList,
			filtered: false
		})
	}

	filterMasterTemplate(filterParam, filterFrom) {
		var tasksList = filterFrom;
		var returnTaskList = {}
		Object.keys(tasksList).forEach(e => {
			returnTaskList[e] = tasksList[e].filter((task) => {
				var found = false;
				var found1 = false;
				var foundText = false;
				var foundEmail = false;
				filterParam.forEach(f => {
					if (f) {

						if (f.value == 'Interactive') {
							found = task.type.name === "INTERACTIVE";
						}
						if (f.value == 'Text') {
							foundText = task.communicationType === f.value.toUpperCase();
						}
						if (f.value == 'Email') {
							foundEmail = task.communicationType === f.value.toUpperCase();
						}
						if (task.contingency === f.value) {
							found1 = true;
						}
					}
					if (this.state.filterType == null) {
						found = true
						foundText = true
						foundEmail = true
					}
					if (this.state.filterContingency == null) {
						found1 = true;
					}

				})
				// console.log('1',task.label,param, ((task.label+"").trim().toLowerCase()).includes((param+"").trim().toLowerCase()))
				return ((found || foundText || foundEmail) && found1)//((task.label+'').trim().toLowerCase()).includes((param+'').trim().toLowerCase())
			})
		})
		// searchMasterTemplate(param, searchFrom)
		this.setState({
			tasksList: returnTaskList,
			filtered: true
		})
	}

	searchMasterTemplate(param, searchFrom) {
		var tasksList = searchFrom;
		var returnTaskList = {}
		Object.keys(tasksList).forEach(e => {
			returnTaskList[e] = tasksList[e].filter((task) => {
				// console.log('1',task.label,param, ((task.label+"").trim().toLowerCase()).includes((param+"").trim().toLowerCase()))
				return ((task.label + '').trim().toLowerCase()).includes((param + '').trim().toLowerCase())
			})
		})
		this.setState({
			tasksList: returnTaskList, searchParam: param
		})
	}

	getContingencyOptions() {
		var options = [];
		Object.keys((this.state.currentCampian.contingencies || {})).forEach(e => {
			options.push({ label: e, value: e })
		})
		this.setState({ contingencyOptions: options });
	}


