import React from 'react';

// Bootstrap components
import { Button, Modal } from "react-bootstrap";
import { editContactAction, getContactByIdAction, getUserRolesAction, addUserRolesAction } from '../../actions/UserActions';
import { connect } from 'react-redux';
import * as Yup from "yup";
import { Field, Form, Formik, ErrorMessage } from 'formik';

class EditContact extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      page: 0,
      isEmailValid: false,
      contact: {
        role: "Home Inspector", // required
        usertype: "VENDOR", // required
        firstname: "admin",
        lastname: "admin",
        mobileNumber: 1234567890,
        email: 'mai@fsd.co',
        showLoader: false
      }
    };
  }

  componentDidMount() {
    this.props.getUserRolesAction();
    this.props.getContactByIdAction(this.props.contactData.id);
  }

  next() {
    this.setState({
      page: 1
    })
  }

  addContact(values) {
    this.setState({
      showLoader: true
    })
    var dataToPut = {
      roles: values.roles,
      default: values.default
    }

    if (values.roles[0] == 'other') {
      this.props.addUserRolesAction(values.other).then(res => {
        dataToPut.roles = res._id;
        this.props.editContactAction(dataToPut, this.props.contact.contactId).then(res => {
          this.setState({
            showLoader: false
          })
          this.props.onClose();
        });
      })
    } else {
      this.props.editContactAction(dataToPut, this.props.contact.contactId).then(res => {
        this.setState({
          showLoader: false
        })
        this.props.onClose();
      });
    }

  }

  setFieldValue(name, e) {
    var contact = this.state.contact
    contact[name] = e
    this.setState({
      contact
    })
  }

  render() {
    var contact = this.props.contact
    return (
      <div>
        {this.state.showLoader &&
          <div className="page-loader">
            <div className="page-loader-inner">
              <div className="spinner-border" role="status">
                <span className="sr-only">Loading...</span>
              </div>
            </div>
          </div>
        }
        <Modal.Body>
          <Formik
            validateOnBlur={true}
            initialValues={this.props.contact}
            enableReinitialize={true}

            validationSchema={Yup.object().shape({
              role: Yup.array().of(
                Yup.string().trim().required('Vendor Type is required.')),
              // firstname: Yup.string().trim().required('First Name is required.'),
              // lastname: Yup.string().trim().required('Last Name is required.'),
              // email: Yup.string().trim().email('Please enter valid Email address.').required('Email ID is required'),
              // mobileNumber: Yup.string()
              //   .required('Mobile Number is required.')
              //   .min(10, "Mobile Number Must be of 10 digits")
              //   .max(10, "Mobile Number Must be of 10 digits"),
            })}
            onSubmit={(values) => {
              console.log(values)
              this.addContact(values)
            }}
          >
            {({
              handleChange,
              values,
              errors,
              setFieldValue
            }) => (
                <Form className="form-sm form-admin">
                  <>
                    <div className="form-group">
                      <label>Email ID</label>
                      <Field type="email" className="form-control" name="email" value={values.email} disabled handleChange={handleChange} />
                    </div>
                    <div className="form-group">
                      <label>Select Vendor Type<span className="text-danger">*</span></label>
                      <div className="c-select">
                        <select className="form-control" name="role[0]" value={values.roles ? values.roles[0] : ''} onChange={(e) => { setFieldValue("roles[0]", e.target.value) }}>
                          <option value='' >Select</option>
                          {this.props.roles &&
                            this.props.roles.map(role => {
                              return <option value={role._id}>{role.name}</option>
                            })
                          }
                          <option value='other'>Other</option>
                        </select>
                      </div>
                      <ErrorMessage name="roles[0]" component="div" className="invalid-feedback" style={{ display: "block" }} />

                    </div>
                    {(values.roles && values.roles[0] == 'other') &&
                      <div className="form-group">
                        <Field type="text" name="other" className="form-control" placeholder='Enter Custom type' required handleChange={handleChange} />
                      </div>}

                  </>
                  <div className="form-group">
                    <div className="custom-control custom-checkbox custom-control-inline">
                      <input type="checkbox" name="DefaultVendor" className="custom-control-input" id="DefaultVendor" checked={values.default} onChange={(e) => { setFieldValue('default', e.target.checked) }} style={{ marginLeft: "10px" }} />
                      <label className="custom-control-label" htmlFor="DefaultVendor">Default Vendor</label>
                    </div>
                  </div>

                  <div className="form-group">
                    <label>First Name</label>
                    <Field type="text" name="firstname" className="form-control" value={values.firstname} disabled handleChange={handleChange} />
                  </div>
                  <div className="form-group">
                    <label>Last Name</label>
                    <Field type="text" name="lastname" className="form-control" value={values.lastname} disabled handleChange={handleChange} />
                  </div>
                  <div className="form-group">
                    <label>Phone No.</label>
                    <Field type="number" name="mobileNumber" className="form-control" value={values.mobileNumber} disabled handleChange={handleChange} />
                  </div>
                  <Button variant="primary" size="sm" className="btn-blue w-140" type="submit" >Update</Button>

                </Form>)}
          </Formik>
        </Modal.Body>
        <Modal.Footer>
          {/* {this.state.page == 0 &&
            <Button variant="primary" size="sm" className="btn-blue w-140" onClick={() => this.next()}>Next</Button>
          } */}
          {/* {this.state.page == 1 &&
            <Button variant="primary" size="sm" className="btn-blue w-140" type="submit" >{this.state.isEmailValid ? 'Save' : 'Add'}</Button>
          } */}
          {/* <Button variant="primary" size="sm" className="btn-outlined btn-blue w-140">Save & Create New</Button> */}
        </Modal.Footer>
      </div>
    );
  }

}


const mapStateToProps = state => {
  return {
    contactResponse: state.reducer.contactResponse,
    contact: state.reducer.contact,
    roles: state.reducer.roles
  };
};


export default connect(mapStateToProps, { editContactAction, getContactByIdAction, getUserRolesAction, addUserRolesAction })(EditContact);
