
import * as types from './../actions/actions';

const initialState = {isAuthenticated : false, users: [], 
    buyerAgents : [] , 
    profileData : {}, 
    isMasterTemplateUpdated : false,
    isEventCommCreated : false,
    isMasterTemplateTaskDeleted : false,
    status : false
    //adminMasterTemplates : [],
   // variablesRefs : []
};

function reducer(state = initialState,action){
    switch(action.type){
        case types.REGISTER_USER:
            return Object.assign({}, state, {
                status: action.status,
                RegMessage : action.RegMessage
            });
        case types.LOGIN_USER:
            return Object.assign({}, state, {
                isAuthenticated: action.isAuthenticated,
                LoginMessage : action.LoginMessage
            });
        
        case types.REGISTRATION_REQUESTS:
            return Object.assign({}, state, {
                users: action.users,
                totalUsers : action.totalUsers
            });

        case types.LOGOUT:
            return Object.assign({}, state, {
                isAuthenticated: false,
            });

        case types.ACTIVE_USER:
            return Object.assign({}, state, {
                status: action.status,
                UserMessage : action.UserMessage
            });
         
        case types.BUYER_AGENT_LIST:
            return Object.assign({}, state, {
                buyerAgents: action.buyerAgents,
                totalBuyerAgents : action.totalBuyerAgents
            });

        case types.MY_PROFILE:
            return Object.assign({}, state, {
                profileData: action.profileData
            });

        case types.UPDATE_PROFILE:
            return Object.assign({}, state, {
                updatedProfileData: action.updatedProfileData
            });

        case types.ADMIN_INVITE_AGENT:
            return Object.assign({}, state, {
                invieAgentMsg: action.invieAgentMsg,
                checkStatus: action.checkStatus,
            });

            //Admin - master template
        case types.ADMIN_GET_MASTER_TEMPLATE_DATA:
            return Object.assign({}, state, {
                adminMasterTemplates: action.adminMasterTemplates,
                totalAdminTemplates : action.totalAdminTemplates
            });

        case types.ADMIN_UPDATE_MASTER_TEMPLATE_DATA:
            return Object.assign({}, state, {
                isMasterTemplateUpdated: action.isMasterTemplateUpdated,
                updatedEventData : action.updatedEventData
            });

        case types.CREATE_EVENT_COMM_MASTER_TEMPLATE:
            return Object.assign({}, state, {
                isEventCommCreated: action.isEventCommCreated,
                addedEventComm : action.addedEventComm
            });

        case types.MASTER_TEMPLATE_GET_ALL_VERIABLE_REF:
            return Object.assign({}, state, {
                variablesRefs: action.variablesRefs,
            });

        case types.DELETE_EVENT_COMM_MASTER_TEMPLATE:
            return Object.assign({}, state, {
                isMasterTemplateTaskDeleted: action.isMasterTemplateTaskDeleted,
            });

        case types.MASTER_TEMPLATE_GET_CAMPAIGN_DATA_BY_ID:
            return Object.assign({}, state, {
                campDataById: action.campDataById,
            });
            
        case types.CREATE_CAMPAIGN_MASTER_TEMPLATE:
            return Object.assign({}, state, {
                isCampaignCreated : action.isCampaignCreated,
                addedCampaign : action.addedCampaign
            });

        case types.COPY_CAMPAIGN_MASTER_TEMPLATE:
            return Object.assign({}, state, {
                isCampaignCreated : action.isCampaignCreated,
                addedCampaign : action.addedCampaign
            });

        case types.GET_CONTINGENCIES:
            return Object.assign({}, state, {
                contingencies : action.contingencies
            });
    

        case types.ADD_CONTACT:
            return Object.assign({}, state, {
                contactResponse: action.contactResponse,
            });

        case types.EDIT_CONTACT:
            return Object.assign({}, state, {
                contactResponse: action.contactResponse
            });

        case types.GET_CONTACT:
            return Object.assign({}, state, {
                contacts: action.contacts,
            });
        
        case types.GET_ROLES:
            return Object.assign({}, state, {
                roles: action.roles,
            });
        
        case types.GET_BY_ID_CONTACT:
            return Object.assign({}, state, {
                contact: action.contact,
            });       

        // Buyer
        case types.BUYER_GET_MASTER_TEMPLATE_DATA:
            return Object.assign({}, state, {
                buyerMasterTemplates: action.buyerMasterTemplates,
                totalBuyerTemplates : action.totalBuyerTemplates
            });
        
        case types.BUYER_CREATE_TASK:
            return Object.assign({}, state, {
                isEventCommCreated: action.isEventCommCreated,
                addedEventComm : action.addedEventComm,
            });
        
        case types.BUYER_UPDATE_MASTER_TEMPLATE_DATA:
            return Object.assign({}, state, {
                isMasterTemplateUpdatedBuyer: action.isMasterTemplateUpdatedBuyer,
                updatedEventDataBuyer : action.updatedEventDataBuyer
            });
        case types.MASTER_TEMPLATE_GET_CAMPAIGN_DATA_BY_ID_BUYER:
            return Object.assign({}, state, {
                BuyerCampDataById: action.BuyerCampDataById
            }); 
            
        case types.BUYER_CREATE_MASTER_TEMPLATE:
            return Object.assign({}, state, {
                newCampaign: action.newCampaign
            }); 

        case types.CREATE_TRANSACTION:
            return Object.assign({}, state, {
                isTrascationCreated: action.isTrascationCreated,
                createTransactionData: action.createTransactionData
            }); 

        case types.GET_ALL_TRANSACTIONS:
            return Object.assign({}, state, {
                transactions: action.transactions,
            }); 

        case types.GET_ALL_TRANSACTIONS:
            return Object.assign({}, state, {
                allContingencies: action.allContingencies,
            }); 
        
        case types.GET_ALL_TASKS:
            return Object.assign({}, state, {
                tasks: action.tasks,
            }); 

        case types.GET_TASK_DETAILS:
            return Object.assign({}, state, {
                taskDetails: action.taskDetails,
            }); 
            
        case types.GET_TRANSACTION:
            return Object.assign({}, state, {
                transactionData: action.transactionData,
            }); 
        case types.UPDATE_TRANSACTION:
            return Object.assign({}, state, {
                updatedTransData: action.updatedTransData,
            });
            case types.FORGOT_PASSWORD:
            return Object.assign({}, state, {
                forgotdata: action.forgotdata,
            }); 
            case types.VALIDATE_RESET_TOKEN:
            return Object.assign({}, state, {
                tokenfailed: action.tokenfailed,
            }); 

        case types.DELETE_TRANSACTION:
            return Object.assign({}, state, {
                isDTransDeleted: action.isDTransDeleted,
            });
        case types.VENDORS_LIST:
            return Object.assign({}, state, {
                vendorUsers: action.vendorUsers,
                totalVendors: action.totalVendors,
            });
        case types.REMOVED_REGISTRATION:
            return Object.assign({}, state, {
                UserMessage: action.UserMessage,
                status: action.status,
            });
        case types.REMOVED_USER:
            return Object.assign({}, state, {
                UserMessage: action.UserMessage,
                status: action.status,
            });
        case types.REJECT_REGISTRATION:
            return Object.assign({}, state, {
                UserMessage: action.UserMessage,
                status: action.status,
            });
        case types.REJECT_REGISTRATION_LIST:
            return Object.assign({}, state, {
                users: action.users,
                status: action.status,
            });
        case types.GET_DEPENDENCY_DATA:
            return Object.assign({}, state, {
                dependencyTasks: action.dependencyTasks,
            });
        case types.PUT_DEPENDENCY_DATA:
            return Object.assign({}, state, {
                dependencyTasks: action.dependencyTasks,
                status: action.status,
                statusMessage: action.statusMessage,
            });

        case types.GET_ALL_VENDOR_TRANSACTIONS:
            return Object.assign({}, state, {
                vendorTransactions: action.vendorTransactions,
            });
            
        case types.GET_ALL_VENDOR_TASKS:
            return Object.assign({}, state, {
                vendorTasks: action.vendorTasks,
            });
            
        case types.TRANSACTION_STATUS:
            return Object.assign({}, state, {
                transactionStatus: action.transactionStatus,
            });

        case types.TRANSACTION_DEPEDENDENT_TASK:
            return Object.assign({}, state, {
                transactionTaskStatus: action.transactionTaskStatus,
            });

        case types.GET_ALL_BUYER_TRANSACTIONS:
            return Object.assign({}, state, {
                buyerTransactions: action.buyerTransactions,
            });
        case types.GET_ALL_BUYER_TASKS:
            return Object.assign({}, state, {
                buyerTasks: action.buyerTasks,
            });

        case types.TRANSACTION_ACTIVITY:
            return Object.assign({}, state, {
                transactionActivities: action.transactionActivities,
            });

        case types.TRANSACTION_ACTIVITY_USER:
            return Object.assign({}, state, {
                activityUsers: action.activityUsers,
            });

        case types.BUYERS_LIST:
            return Object.assign({}, state, {
                buyers: action.buyers,
                totalbuyers: action.totalbuyers,
            });
        case types.GET_UPCOMING_TASKS_DASHBOARD:
            return Object.assign({}, state, {
                upcomingTasksData: action.upcomingTasksData,
                
            });

        case types.DASHBOARD_TRANSACTIONS_VALUE:
            return Object.assign({}, state, {
                valuesData: action.valuesData,
        });
        case types.DASHBOARD_TRANSACTIONS:
            return Object.assign({}, state, {
                dashboardTranscations : action.dashboardTranscations,
                countTransactions : action.countTransactions,           
            });



        default:
            return state;
    }
  }

export default reducer;
