import React from "react";

// Bootstrap components 
import { Button, Alert } from "react-bootstrap";

// import pages
import HeaderLogin from "./../HeaderLogin/HeaderLogin.jsx";

import * as Yup from "yup";
import { Formik, Field, Form, ErrorMessage } from 'formik';

// icons
import { EmailOutlined, VpnKey } from '@material-ui/icons';

import { connect } from 'react-redux'
import { loginUserAction, myProfileAction } from './../../actions/UserActions'

const initialValue = {
  email: '',
  password: '',
};

class Login extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      accountLocked: false,
      passwordNotSet: false
    }
    // props.message = ''
  }

  onChange(event) {
    this.setState({
      [event.target.name]: event.target.value
    })
  }

  loginAction(data) {
    data.email = data.email.toLowerCase();
    localStorage.setItem('EMAIL', data.email);
    this.props.loginUserAction(data).then(res => {

      if(res.status == 200) {
        this.props.myProfileAction();
      }

      if (res.status === 403 && res.data === 'Account locked.') {
        this.setState({
          LoginMessage: 'Your account has been locked for more than 3 failed login attemps',
          accountLocked: true
        })
      }
      if (res.status === 400 && res.data === 'Password not set') {
        localStorage.setItem('PS', false);
        this.setState({
          LoginMessage: 'Your access password is not set.',
          passwordNotSet: true,
          accountLocked: true
        })
      }
    });
  }

  postLogin() {
    
    var userType = localStorage.getItem('usertype');
    if (userType !== null && userType.toUpperCase() === 'ADMIN') {
      this.props.history.push("/RegistrationRequests")
    }
    else if (userType !== null && userType.toUpperCase() === 'BUYERAGENT') {
      this.props.history.push("/dashboard")
    }    
    else {      
      this.props.history.push("/transactions")
    }
  }

  goToRegister() {
    this.props.history.push("/register")
  }
  goToForgotPassword() {
    if (this.state.passwordNotSet) {
      this.props.history.push("/resetpassword/setpass")
    } else {
      this.props.history.push("/forgotpassword")
    }

  }

  // componentWillReceiveProps(nextProps) {
  //   // console.log('componentWillReceiveProps', nextProps);
  //   // this.setState(nextProps);
  // }

  render() {

    if (localStorage.getItem('accessToken')) {
      this.postLogin();
    }

    // console.log(this.props.history.push("/register"));
    return (
      <div>

        {/* header */}
        <HeaderLogin />

        {/* login box */}
        <div className='loginBg loginContent align-items-end flex-column'>
          <div className='loginBox'>
            <h4>Login to your account</h4>
            {((this.props.LoginMessage != '' && this.props.LoginMessage !== undefined) || (this.state.LoginMessage !== undefined)) &&
              <Alert variant="danger">{this.state.LoginMessage ? this.state.LoginMessage : this.props.LoginMessage }</Alert>
            }
            {/* <Alert variant="success">Your password has been Successfully changed!</Alert> */}
            {
              (!this.state.accountLocked && this.state.passwordNotSet == false) ? (
                <Formik
                  validateOnBlur={true}
                  initialValues={initialValue}
                  enableReinitialize={true}
                  //Validation schema
                  validationSchema={Yup.object().shape({
                    email: Yup.string()
                      .email('Please enter valid Email address.')
                      .required('Please enter Email.'),
                    password: Yup.string().nullable().trim()
                      .required('Please enter Password.')
                      .min(6, "Password should be of minimum 6 character.")

                  })}
                  //Form submit method
                  onSubmit={(values, { setSubmitting }) => {
                    //console.log("adf",values)
                    this.loginAction(values);
                  }}
                >
                  {({ handleChange,
                    errors,
                    values,
                    handleBlur,
                    setFieldValue,
                    submitForm,
                    touched,
                    loginError
                  }) => (

                      <Form>

                        <div className="form-group">
                          <label className="form-label" htmlFor="email">Email address</label>
                          <div className="icon-wrapper input-group">
                            <Field
                              type="email"
                              name="email"
                              type="email"
                              value={values.email}
                              className={'form-control form-control-lg' + (errors.email && touched.email ? ' is-invalid' : '')}
                            ></Field>
                            <span className="form-control-icn">
                              <EmailOutlined />
                            </span>
                            <ErrorMessage name="email" component="div" className="invalid-feedback" style={{ display: "block" }} />
                          </div>
                        </div>

                        <div className="form-group mb-0">
                          <label className="form-label" htmlFor="password">Password</label>
                          <div className="icon-wrapper input-group">
                            <Field
                              name="password"
                              type="password"
                              value={values.password}
                              className={'form-control form-control-lg ' + (errors.password && touched.password ? ' is-invalid' : '')}
                            ></Field>
                            <span className="form-control-icn">
                              <VpnKey />
                            </span>
                            <ErrorMessage name="password" component="div" className="invalid-feedback" style={{ display: "block" }} />
                          </div>
                        </div>

                        <div className="form-group text-right">
                          <Button variant="link" className='pr-0 font-14 font-500' style={{ color: '#4a90e2' }} onClick={() => this.goToForgotPassword()}>Forgot Password?</Button>
                        </div>

                        <div className="form-group pt-2">
                          <Button type="submit" variant="primary" size='lg' block className='btn-blue btn-medium' >Login</Button>
                        </div>

                        <div className="form-group pt-4 text-center mb-0">
                          <span className='font-14 color-grey-1 font-300'>Don't have an account?</span> <Button variant="link" className='pl-0 pr-0 font-14 font-500' style={{ color: '#4a90e2' }} onClick={() => this.goToRegister()}>Register</Button>
                        </div>

                      </Form>
                    )}
                </Formik>
              ) : (
                  <Button variant="link" className='pr-0 font-14 font-500' style={{ color: '#4a90e2' }} onClick={() => this.goToForgotPassword()}> Please Click here to reset your password.</Button>
                )
            }
          </div>
        </div>
      </div>
    );
  }

}


const mapStateToProps = state => {
  return {
    isAuthenticated: state.reducer.isAuthenticated,
    LoginMessage: state.reducer.LoginMessage
  };
};

//export default  Login;


export default connect(mapStateToProps, { loginUserAction, myProfileAction })(Login)
