	// show Task Type
	showTaskType(e) {

		this.setState({ selectValue: e.target.value });
		if (e.target.value == 'COMMUNICATION') {
			this.state.initialValueAddTask.taskType = "COMMUNICATION";
			this.setState({ taskType: "COMMUNICATION", showText: true });
			if (this.state.initialValueAddTask.communicationType === 0) {
				this.setState({ showText: true });
			} else {
				this.setState({ showText: false });
			}

		} else {
			this.state.initialValueAddTask.taskType = "INTERACTIVE";
			this.setState({ taskType: "INTERACTIVE", showText: false });
		}
	}

	// Communication Type condition
	showText(e) {
		if (e.target.value == '0') {
			this.state.initialValueAddTask.communicationType = 0;
			this.setState({ showText: true });

		} else {
			this.state.initialValueAddTask.communicationType = 1;
			this.setState({ showText: false });
		}
	}

