	//Update campaign data - API call
	updateCampaignData(data) {
		this.setState({ editCampaign: !this.state.editCampaign, showLoader: true })
		let contingencies = data.contingencies;

		let postData = { "name": data.campainName, "description": data.campaignDesc, "campaignDays": data.campaignDays, "contingencies": contingencies }
		// call api for update
		this.props.adminUpdateMaterTemplateAction(postData, "campaign", data.campainId, 0, 0, 0).then(res => {

			//update contingencies to state
			let newcampaignstyles = this.state.campaignstyles;
			for (let i = 0; i < newcampaignstyles.length; i++) {
				if (newcampaignstyles[i]._id === data.campainId) {
					newcampaignstyles[i].contingencies = this.props.updatedEventData.contingencies;
					newcampaignstyles[i].campaignDesc = this.props.updatedEventData.description;
					newcampaignstyles[i].name = this.props.updatedEventData.name;
					newcampaignstyles[i].campaignDays = this.props.updatedEventData.campaignDays;

				}
			}
			this.setState({ campaignstyles: newcampaignstyles });

			this.changeCampaign(data.campainId);
			this.setState({ showLoader: false })
		});
	}

	selectCampaignCreate(e) {
		if (e.target.value == 0) {
			this.setState({ isTemplateOptSelected: true, isCampFromClone: true })
		} else {
			this.setState({ isTemplateOptSelected: true, isCampFromClone: false })
		}
	}

	//Add new campaign data - API call
	createCampaign(data) {
		this.setState({ showLoader: true })
		let contingencies = data.contingencies;
		if (this.state.isTemplateOptSelected && this.state.isCampFromClone) {
			let postData = {
				"campaign": data.selectedCampaign,
				"name": data.campainName,
				"description": "",
				"campaignDays": data.campaignDays,
				"contingencies": contingencies
			}
			// call api for copy
			this.props.adminCopyCampaignMaterTemplateAction(postData).then(res => {
				let newcampaign = this.state.campaignstyles;
				newcampaign.push(this.props.addedCampaign)
				this.setState({ campaignstyles: newcampaign });
				this.changeCampaign(this.props.addedCampaign._id);
				this.setState({ showLoader: false })
			});
		}
		else {
			let postData = {
				"name": data.campainName,
				"description": data.campaignDesc,
				"campaignDays": data.campaignDays,
				"contingencies": contingencies
			}
			// call api for create
			this.props.adminCreatCampaignMaterTemplateAction(postData).then(res => {
				let newcampaign = this.state.campaignstyles;
				newcampaign.push(this.props.addedCampaign)
				this.setState({ campaignstyles: newcampaign });
				this.changeCampaign(this.props.addedCampaign._id);
				this.setState({ showLoader: false })
			});
		}

		this.setState({ editCampaign: !this.state.editCampaign });
	}

	existingCampaignList() {

		let campaignList = [];

		for (var i = 0, len = this.state.campaignstyles.length; i < len; i++) {
			let camp = this.state.campaignstyles[i];
			let data = <option value={camp._id} key={"camp-" + camp._id}>{camp.name}</option>
			campaignList.push(data);
		}
		return (campaignList);
	}
	//Update task data - API call 
	updateTaskData(data, dragType) {
		if (dragType !== 'drag') {
			this.setState({ showLoader: true })
		}
		let templateData = {}
		let postData = {};
		let type = 0;


		if (data.taskType === "INTERACTIVE") {
			let type = 1;
			let to = (data.assignee !== undefined && data.assignee !== null && data.assignee !== "") ? data.assignee.replace('{(', '').replace(')}', '') : ""
			templateData = {
				"to": data.assignee,
				"via": "",
				"subject": "",
				"what": data.detailsMsg,
				"document": data.document
			}

			postData = {
				"eventtemplate": {
					"_id": data.eventtemplateId,
					"predecessor": data.predecessor,
					"successor": data.successor,
					"eventType": data.taskType,
					"predecessorData": data.predecessorData ? data.predecessorData : [],
					"successorData": data.successorData ? data.successorData : [],
					"day": data.eventDay,
					"title": data.taskTitle,
					"contingency": data.contingency,
					"campaign": this.state.currentCampian._id,
					"assignee": to,
					"description": data.detailsMsg,
					"document": data.document,
					"time": data.time
				}
			}

		}
		else {
			if (data.communicationType === 0) {
				// email
				let to = data.emailTo.replace('{(', '').replace(')}', '')
				let sub = data.emailSubject.replace('{(', '').replace(')}', '')
				templateData = {
					"to": to,
					"via": "EMAIL",
					"subject": sub,
					"what": data.emailText
				}
			} else {
				//text
				let to = data.msgTo.replace('{(', '').replace(')}', '')
				templateData = {
					"to": to,
					"via": "TEXT",
					"subject": "",
					"what": data.msgText
				}
			}

			postData = {
				"eventtemplate": {
					"predecessor": data.predecessor,
					"successor": data.successor,
					"_id": data.eventtemplateId,
					"eventType": data.taskType,
					"predecessorData": data.predecessorData ? data.predecessorData : [],
					"successorData": data.successorData ? data.successorData : [],
					"day": data.eventDay,
					"title": data.taskTitle,
					"contingency": data.contingency,
					"campaign": this.state.currentCampian._id,
					"time": data.time
				},
				"communicationtemplate": {
					"template": templateData,
					"eventtemplateId": data.eventtemplateId,
				}
			}

		}
		//   console.log("form",postData)

		// call api for update
		this.props.adminUpdateMaterTemplateAction(postData, "both", 0, data.eventtemplateId, data.commId, type).then(res => {

			if (res != undefined && res.length > 0) {
				var affectedTasks = res || [];
				this.setState({
					moveConfirmationModal: true,
					affectedTasks,
					moveTaskPostData: { postData: postData, model: "both", eventtemplateId: data.eventtemplateId, communicationtemplateId: data.commId, type: type }
				})
			} else {

				if (res != undefined && res.hasOwnProperty('message')) {
					this.setState({ moveErrorModal: true, moveErrorMessage: res.message })
				} else {
					this.setState({ showLoader: true })
					this.props.adminUpdateDependentTasksAction(postData, "both", 0, data.eventtemplateId, data.commId, type).then(res => {
						this.setState({ showLoader: false })
						this.changeCampaign(this.state.currentCampian._id);
					});
				}
			}
			// else{

			// }
			// if (dragType != 'drag') {

			// }
		});

		this.setState({
			addTask: ""
		});
	}
	//Create new task - API call
	createTaskData(data) {
		this.setState({ showLoader: true })

		let postData = {
			"campaign": this.state.currentCampian._id,
			"title": data.taskTitle,
			"day": data.eventDay,
			"contingency": data.contingency,
			"eventType": data.taskType,
			"time": data.time
		}

		if (data.taskType === "INTERACTIVE") {
			postData['assignee'] = data.assignee.replace('{(', '').replace(')}', '');
			postData.description = data.detailsMsg;
			postData.document = data.document;
		}
		else {
			if (data.communicationType === 0) {
				// email
				let to = data.emailTo.replace('{(', '').replace(')}', '')
				let sub = data.emailSubject.replace('{(', '').replace(')}', '')
				postData.communicationType = "EMAIL"
				postData.communicationTo = to
				postData.communicationSubject = sub
				postData.description = data.emailText
			} else {
				//text
				let to = data.msgTo.replace('{(', '').replace(')}', '')
				postData.communicationType = "TEXT"
				postData.communicationTo = to
				postData.description = data.msgText
			}
		}
		//	console.log("form",postData)
		// call api for add
		this.props.adminCreatEventCommMaterTemplateAction(postData).then(res => {
			this.changeCampaign(this.state.currentCampian._id);
		});
		this.setState({ addTask: "" });
		this.setState({
			initialValueAddTask: {
				"taskTitle": "",
				"taskType": "Communication",
				"communicationType": 0,
				"emailTo": "",
				"emailSubject": "",
				"emailText": "",
				"msgTo": "",
				"msgText": "",
				"assignee": "",
				"detailsMsg": "",
				"contingency": "",
				"document": true,
				"eventDay": 0,
				"time": 540,
			}
		});
	}
