	changeCampaign(id) {
		this.setState({ showLoader: true, addTask: "" })

		this.props.adminGetCampDataByIdAction(id).then(res => {

			//Set default data to load
			this.setState({ campaignDaysCnt: this.props.campDataById[0].campaignDays, activeCampaign: this.props.campDataById[0].name });
			this.setState({ currentCampian: this.props.campDataById[0] }, () => {
				this.getContingencyOptions()
			});

			let recordToLoad = "";
			let tasksList = {};
			if (this.props.campDataById !== undefined) {
				recordToLoad = this.props.campDataById[0];
			}

			let DayWiseTasks = [];

			if (recordToLoad !== undefined && recordToLoad !== "" && recordToLoad !== null) {
				let events = recordToLoad.events;
				for (var i = 0, len = events.length; i < len; i++) {
					DayWiseTasks['day' + events[i].day] = DayWiseTasks['day' + events[i].day] || []
					DayWiseTasks['day' + events[i].day].push(events[i]);
				}
			}

			let data = [];
			for (var i = 0, len = this.state.campaignDaysCnt; i < len; i++) {

				var tasks = (DayWiseTasks['day' + i] !== undefined) ? DayWiseTasks['day' + i] : [];

				let dayTasks = [];

				for (var j = 0; j < tasks.length; j++) {
					if (tasks[j].type.name === "COMMUNICATION") {
						let communications = (tasks[j] !== undefined && tasks[j].communication !== undefined) ? tasks[j].communication : [];
						for (var k = 0; k < communications.length; k++) {
							var d = communications[k];
							d.type = tasks[j].type;
							d.eventType = tasks[j].eventType;
							d.isActive = tasks[j].isActive;
							d.predecessor = tasks[j].predecessor;
							d.predecessorData = tasks[j].predecessorData ? tasks[j].predecessorData : [];
							d.successorData = tasks[j].successorData ? tasks[j].successorData : [];
							d.successor = tasks[j].successor;
							d.day = tasks[j].day;
							d.label = tasks[j].title;

							d.contingency = tasks[j].contingency;
							d['modifiedDate'] = tasks[j].modifiedDate;
							d['time'] = tasks[j].time;

							dayTasks.push(d);
						}
					}
					else {
						var d = { template: tasks[j].template };
						d._id = tasks[j]._id;
						d.eventtemplateId = tasks[j]._id;
						d.predecessor = tasks[j].predecessor;
						d.predecessorData = tasks[j].predecessorData ? tasks[j].predecessorData : [];
						d.successorData = tasks[j].successorData ? tasks[j].successorData : [];
						d.successor = tasks[j].successor;
						d.type = tasks[j].type;
						d.eventType = tasks[j].eventType;
						d.isActive = tasks[j].isActive;
						d.day = tasks[j].day;
						d.label = tasks[j].title;
						d.contingency = tasks[j].contingency;
						d['modifiedDate'] = tasks[j].modifiedDate;
						d['time'] = tasks[j].time;
						dayTasks.push(d);
					}
					if (d.predecessorData.length > 0) {
						this.setState({
							dependencyIsActive: true
						})
					} else {
						this.setState({
							dependencyIsActive: false
						})
					}
				}

				//console.log("dayTasks",dayTasks)
				dayTasks.sort((a, b) => { return a.modifiedDate > b.modifiedDate ? -1 : 1; })
				tasksList["droppable" + i] = dayTasks;

			}
			this.setState({
				tasksList,
				originalTaskList: tasksList,
				showLoader: false
			})
		});
	}

	getContingenciesByCampIdForClone(id) {
		let contingenciesListAr = this.state.campaignstyles;
		let data = [];
		for (let i = 0; i < contingenciesListAr.length; i++) {
			if (contingenciesListAr[i]._id === id) {
				data = contingenciesListAr[i].contingencies;
			}
		}
		return data;
	}

	changeDay(type) {
		if (type == 'prev') {
			$('.go-to-dropdown').val($('.go-to-dropdown').val() - 1 >= 0 ? $('.go-to-dropdown').val() - 1 : 0);
		} else {
			$('.go-to-dropdown').val(Number($('.go-to-dropdown').val()) < this.state.campaignDaysCnt - 1 ? (Number($('.go-to-dropdown').val()) + 1) : this.state.campaignDaysCnt - 1);
		}
		this.goToDate();
	}

	confirmMove() {
		this.setState({ moveConfirmationModal: false, showLoader: true })
		this.props.adminUpdateDependentTasksAction(this.state.moveTaskPostData.postData, "both", 0,
			this.state.moveTaskPostData.eventtemplateId, this.state.moveTaskPostData.communicationtemplateId,
			this.state.moveTaskPostData.type).then((res) => {
				this.setState({ showLoader: false });
				this.changeCampaign(this.state.currentCampian._id);
			});
	}

	closeErrorModal() {
		this.setState({ moveErrorModal: false })
		this.changeCampaign(this.state.currentCampian._id);
	}

	updateAssigneeOptions(con, setFieldValue) {
		let assigneeOptions = [];
		this.state.variablesRefs.forEach(v => {
			if ((v.contingency === 'all' || v.contingency === con) && v.account === 'user') {
				assigneeOptions.push({ label: v.name, value: v.name });
			}
		})
		this.setState({
			assigneeOptions
		}, () => {
			setFieldValue('assignee', '')
		})
	}


	clearFilter() {
		this.setState({
			filterContingency: null,
			filterType: null,
			tasksList: this.state.originalTaskList,
			filtered: false
		})
	}

	filterMasterTemplate(filterParam, filterFrom) {
		var tasksList = filterFrom;
		var returnTaskList = {}
		Object.keys(tasksList).forEach(e => {
			returnTaskList[e] = tasksList[e].filter((task) => {
				console.log(task)
				var found = false;
				var found1 = false;
				var foundText = false;
				var foundEmail = false;
				filterParam.forEach(f => {
					if (f) {

						if (f.value == 'Interactive') {
							found = task.type.name === "INTERACTIVE";
						}
						if (f.value == 'Text') {
							foundText = task.template.via === f.value.toUpperCase();
						}
						if (f.value == 'Email') {
							foundEmail = task.template.via === f.value.toUpperCase();
						}
						if (task.contingency === f.value) {
							found1 = true;
						}
					}
					if (this.state.filterType == null) {
						found = true
						foundText = true
						foundEmail = true
					}
					if (this.state.filterContingency == null) {
						found1 = true;
					}

				})
				// console.log('1',task.label,param, ((task.label+"").trim().toLowerCase()).includes((param+"").trim().toLowerCase()))
				return ((found || foundText || foundEmail) && found1)//((task.label+'').trim().toLowerCase()).includes((param+'').trim().toLowerCase())
			})
		})
		// searchMasterTemplate(param, searchFrom)
		this.setState({
			tasksList: returnTaskList,
			filtered: true
		})
	}

	searchMasterTemplate(param, searchFrom) {
		var tasksList = searchFrom;
		var returnTaskList = {}
		Object.keys(tasksList).forEach(e => {
			returnTaskList[e] = tasksList[e].filter((task) => {
				// console.log('1',task.label,param, ((task.label+"").trim().toLowerCase()).includes((param+"").trim().toLowerCase()))
				return ((task.label + '').trim().toLowerCase()).includes((param + '').trim().toLowerCase())
			})
		})
		this.setState({
			tasksList: returnTaskList, searchParam: param
		})
	}

	getContingencyOptions() {
		var options = [];
		Object.keys((this.state.currentCampian.contingencies || {})).forEach(e => {
			options.push({ label: e, value: e })
		})
		this.setState({ contingencyOptions: options });
	}

