/**
 * 
 * @desc User - Buyer Agent registration API call
 */
export function registerUserAction(data) {

    return function (dispatch) {
        return APIUtil.postMethod(UrlConstants.RegisterUrl, data, false).then(response => {
            // dispatch  
            // console.log("response",response)    
            if (response.status === 200) {

                if (response.data === 'User already registered.') {
                    dispatch({
                        type: types.REGISTER_USER,
                        status: false,
                        RegMessage: "Email address already exist."
                    });
                }
                else {
                    dispatch({
                        type: types.REGISTER_USER,
                        status: true,
                        RegMessage: "Thanks for registering with us, Our team will review your application and you will be notified once your account is Approved."
                    });
                }
            }
            else if (response.status === 400) {
                dispatch({
                    type: types.REGISTER_USER,
                    status: false,
                    RegMessage: "Something went wrong. Please try again!"
                });
            }
            else {
                dispatch({
                    type: types.REGISTER_USER,
                    status: false,
                    RegMessage: "Something went wrong. Please try again!"
                });
            }
        });
    }
}
/**
 * 
 * @desc Login User API call
 */

export function loginUserAction(data) {
    data = { "data": data }
    return function (dispatch) {
        return APIUtil.postMethod(UrlConstants.LoginUrl, data, false).then(response => {
            // dispatch
            if (response.status === 200) {
                var accessToken = response.data;
                var playload = JSON.parse(atob(accessToken.split('.')[1]));

                localStorage.setItem("accessToken", accessToken);
                localStorage.setItem("usertype", playload.usertype);
                localStorage.setItem("ID", playload._id);

                dispatch({
                    type: types.LOGIN_USER,
                    isAuthenticated: true,
                    LoginMessage: ""
                });
            }
            else if (response.status === 403) {
                dispatch({
                    type: types.LOGIN_USER,
                    isAuthenticated: false,
                    LoginMessage: "Please try after some time, your account is still under review."
                });
            }
            else if (response.status === 400) {
                dispatch({
                    type: types.LOGIN_USER,
                    isAuthenticated: false,
                    LoginMessage: "Incorrect credentials."
                });
            }
            else {
                dispatch({
                    type: types.LOGIN_USER,
                    isAuthenticated: false,
                    LoginMessage: "Incorrect credentials."
                });
            }
            return response;
        });
    }
}
/**
 * 
 * @desc List Registration requests API call
 */

export function registrationRequestsAction(recordPerPage, lastSyncTimestamp) {

    var url = UrlConstants.RegistrationRequestsUrl.replace("{recordPerPage}", recordPerPage);
    var finalurl = url.replace("{modifieddate}", lastSyncTimestamp);

    return function (dispatch) {
        return APIUtil.getMethod(finalurl, true).then(response => {
            // dispatch
            // console.log("sdaf",response)
            if (response.status === 200) {
                dispatch({
                    type: types.REGISTRATION_REQUESTS,
                    users: response.data.data,
                    totalUsers: response.data.total
                });
            }
            else {
                dispatch({
                    type: types.REGISTRATION_REQUESTS,
                    users: {}
                });
            }
        });
    }
}

/**
 * 
 * @desc Logout action
 */
export function logout() {
    return function (dispatch) {
        localStorage.removeItem("accessToken");
        localStorage.removeItem("usertype");

        dispatch({
            type: types.LOGOUT
        });
    }
}

/**
 * 
 * @desc Active buyer agent API call
 */
export function removeRegistartionAction(userID) {

    return function (dispatch) {
        let url = UrlConstants.removeRegistrationRequest + userID
        return APIUtil.deleteMethod(url, true).then(response => {
            if (response.status === 200) {
                dispatch({
                    type: types.REMOVED_REGISTRATION,
                    status: true,
                    UserMessage: "Registration Request has been removed."
                });
            }
            else if (response.status === 400) {
                dispatch({
                    type: types.REMOVED_REGISTRATION,
                    status: false,
                    UserMessage: "Something went wrong. Please try again!"
                });
            }
            else {
                dispatch({
                    type: types.REMOVED_REGISTRATION,
                    status: false,
                    UserMessage: "Something went wrong. Please try again!"
                });
            }
            return response;
        });
    }
}
