/**
 *  
 * @desc GET profile details of logged in user
 */
export function myProfileAction(id) {
    return function (dispatch) {
        let finalURL = ''
        let USERID = ''
        if (id) {
            USERID = id
            finalURL = UrlConstants.getProfileUrl + id;
        } else {
            finalURL = UrlConstants.MyProfileUrl;
        }
        return APIUtil.getMethod(finalURL, true).then(response => {
            // dispatch
            if (response.status === 200) {

                if(USERID == "") {
                    localStorage.setItem("fname", response.data.firstname);
                    localStorage.setItem("lname", response.data.lastname);
                    localStorage.setItem("ID", response.data._id);
                }
                dispatch({
                    type: types.MY_PROFILE,
                    profileData: response.data
                });
            }
            else {
                dispatch({
                    type: types.MY_PROFILE,
                    profileData: {}
                });
            }
        });
    }
}

