export function validateResetTokenAction(Token, apidata, module) {
    return function (dispatch) {
        let final = ''
        if (module) {
            final = UrlConstants.setVendorPassword
        } else {
            final = UrlConstants.validateResetPasswordToken + Token
        }
        return APIUtil.postMethod(final, apidata).then(response => {
            // dispatch
            if (response.status === 200) {
                dispatch({
                    type: types.VALIDATE_RESET_TOKEN,
                    tokensuccess: response.data
                });
            }
            else {
                dispatch({
                    type: types.VALIDATE_RESET_TOKEN,
                    tokenfailed: response.data
                });
            }
            return response;
        });
    }
}

