import React from "react";

// Bootstrap components 
import { Button, Alert } from "react-bootstrap";

// import pages
import HeaderLogin from "./../HeaderLogin/HeaderLogin.jsx";

import * as Yup from "yup";
import { Formik, Field, Form, ErrorMessage } from 'formik';

// icons
import { EmailOutlined, VpnKey } from '@material-ui/icons';

import { connect } from 'react-redux'
import { forgotPasswordAction } from './../../actions/UserActions'

const initialValue = {
  email: ''
};

class ForgotPassword extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      forgotSuccess: false
    }
    // props.message = ''
  }

  onChange(event) {
    this.setState({
      [event.target.name]: event.target.value
    })
  }

  forgotpasswordAction(data) {
    data.email = data.email.toLowerCase();
    this.props.forgotPasswordAction(data).then(res => {
      if (res.status === 200) {
        this.setState({
          forgotSuccess: true
        })
      }
    });
  }


  goToLogin() {
    this.props.history.push("/login")
  }

  componentWillReceiveProps(nextProps) {
    this.setState(nextProps);
  }

  render() {
    // console.log(this.props.history.push("/register"));
    return (
      <div>

        {/* header */}
        <HeaderLogin />

        {/* login box */}
        <div className='loginBg loginContent align-items-end flex-column'>
          <div className='loginBox'>
            {(!this.state.forgotSuccess) ? <div>
              <h4>Forgot Password?</h4>
              {(this.props.forgotdata != '' && this.props.forgotdata !== undefined) &&
                <Alert variant="danger">{this.props.forgotdata}</Alert>
              }
              <Formik
                validateOnBlur={true}
                initialValues={initialValue}
                enableReinitialize={true}
                //Validation schema
                validationSchema={Yup.object().shape({
                  email: Yup.string()
                    .email('Please enter valid Email address.')
                    .required('Please enter Email.')

                })}
                //Form submit method
                onSubmit={(values, { setSubmitting }) => {
                  //console.log("adf",values)
                  this.forgotpasswordAction(values);
                }}
              >
                {({ handleChange,
                  errors,
                  values,
                  handleBlur,
                  setFieldValue,
                  submitForm,
                  touched,
                  loginError
                }) => (

                    <Form>

                      <div className="form-group">
                        <label className="form-label" htmlFor="email">Email address</label>
                        <div className="icon-wrapper input-group">
                          <Field
                            type="email"
                            name="email"
                            type="email"
                            value={values.email}
                            className={'form-control form-control-lg' + (errors.email && touched.email ? ' is-invalid' : '')}
                          ></Field>
                          <span className="form-control-icn">
                            <EmailOutlined />
                          </span>
                          <ErrorMessage name="email" component="div" className="invalid-feedback" style={{ display: "block" }} />
                          <p className="font-14 color-grey-1 font-300 mt-1">Enter your email id and we will email you the instructions to reset your password.</p>
                        </div>
                      </div>
                      <div className="form-group pt-1">
                        <Button type="submit" variant="primary" size='lg' block className='btn-blue btn-medium' >Submit</Button>
                      </div>

                      <div className="form-group pt-4 text-center mb-0">
                        <span className='font-14 color-grey-1 font-300'>Remember Password ?</span> <Button variant="link" className='pl-0 pr-0 font-14 font-500' style={{ color: '#4a90e2' }} onClick={() => this.goToLogin()}>Login</Button>
                      </div>

                    </Form>
                  )}
              </Formik>


            </div> :

              <div className="message-log-before mb-3">
                <h4 className="mb-3">Reset Instructions</h4>
                <p className="font-14 color-grey-1 font-400">We have received your request, Please check your inbox for the password reset instructions.</p>
                <Button variant="primary" className='btn-blue btn-medium btn btn-block btn-lg' onClick={() => this.goToLogin()}>Login</Button>
              </div>
            }


          </div>
        </div>
      </div>
    );
  }

}


const mapStateToProps = state => {
  return {
    forgotdata: state.reducer.forgotdata,
    forgotsuccess: state.reducer.forgotsuccess
  };
};

//export default  Login;


export default connect(mapStateToProps, { forgotPasswordAction })(ForgotPassword)
