import React from "react";

import StepZilla from "react-stepzilla";

// Bootstrap components 
import { Alert } from "react-bootstrap";

// import pages
import HeaderLogin from "./../HeaderLogin/HeaderLogin.jsx";
import PersonalInfo from "./Steps/PersonalInfo";
import CompanyInfo from "./Steps/CompanyInfo";
import CommissionStep from "./Steps/Commission";
import ReviewStep from "./Steps/Review";

import { connect } from 'react-redux'
import { registerUserAction } from './../../actions/UserActions'

import $ from 'jquery'

var today = new Date();
var date = (today.getMonth()+1)+'/'+today.getDate()+'/'+today.getFullYear();

class Register extends React.Component {

  // defaultState = {
  //   //roles: [
  //  //     "BUYERAGENT"
  //  // ],
  //  usertype:"BUYERAGENT",
  //   firstname : 'a',
  //   lastname : 'a',
  //   email: 'a@aa.com',
  //   recoveryEmail: 'a@aa.com',
  //   password : '123456',
  //   confirmPassword : '123456',
  //   mobileNumber : '1235467890',
  //   otherNumber : '',
  //   brokerLicenseNo : '32122',
  //   licenseExpirationDate : date,
  //   knowAboutUs : '',
  //   companyInformation : {
  //     name : '',
  //     website : '',
  //     address : '',
  //     zipcode : '',
  //     city : '',
  //     state : '',
  //     phoneNumber : '',
  //     faxNumber : '',
  //     typeOfBusiness : '',
  //     companySize : ''
  //   },
  //   address: "",
  //   bio: "-",
  //   education: " ",
  //   certifications: "",
  //   awards: "",
  //   jobTitle: "",
  //   quote: "",
  //   avatar: "-",
  //   socialMediaHandles: {
  //       facebook: "",
  //       twitter: "",
  //       instagram: ""
  //   },
  //   education : [],
  //   certifications : [],
  //   awards : [],
  //   regMsgDisplay : false
  // };

  defaultState = {
    //roles: [
   //     "BUYERAGENT"
   // ],
   usertype:"BUYERAGENT",
    firstname : '',
    lastname : '',
    email: '',
    recoveryEmail: '',
    password : '',
    confirmPassword : '',
    mobileNumber : '',
    otherNumber : '',
    brokerLicenseNo : '',
    licenseExpirationDate : date,
    knowAboutUs : '',
    companyInformation : {
      name : '',
      website : '',
      address : '',
      zipcode : '',
      city : '',
      state : '',
      phoneNumber : '',
      faxNumber : '',
      typeOfBusiness : '',
      companySize : ''
    },
    address: "",
    bio: "-",
    education: " ",
    certifications: "",
    awards: "",
    jobTitle: "",
    quote: "",
    avatar: "-",
    socialMediaHandles: {
        facebook: "",
        twitter: "",
        instagram: ""
    },
    education : [],
    certifications : [],
    awards : [],
    regMsgDisplay : false
  };

  constructor(props) {
    super(props);
    this.state = this.defaultState
  }

  getUserData() {
    return this.state;
  }

  updateUserData(key,value) {
    this.setState({
        [key] :value
    })
  }

  updateUserCmpnyData(key,value) {

    var comp = this.state.companyInformation;
    comp[key] = value
    this.setState({companyInformation : comp});

    // console.log("companyInformation",this.state.companyInformation)
  }

  componentWillReceiveProps(nextProps) {
    // console.log('componentWillReceiveProps', nextProps);
     this.setState(nextProps);
   }

   getLastStep(step) {
   
    this.setState({regMsgDisplay : false});

    if(step === 0 || step === 1 || step === 2) {
      $(".saveBtn").html('Next')
    }

    if(step === 3) {
      $(".saveBtn").html('Confirm & Register')
    }

    if(step === 4) {
      this.submitAction();
    }
   }

   submitAction() {
      let data = this.state;
      delete data.recoveryEmail;
      delete data.confirmPassword;

      data.mobileNumber = data.mobileNumber.replace(/\-/g, "")
      data.otherNumber = data.otherNumber.replace(/\-/g, "")
      data.companyInformation.phoneNumber = data.companyInformation.phoneNumber.replace(/\-/g, "")


      this.props.registerUserAction(data).then(res => {

        if(this.props.status === true) {
            this.setState(this.defaultState);   
            this.props.history.push("/thankyou")         
        }
        this.setState({regMsgDisplay : true});        
      });
  }


  checkLoggedIn() {
    
    var userType = localStorage.getItem('usertype');
    if (userType !== null && userType.toUpperCase() === 'ADMIN') {
      this.props.history.push("/RegistrationRequests")
    } else {
      this.props.history.push("/transactions")
    }
  }

  render() {

    if (localStorage.getItem('accessToken')) {
      this.checkLoggedIn();
    }

    var cls = "multi-step";
    if( this.state.regMsgDisplay === true && this.props.status === true) {      
      cls = "multi-step error";
    }

    if(this.state.regMsgDisplay === true && this.props.status === false) {
      cls = "multi-step error";
    }


    const steps =
    [
      {name: 'Personal Info', component: <PersonalInfo getUserData={() => (this.getUserData())} updateUserData={(k,v) => {this.updateUserData(k,v)}} />},
      {name: 'Company Info', component: <CompanyInfo  getUserData={() => (this.getUserData())} updateUserData={(k,v) => {this.updateUserCmpnyData(k,v)}} />},
      {name: 'Commission', component: <CommissionStep />},
      {name: 'Review', component: <ReviewStep getUserData={() => (this.getUserData())} />}
    ]
  
    
    return (
      
      <div className="reg-page">
        <HeaderLogin/>
        <div className='loginBg'>
          <div className='step-progress'>
              <div className="reg-title-wrapper">
                <h4 className="title-big mt-0 pt-2">Create Your Account</h4>
              </div>

              {(this.state.regMsgDisplay === true && this.props.RegMessage != '' && this.props.RegMessage !== undefined && this.props.status === true) &&
                        <Alert variant="success">{this.props.RegMessage}</Alert>
              }

            {(this.state.regMsgDisplay === true && this.props.RegMessage != '' && this.props.RegMessage !== undefined && this.props.status === false) &&
                        <Alert variant="danger">{this.props.RegMessage}</Alert>
              }

              <div className={cls}>
                <StepZilla 
                  steps={steps} 
                  showNavigation={true}
                  backButtonCls="btn btn-sm btn-primary btn-blue btn-outlined w-140 mr-3"
                  nextButtonCls="btn btn-sm btn-primary btn-blue w-140 saveBtn"
                  onStepChange = {(step) => this.getLastStep(step)}
                  hocValidationAppliedTo = {[0,1]}
                />
              </div>
          </div>
        </div>
      </div>
    );
  }

}

const mapStateToProps = state => {
  return {  
    status: state.reducer.status,
    RegMessage: state.reducer.RegMessage
  };
};



export default connect(mapStateToProps, {registerUserAction})(Register)
