import React, { Component } from 'react'
import { Route, Redirect } from 'react-router-dom';
import Sidebar from "components/Sidebar/Sidebar.jsx";
import Header from 'components/Header/Header.jsx';

export const PrivateRoute = ({ miniActive:miniActive, onUpdate, component: Component, ...rest }) => (
    <Route {...rest} render={props => (
        (localStorage.getItem("accessToken") !== undefined && localStorage.getItem("accessToken") !== "" && localStorage.getItem("accessToken") != null )
            ? <React.Fragment> 
                <Sidebar miniActive={miniActive}/>
                <div className={miniActive+" contentArea"}>
                <Header miniActive={miniActive} onUpdate={onUpdate}/>
                <Component {...props} /></div>
            </React.Fragment>
            : 
            <Redirect to={{ pathname: '/login' }} />
    )} />
)