// icons
import { Close, DateRange, Loop, PauseCircleOutline, PlayCircleOutline } from '@material-ui/icons';
import 'bootstrap-daterangepicker/daterangepicker.css';
import { Form, Formik } from 'formik';
import moment from 'moment';
import React from 'react';
// Bootstrap components
import { Button, Col, InputGroup, Modal, Nav, Row, Tab } from 'react-bootstrap';
// date picker
import DateRangePicker from 'react-bootstrap-daterangepicker';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import * as Yup from "yup";
import UrlConstants from '../../../config/UrlConstants';
import Documents from '../../Documents/Documents';
import { getBuyerAgentMasterTemplateAction } from './../../../actions/BuyerAgentMasterTemplateActions';
import { changeTransactionStatusAction, getTransactionDataAction } from './../../../actions/TransactionActions';
import placeholderImage from './../../../assets/img/property-placeholder.jpg';
import Activity from './Activity';
import TimelineComponent from './Timeline';
import TransactionDetailTab from './transaction-detail-tab';
import TransactionTask from './transaction-task';


var todayDate = new Date();
var minDate = (todayDate.getMonth() + 1) + '/' + todayDate.getDate() + '/' + todayDate.getFullYear();


class Detail extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			allTransdata: [],
			propertyDataInfo: {},
			ownerId: '',
			userRole: localStorage.getItem('usertype').toUpperCase(),
			transStatus: "SCHEDULED",
			startTransaction: false,
			promptMsg: "",
			initialValues: {
				startDate: "",
				closeDate: ""
			},
			chanedSts: "SCHEDULED",
			showStartDate: true,
			showCloseDate: true,
			campaignStyle: [],
			updated : false,
			activeKey: 'first',
		};
	}

	componentWillMount() {
		let transId = this.props.match.params.transId;
		this.props.getTransactionDataAction(transId).then(res => {
			if (
				this.props.transactionData !== undefined &&
				this.props.transactionData !== null
			) {


				let initialValues = {
					startDate: this.props.transactionData.startDate,
					closeDate: this.props.transactionData.endDate
				}
				this.setState({
					allTransdata: this.props.transactionData,
					propertyDataInfo: this.props.transactionData.property || {},
					ownerId: this.props.transactionData.ownerId,
					transStatus: this.props.transactionData.status,
					initialValues: initialValues
				},()=>{
					if(this.props.history.location.taskId != undefined){
						console.log('in here')
						this.setState({
							activeKey : 'second'
						})
					}
				});
			}
		});
		this.props.getBuyerAgentMasterTemplateAction().then(res => {
			this.setState({ campaignStyle: this.props.buyerMasterTemplates })
		})
	}

	changeTransactionStatus(val) {
		let transId = this.props.match.params.transId;


		let data = { "status": this.state.chanedSts };

		// if (this.state.showStartDate) {
		data.startDate = new Date(val.startDate)
		// }

		// if (this.state.showCloseDate) {
		data.endDate = new Date(val.closeDate)
		// }

		this.props.changeTransactionStatusAction(transId, data).then(res => {

			this.props.getTransactionDataAction(transId).then(res => {
				if (
					this.props.transactionData !== undefined &&
					this.props.transactionData !== null
				) {
					let initialValues = {
						startDate: this.props.transactionData.startDate,
						closeDate: this.props.transactionData.endDate
					}
					this.setState({
						allTransdata: res,
						propertyDataInfo: this.props.transactionData.property || {},
						ownerId: this.props.transactionData.ownerId,
						transStatus: this.props.transactionData.status,
						initialValues: initialValues,
						promptMsg: "", startTransaction: false,
						updated : true,
					});
				}
			});

		})
	}

	openConfirmPopup(sts) {

		let msg = "";
		if (sts === "start") {
			msg = "Please confirm the new start date & closing date";
			this.setState({ showStartDate: true, showCloseDate: true, chanedSts: "INPROGRESS" })
		}

		if (sts === "resume") {
			msg = "Please select the new closing date";
			this.setState({ showStartDate: false, showCloseDate: true, chanedSts: "INPROGRESS" })
		}

		if (sts === "pause") {
			msg = "All the pending tasks will be stopped and the transaction will be marked as Paused, Are you sure want to continue?"
			this.setState({ showStartDate: false, showCloseDate: false, chanedSts: "PAUSED" })
		}

		if (sts === "cancel") {
			msg = "All the pending tasks will be marked as cancled. Are you sure want to continue?"
			this.setState({ showStartDate: false, showCloseDate: false, chanedSts: "CANCELED" })
		}

		if (msg !== "") {
			this.setState({ promptMsg: msg, startTransaction: true })
		}

	}

	closeConfirmPopup() {
		this.setState({ promptMsg: "", startTransaction: false })
	}

	gotoUserProfile(stateValues){
		this.props.history.push("/UserProfile/" + stateValues.ownerId)
	}
	render() {
		let transId = this.props.match.params.transId;

		let img = '';

		if (this.state.propertyDataInfo !== undefined) {
			let imgs = this.state.propertyDataInfo.image;
			if (imgs !== undefined && imgs.length > 0) {
				img = this.state.propertyDataInfo.image[0];
			}
		}


		let sts = ""; //<Button variant="transparent" className="switch-button py-0">Start <PlayCircleOutline /></Button>;
		if (this.state.userRole !== "BUYER") {
			if (this.state.transStatus === "SCHEDULED") {
				sts = <Button variant="transparent"
					onClick={() => this.openConfirmPopup("start")}
					//onClick={() => this.changeTransactionStatus("INPROGRESS")}
					className="switch-button py-0"><span>Start</span> <PlayCircleOutline /></Button>;
			}

			if (this.state.transStatus === "INPROGRESS") {
				sts = <Button variant="transparent"
					onClick={() => this.openConfirmPopup("pause")}
					// onClick={() => this.changeTransactionStatus("PAUSED")}
					className="switch-button py-0"> <span>Pause</span> <PauseCircleOutline /></Button>;
			}

			if (this.state.transStatus === "PAUSED") {
				sts = <Button variant="transparent"
					onClick={() => this.openConfirmPopup("resume")}
					//onClick={() => this.changeTransactionStatus("INPROGRESS")}
					className="switch-button py-0"> <span>Resume</span> <Loop /></Button>;
			}

			if (this.state.transStatus === "CANCELED") {
				sts = <Button variant="transparent"
					onClick={() => this.openConfirmPopup("start")}
					//onClick={() => this.changeTransactionStatus("INPROGRESS")}
					className="switch-button py-0"><span>Start</span> <PlayCircleOutline /></Button>;
			}
		}




		return (
			<div>
				<Tab.Container
					id="left-tabs-example"
					defaultActiveKey='first'
					activeKey={this.state.activeKey}
					onSelect={k => {this.props.history.location.taskId = undefined ;this.setState({ activeKey : k})}} //First removes task id from history then navigates to specific tab
					mountOnEnter={true}
					unmountOnExit={true}
				>
					<Row className="ml-0 mr-0 page-head-row page-head-row-big align-items-center">
						<Col xs="12" sm="3">
							<div className="trans-combo align-items-center">
								<div className="trans-img">
									<img
										src={
											img != ''
												? UrlConstants.AdminCreateTransactionUrl +
												'/upload?id=' +
												this.state.ownerId +
												'&img=' +
												img
												: placeholderImage
										}
										alt=""
									/>
								</div>
								<div className="trans-info">
									<h5>
										{this.state.propertyDataInfo.street +
											(this.state.propertyDataInfo
												.street1 !== ''
												? ' ' +
												this.state.propertyDataInfo
													.street1
												: '')}
									</h5>
									<p>
										{this.state.propertyDataInfo.city +
											', ' +
											this.state.propertyDataInfo.state +
											' ' +
											this.state.propertyDataInfo.zipcode}
									</p>
									{(this.state.userRole == 'BUYER') ?
										<p>Buyer Agent:
											<span className="color-dark-blue font-500 c-pointer text-capitalize" onClick={() => this.gotoUserProfile(this.state)}> 
												{' '+this.state.allTransdata.buyerAgentFirstname + ' ' + this.state.allTransdata.buyerAgentLastName}
											</span>
										</p> : ''
									}
								</div>
							</div>
						</Col>
						{(this.state.userRole !== 'VENDOR')
							?
							<Col xs="12" sm="6">
								{(this.state.userRole === 'BUYER') ?
									<Nav className="justify-content-center align-items-end trans-tabs">
										<Nav.Item>
											<Nav.Link eventKey="first">
												Transaction Detail
									</Nav.Link>
										</Nav.Item>
										<Nav.Item>
											<Nav.Link eventKey="second">
												Task
									</Nav.Link>
										</Nav.Item>
										<Nav.Item>
											<Nav.Link eventKey="fifth">
												Document
									</Nav.Link>
										</Nav.Item>
									</Nav>
									:
									<Nav className="justify-content-center align-items-end trans-tabs">
										<Nav.Item>
											<Nav.Link eventKey="first">
												Transaction Detail
									</Nav.Link>
										</Nav.Item>
										<Nav.Item>
											<Nav.Link eventKey="second">Task</Nav.Link>
										</Nav.Item>
										<Nav.Item className="hide-767">
											<Nav.Link eventKey="third">
												Timeline
									</Nav.Link>
										</Nav.Item>
										{/* <Nav.Item>
									<Nav.Link eventKey="fourth">
										Contact
									</Nav.Link>
								</Nav.Item> */}
										<Nav.Item>
											<Nav.Link eventKey="fifth">
												Document
									</Nav.Link>
										</Nav.Item>
										<Nav.Item className="hide-767">
											<Nav.Link eventKey="sixth">
												Activity
									</Nav.Link>
										</Nav.Item>
									</Nav>
								}




							</Col> : ""
						}
						{(this.state.userRole === 'VENDOR')
							?


							<Col xs="12" sm="6" >
								<Nav className="justify-content-center align-items-end trans-tabs">

									<Nav.Item>
										<Nav.Link eventKey="first">
											Task
										</Nav.Link>
									</Nav.Item>
									<Nav.Item>
										<Nav.Link eventKey="second">
											Document
										</Nav.Link>
									</Nav.Item>
								</Nav>
							</Col>
							: ''}

						< Col xs="12" sm="3">



							<div className="d-flex justify-content-end align-items-center switch-div">


								{/* <div className="custom-control switch-right material-switch">
                    <input type="checkbox" id="customSwitch1"/>
                    <label htmlFor="customSwitch1" className="mb-0 font-12">Pause Transaction</label>
                  </div> */}
								<div>
									{(this.state.userRole === 'VENDOR' || this.state.userRole === 'BUYER')
										? ""
										:
										<div>{sts}</div>

									}


									{/* <Button variant="transparent" className="switch-button">
                        <PlayCircleOutline/>
                    </Button>
                    <Button variant="transparent" className="switch-button">
                        <Loop/>
                    </Button> */}
								</div>
								<Link
									to="/transactions"
									className="nav-link p-0 color-black sep-left"
								>
									<Close
										style={{
											stroke: '#fff',
											height: '40px',
											width: '40px'
										}}
									/>
								</Link>
							</div>

						</Col>
					</Row>
					<div className="trans-tab-content">

						{(this.state.userRole === 'VENDOR')
							?
							<Tab.Content>

								<Tab.Pane eventKey="first">
									<TransactionTask
										transactionId={transId}
										history={this.props.history}
										transactionData={this.state.allTransdata}
									/>
								</Tab.Pane>

								<Tab.Pane eventKey="second">
									<Documents transactionId={transId} />
								</Tab.Pane>

							</Tab.Content>

							:

							<Tab.Content>
								<Tab.Pane eventKey="first">
									<TransactionDetailTab
										transactionId={transId}
										isDataUpdate={data =>
											this.setState({
												propertyDataInfo: data
											})
										}
										isTransactionUpdate={data =>
											this.setState({
												allTransdata: data
											})
										}
										setTransSts={(transStatus) => this.setState({ transStatus: transStatus })}
										transStatus={this.state.transStatus}
										updatedData={this.state.allTransdata}
										updated={this.state.updated}
									/>
								</Tab.Pane>
								<Tab.Pane eventKey="second">
									<TransactionTask
										transactionId={transId}
										history={this.props.history}
										transactionData={this.state.allTransdata}
									/>
								</Tab.Pane>
								<Tab.Pane eventKey="third" transactionId={transId}>
									<TimelineComponent
										transactionId={transId}
										transactionData={this.state.allTransdata} />
								</Tab.Pane>
								{/* <Tab.Pane eventKey="fourth" transactionId={transId}>
                      <Contacts/>
                    </Tab.Pane> */}
								<Tab.Pane eventKey="fifth">
									<Documents transactionId={transId} />
								</Tab.Pane>
								<Tab.Pane eventKey="sixth">
									<Activity transactionId={transId} transactionData={this.state.allTransdata} />
								</Tab.Pane>
							</Tab.Content>

						}

					</div>
				</Tab.Container>

				{/* Start transaction Confirmation */}
				<Modal
					show={this.state.startTransaction}
					onHide={() => this.closeConfirmPopup()}
					dialogClassName="modal-90w"
					aria-labelledby="example-custom-modal-styling-title"
					className="modal-med"
				>

					<Formik
						validateOnBlur={true}
						initialValues={this.state.initialValues}
						enableReinitialize={true}
						//Validation schema
						validationSchema={Yup.object().shape({

						})}
						//Form submit method
						onSubmit={(values, { setSubmitting }) => {
							this.changeTransactionStatus(values);
						}}
					>
						{({ handleChange,
							errors,
							values,
							handleBlur,
							setFieldValue,
							submitForm,
							touched,
							loginError
						}) => (



								<Form className="form-sm form-admin">

									<Modal.Header closeButton>
										<Modal.Title id="example-custom-modal-styling-title">Update Transaction</Modal.Title>
									</Modal.Header>
									<Modal.Body className="pb-0">
										<div>

											<p className="font-20 color-black mb-2">{this.state.promptMsg}</p>
											{/* <p className="font-14 color-grey-2">Please confirm the new start date & closing date</p>
								<p className="font-14 color-grey-2">Please select the new closing date</p>
								<p className="font-14 color-grey-2">All the tasks pending will be marked as paused, Are you sure want to continue transaction?</p>
								<p className="font-14 color-grey-2">All the pending tasks will be marked as canceled. Are you sure want to continue?</p> */}
										</div>


										{(this.state.showStartDate && this.state.showCloseDate) &&
											<Row>
												<Col xs={12} sm={6}>

													<div className="form-group">
														<label className="form-label" htmlFor="emd">Start Date<span className="text-danger">*</span></label>

														<InputGroup className='icon-wrapper blue-wrapper'>
															<DateRangePicker
																singleDatePicker
																startDate={moment(values.startDate)}
																onEvent={(e, picker) => {
																	setFieldValue("startDate", picker.startDate.format('YYYY-MM-DD'));

																	let selectedcamp = this.state.allTransdata.campaign;
																	let camDays = 0;
																	for (var i = 0, len = this.state.campaignStyle.length; i < len; i++) {
																		let camp = this.state.campaignStyle[i];
																		if (camp._id === selectedcamp) {
																			camDays = camp.campaignDays;
																		}
																	}

																	let edate = picker.startDate.subtract(-camDays, 'days');
																	setFieldValue("closeDate", edate.format('YYYY-MM-DD'));

																}}
																minDate={minDate}
															>
																<input type="text" className="form-control" placeholder="MM/DD/YYYY"
																	value={moment(values.startDate).format('MM/DD/YYYY')} />
																<span className='form-control-icn form-control-icn-sm'>
																	<DateRange />
																</span>
															</DateRangePicker>
														</InputGroup>
													</div>

												</Col>

												<Col xs={12} sm={6}>

													<div className="form-group">
														<label className="form-label" htmlFor="emd">Expected Closing Date<span className="text-danger">*</span>​​​​​​​</label>
														<InputGroup className='icon-wrapper blue-wrapper'>
															<DateRangePicker
																singleDatePicker
																startDate={moment(values.closeDate)}
																onEvent={(e, picker) => {
																	setFieldValue("closeDate", picker.startDate.format('YYYY-MM-DD'));
																}}
																minDate={moment(values.startDate)}
															>
																<input type="text" className="form-control" placeholder="MM/DD/YYYY"
																	value={moment(values.closeDate).format('MM/DD/YYYY')} />

																<span className='form-control-icn form-control-icn-sm'>
																	<DateRange />
																</span>
															</DateRangePicker>
														</InputGroup>
													</div>

												</Col>
											</Row>
										}

										{(this.state.showStartDate && !this.state.showCloseDate) &&
											<Row>
												<Col xs={12} sm={6}>

													<div className="form-group">
														<label className="form-label" htmlFor="emd">Start Date<span className="text-danger">*</span></label>

														<InputGroup className='icon-wrapper blue-wrapper'>
															<DateRangePicker
																singleDatePicker
																startDate={moment(values.startDate)}
																onEvent={(e, picker) => {
																	setFieldValue("startDate", picker.startDate.format('YYYY-MM-DD'));
																}}
																minDate={minDate}
															>
																<input type="text" className="form-control" placeholder="MM/DD/YYYY"
																	value={moment(values.startDate).format('MM/DD/YYYY')} />
																<span className='form-control-icn form-control-icn-sm'>
																	<DateRange />
																</span>
															</DateRangePicker>
														</InputGroup>
													</div>

												</Col>

											</Row>
										}


										{(!this.state.showStartDate && this.state.showCloseDate) &&
											<Row>
												<Col xs={12} sm={6}>

													<div className="form-group">
														<label className="form-label" htmlFor="emd">Expected Closing Date<span className="text-danger">*</span>​​​​​​​</label>
														<InputGroup className='icon-wrapper blue-wrapper'>
															<DateRangePicker
																singleDatePicker
																startDate={moment(values.closeDate)}
																onEvent={(e, picker) => {
																	setFieldValue("closeDate", picker.startDate.format('YYYY-MM-DD'));
																}}
															>
																<input type="text" className="form-control" placeholder="MM/DD/YYYY"
																	value={moment(values.closeDate).format('MM/DD/YYYY')} />

																<span className='form-control-icn form-control-icn-sm'>
																	<DateRange />
																</span>
															</DateRangePicker>
														</InputGroup>
													</div>

												</Col>


											</Row>
										}



									</Modal.Body>
									<Modal.Footer>
										<Button variant="primary" className="btn-blue btn-outlined w-140" onClick={() => this.closeConfirmPopup()}>Cancel</Button>
										<Button type="submit" variant="primary" className="btn-blue w-140">Yes</Button>
									</Modal.Footer>
								</Form>

							)}
					</Formik>

				</Modal>
			</div >
		);
	}
}

const mapStateToProps = state => {
	return {
		buyerMasterTemplates: state.reducer.buyerMasterTemplates,
		transactionData: state.reducer.transactionData,
		transactionStatus: state.reducer.transactionStatus
	};
};

export default connect(
	mapStateToProps, {
	getTransactionDataAction,
	changeTransactionStatusAction,
	getBuyerAgentMasterTemplateAction
})(Detail);
