import React from "react";

// Bootstrap components
import { Navbar, Nav, Button, Dropdown } from "react-bootstrap";

// icons
import { Menu, Notifications, Close } from '@material-ui/icons';

// images
import userImage from './../../assets/img/user.jpeg';
import Logo from './../../assets/img/logo.png';

import { connect } from 'react-redux'
import { logout, myProfileAction } from './../../actions/UserActions'
import $ from 'jquery';

class Header extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      showLoader: false,
      loggedFName: localStorage.getItem('fname'), 
      loggedLName: localStorage.getItem('lname'), 
    }
  }

  componentWillMount(){
    this.setState({
      showLoader : true
    })
    this.props.myProfileAction().then((res)=>{
      this.setState({
        showLoader : false
      })
    })
    
  }

  handleChange(){
    var datavalue =  "";
    if(this.props.miniActive == "collapsed"){
      datavalue = "";
    } else {
      datavalue = "collapsed";
    }
    this.setState({},this.props.onUpdate(datavalue))
  }

  logout() {
    this.props.logout();
    window.location.reload();
    //this.props.history.push("/login")
  }

  componentDidMount(){
    if(window.innerWidth < 991){
      var that =this;
      $('.sidebar .nav-link').click(function(){
        that.handleChange();
      });
    }
  }

  render() {

    let lname = (this.props.profileData.lastname) ? this.props.profileData.lastname : (this.props.profileData.lastName ? this.props.profileData.lastName : "")

    // if(this.state.loggedFName == this.props.profileData.firstname && (lname === this.state.loggedLName)){
    //   var username =  this.props.profileData.firstname + " " +  lname;
    // } else {
      var username =  localStorage.getItem('fname') + " " +  localStorage.getItem('lname');
    // }
    

    return (<>
      {this.state.showLoader && 
        <div className="page-loader">
          <div className="page-loader-inner">
          <div className="spinner-border" role="status">
            <span className="sr-only">Loading...</span>
          </div>
          </div>
        </div>
      }
        <Navbar expand="lg" className='align-items-center nav-admin'>
        <Navbar.Brand>
          <img src={Logo} alt="Logo" className="logo-mobile"/>
        </Navbar.Brand>
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="ml-auto align-items-center">
            <Nav.Item>
              <Button variant="transparent" className="color-grey-1 pr-0">
                <Notifications className="w-18"/>
              </Button>
            </Nav.Item>
            <Nav.Item>
            <Dropdown className="user-dropdown">
              <Dropdown.Toggle variant="transparent" id="dropdown-basic" className="d-flex align-items-center font-14">
                <span className="user-profile">
                    <img src={userImage} alt=""/>
                </span>
                <span className="ml-2 user-dropdown-name">{username}</span>
              </Dropdown.Toggle>

              <Dropdown.Menu className="dropdown-menu-right">
                <Dropdown.Item href="#/userProfile" style={{color:"black"}}> Profile  </Dropdown.Item>
                <Dropdown.Item href="#" onClick={()=>this.logout()} >Logout</Dropdown.Item>
              </Dropdown.Menu>
            </Dropdown>
            </Nav.Item>
          </Nav>
          <Button variant="transparent" className="color-grey-1 pr-2" onClick={(e) => this.handleChange(e)}>
            <Menu className="menu-icn"/>
            <Close className="close-icn"/>
          </Button>
        </Navbar.Collapse>
      </Navbar> 
    </>);
  }

}

const mapStateToProps = state => {
  return {  
    isAuthenticated: state.reducer.isAuthenticated,
    profileData: state.reducer.profileData
  };
};

export default connect(mapStateToProps, {logout, myProfileAction})(Header)
