import React from "react";

// Bootstrap components 
import { Button, Alert } from "react-bootstrap";

// import pages
import HeaderLogin from "../HeaderLogin/HeaderLogin.jsx";

import * as Yup from "yup";
import { Formik, Field, Form, ErrorMessage } from 'formik';

// icons
import { EmailOutlined, VpnKey } from '@material-ui/icons';

import { connect } from 'react-redux'
import { validateResetTokenAction } from '../../actions/UserActions'

const initialValue = {
  password:'',
  confirmPassword:''
};

class ResetPassword extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      showResetPassword: false,
      token: null,
      passwordSuccess: false,
      changedPasswordSuccess: false,
      LoginMessage: ''
    }
    // props.message = ''
  }

  onChange(event) {
    this.setState({
      [event.target.name]: event.target.value
    })
  }

  componentWillMount() {

    
    if (this.props.match.params.token) {
      let Token = this.props.match.params.token;
      this.setState({
        showResetPassword: true,
        token: Token
      })
      
    } else {

    }


  }
  goToLogin() {
    this.props.history.push("/login")
  }
  resetUserPassword(vals) {
    if(localStorage.getItem('PS')){
      this.setState({
        psemail: localStorage.getItem('EMAIL'),
        ps: true
      })
      let apiData = {
        email: this.state.psemail,
        password: vals.password
      }
      this.props.validateResetTokenAction(null,  apiData, 'SETPASS').then(res => {
        if(res){
          localStorage.clear();
          if(res.status == 200){
            this.setState ({
              changedPasswordSuccess : true,
              showResetPassword : null,
              passwordSuccess : null
            })
          }
          // this.props.history.push("/login")
        }
        

      });
    } else{
      let apiData = {
        password: vals.password
      }
      this.props.validateResetTokenAction(this.state.token,  apiData).then(res =>{
        if(res){
  
          this.setState({
            passwordSuccess: true,
            successMessage: "Your Password has been successfully updated"
          
          })
          // this.props.history.push("/login")
          
        }
      })
    }
    
  }

  componentWillReceiveProps(nextProps) {
    this.setState(nextProps);
  }

  render() {
    return (
      <div>

        {/* header */}
        <HeaderLogin />

        {/* login box */}
        <div className='loginBg loginContent align-items-end flex-column'>
          <div className='loginBox'>
          
              
              {(this.state.LoginMessage != '' && this.state.LoginMessage !== undefined) &&
                <Alert variant="danger">{this.props.LoginMessage}</Alert>
              }
              {(this.state.passwordSuccess) ? 
              <>
                <Alert variant="success">{this.state.successMessage}</Alert>

                <Button variant="link" className='pr-0 font-14 font-500' style={{ color: '#4a90e2' }} onClick={() => this.goToLogin()}> Please Click Here to login to your account</Button>
                </> : 
                ''

              }
              {(this.state.showResetPassword && !this.state.passwordSuccess) ?  <div>
                <h4>Reset Password</h4>
              <Formik
                validateOnBlur={true}
                initialValues={initialValue}
                enableReinitialize={true}
                //Validation schema
                validationSchema={Yup.object().shape({
                  password: Yup.string().required("Please enter Password").min(6, "Password should be of minimum 6 character."),
                  confirmPassword: Yup.string().when("password", {
                    is: val => (val && val.length > 0 ? true : false),
                    then: Yup.string().oneOf(
                      [Yup.ref("password")],
                      "Confirm Password should be same as Password entered"
                    )
                  })
                  .required('Please enter Confirm Password')
                  .min(6, "Password should be of minimum 6 character.")
                })}
                //Form submit method
                onSubmit={(values, { setSubmitting }) => {
                  this.resetUserPassword(values);
                }}
              >
                {({ handleChange,
                  errors,
                  values,
                  handleBlur,
                  setFieldValue,
                  submitForm,
                  touched,
                  loginError
                }) => (

                    <Form>

                      <div className="form-group">
                        <label className="form-label" htmlFor="password">Password</label>
                        <div className="icon-wrapper input-group">
                          <Field
                            type="password"
                            name="password"
                            onChange={handleChange}
                            value={values.password}
                            className={'form-control form-control-lg' + (errors.password && touched.password ? ' is-invalid' : '')}
                          ></Field>
                          <span className="form-control-icn">
                            <VpnKey />
                          </span>
                          <ErrorMessage name="password" component="div" className="invalid-feedback" style={{ display: "block" }} />
                        </div>
                      </div>
                      <div className="form-group">
                        <label className="form-label" htmlFor="confirmPassword">Confirm Password</label>
                        <div className="icon-wrapper input-group">
                          <Field
                            type="password"
                            name="confirmPassword"
                            value={values.confirmPassword}
                            onChange={handleChange}
                            className={'form-control form-control-lg' + (errors.confirmPassword && touched.confirmPassword ? ' is-invalid' : '')}
                          ></Field>
                          <span className="form-control-icn">
                            <VpnKey />
                          </span>
                          <ErrorMessage name="confirmPassword" component="div" className="invalid-feedback" style={{ display: "block" }} />
                        </div>
                      </div>
                      <div className="form-group pt-2">
                        <Button type="submit" variant="primary" size='lg' block className='btn-blue btn-medium' >Submit</Button>
                      </div>

                      {/* <div className="form-group pt-4 text-center mb-0">
                        <span className='font-14 color-grey-1 font-300'>Remember Password ?</span> <Button variant="link" className='pl-0 pr-0 font-14 font-500' style={{ color: '#4a90e2' }} onClick={() => this.goToLogin()}>Login</Button>
                      </div> */}

                    </Form>
                  )}
              </Formik>


            </div> :  ((!this.state.showResetPassword && this.state.showResetPassword!= null) &&(!this.state.passwordSuccess && this.state.passwordSuccess!= null) ) ?
            <div className="message-log-before mb-3">
              <h4 className="mb-3">Reset Password</h4>
              <p className="font-14 color-grey-1 font-400">The Token You provided is either INVALID or EXPIRED.</p>
              {/* <Button variant="primary" className='btn-blue btn-medium btn btn-block btn-lg' onClick={() => this.goToLogin()}>Login</Button> */}
            </div> : (this.state.changedPasswordSuccess) ?
            <div className="message-log-before mb-3">
            <h4 className="mb-3">Reset Password</h4>
            <p className="font-14 color-grey-1 font-400">Created Password Successfully.</p>
            <Button variant="primary" className='btn-blue btn-medium btn btn-block btn-lg' onClick={() => this.goToLogin()}>Login</Button>
          </div>
             :
            ''
  }
          </div>
        </div>
      </div>
    );
  }

}


const mapStateToProps = state => {
  return {
    isAuthenticated: state.reducer.isAuthenticated,
    LoginMessage: state.reducer.LoginMessage
  };
};

//export default  Login;


export default connect(mapStateToProps, { validateResetTokenAction })(ResetPassword)
