import React from 'react';

// Bootstrap components
import { Row, Col, Form, Button, Collapse, Modal, Dropdown } from "react-bootstrap";

// icons
import { Search, MoreVert, Check } from '@material-ui/icons';

// add contact
import AddContact from './CreateContact';
import { getContactsActions, deleteContactAction, editSetAsDefaultContactAction, getUserRolesAction } from '../../actions/UserActions';
import { connect } from 'react-redux';
import EditContact from './EditContact';
import noRecord from './../../assets/img/no-record.png';
import InputMask from 'react-input-mask';
// images
import NoContact from './../../assets/img/no-contact.jpg';


var _ = require('lodash');
class Contacts extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      categories: [],
      show: false,
      showEdit: false,
      filtered: false,
      showLoader: false,
      searchParam: '',
    };
  }

  setOpen(i) {
    var categories = this.state.categories
    categories[i].open = !categories[i].open;
    this.setState({
      categories
    })
  }

  setModalOpen() {
    this.setState({ show: !this.state.show });
  }

  edit(id) {
    this.setState({
      showEdit: !this.state.showEdit,
      contactData: {
        id: id
      }
    });
  }

  delete(id) {
    this.props.deleteContactAction(id).then(res => {
      this.getAllContacts();
    });

  }

  setAsDefault(name, item, checked) {
    var categories = this.state.categories
    var itemToSend;
    categories[name].data.forEach((e, i) => {
      if (e._id == item._id) {
        e.default = checked
        itemToSend = e
      } else {
        e.default = false
      }
    })
    this.props.editSetAsDefaultContactAction(itemToSend, item.contactId).then(res => {
      this.props.getContactsActions();
      this.setState({
        categories
      })
    })
  }

  componentWillMount() {
    this.getAllContacts()
  }

  getAllContacts() {
    this.setState({
      showLoader: true,
      searchParam: ''
    })

    this.props.getContactsActions().then(res => {
      var categories = {}
      var response = this.props.contacts;
      this.props.getUserRolesAction().then(cats => {

        if (cats) {

          categories = _.groupBy(response, function (b) { return b.roles ? b.roles[0] : '' })
          var newCategories = {};
          Object.keys(categories).forEach((cat) => {
            var sortedData = _.sortBy(categories[cat], 'firstname')
            var data = { data: sortedData, open: true }
            newCategories[cat] = data
          })

          var sortedCat = {}
          Object.keys(newCategories).sort((a,b)=>{
            return (_.find(this.props.roles, val => val._id == a) || {}).name > (_.find(this.props.roles, val => val._id == b) || {}).name ? 1 : -1
          }).forEach(function(key) {
            sortedCat[key] = newCategories[key];
          });

          this.setState({
            categories: sortedCat,
            showLoader: false
          })
        } else {
          this.setState({
            categories: [],
            showLoader: false
          })
        }

      })
    }, error => {
      this.setState({
        categories: [],
        showLoader: false
      })
    });
  }

  handleOnClose() {
    this.setModalOpen()
    this.getAllContacts()
  }

  closeEdit() {
    this.setState({
      showEdit: !this.state.showEdit,
    })

    this.getAllContacts();
  }

  search(param) {

    var response = this.props.contacts;

    var categories = _.groupBy(response, function (b) { return b.roles })
    var newCategories = {};
    Object.keys(categories).forEach((cat) => {
      var sortedData = _.sortBy(categories[cat], 'firstname')
      var data = { data: sortedData, open: true }
      categories[cat] = data
    })

    Object.keys(categories).forEach((cat) => {
      categories[cat].data = categories[cat].data.filter((value) => {
        return (value.firstname.toLowerCase().includes(param.toLowerCase()) || value.lastname.toLowerCase().includes(param.toLowerCase()))
      })
      if(categories[cat].data.length > 0){
        newCategories[cat] = categories[cat]
      }
    });


    var sortedCat = {}
    Object.keys(newCategories).sort((a,b)=>{
      return _.find(this.props.roles, val => val._id == a).name > _.find(this.props.roles, val => val._id == b).name ? 1 : -1
    }).forEach(function(key) {
      sortedCat[key] = newCategories[key];
    });
    
    this.setState({
      filtered: true,
      categories: sortedCat,
      searchParam: param
    })
  }

  render() {
    return (
      <div className="contact-page assign-vendor-step">

        {this.state.showLoader ?
          <div className="page-loader">
            <div className="page-loader-inner">
              <div className="spinner-border" role="status">
                <span className="sr-only">Loading...</span>
              </div>
            </div>
          </div>
          : <>
            {(Object.keys(this.state.categories).length <= 0 && this.state.filtered == false)?
                  <div className="container-custom admin-content-area no-data-container">
                    <div className="no-transaction no-contact">
                      <div className="no-transaction-inner">
                        <p className="color-grey-2">No Contacts Found</p>
                        <img src={NoContact} className="no-transaction-content" alt="" />
                        <h2 className="no-transaction-content">Manage your Contact</h2>
                        <p className="no-transaction-content color-grey-1">Want to assign new contact with your transaction,<br /> click on "Add new contact"</p>
                        <Button className="text-uppercase btn-orange btn-orange-lg" onClick={() => { this.setModalOpen() }}>add new contact</Button>
                      </div>
                    </div>
                  </div>
              : <>
                <Row className="ml-0 mr-0 page-head-row page-head-row-inner align-items-center mt-10">
                  <Col xs="4">
                    <h4 className="title-med mt-0 pt-1">Contacts</h4>
                  </Col>
                  <Col className="contact-search">
                    <div className='icon-wrapper'>
                      <Form.Control type="text" className="form-control-head" value={this.state.searchParam} placeholder="Search by Name" onChange={(e) => { this.search(e.target.value) }} />
                      <span className='form-control-icn'>
                        <Search />
                      </span>
                    </div>
                  </Col>
                  <Col align="right" className="contact-btn">
                    <Button variant="primary" className="btn-blue btn-head" onClick={() => this.setModalOpen()}>Add New Contact</Button>
                  </Col>
                </Row>
                {(Object.keys(this.state.categories).length <= 0 && this.state.filtered)?
                  <div className="no-transaction no-record">
                    <div className="no-transaction-inner">
                        <img src={noRecord} alt="no record"/>
                        <p>No Contacts found</p>
                    </div>
                  </div> :
                <div className="container-custom admin-content-area">

                  <div className="inspector-list">

                    {/* inspector loop */}
                    {Object.keys(this.state.categories).map((cat) => {
                      return <div key={cat} className="inspector-acc">
                        <div className="inspector-acc-head">
                          <Button
                            variant="link"
                            onClick={() => this.setOpen(cat)}
                            aria-controls="example-collapse-text"
                            aria-expanded={this.state.categories[cat].open}
                          >
                            {_.find(this.props.roles, val => val._id == cat) ? _.find(this.props.roles, val => val._id == cat).name : cat}
                            <span>

                            </span>
                          </Button>
                        </div>
                        <Collapse in={this.state.categories[cat].open} className="inspector-acc-body">
                          <Row>
                            {this.state.categories[cat].data.map((home, i) => {
                              return <Col xs="12" sm="3" key={i} className="inspector-card">
                                <Dropdown className="contact-more">
                                  <Dropdown.Toggle variant="transparent" className="p-0" id="dropdown-basic">
                                    <MoreVert />
                                  </Dropdown.Toggle>

                                  <Dropdown.Menu>
                                    <Dropdown.Item onClick={() => { this.edit(home.contactId) }}>Edit</Dropdown.Item>
                                    <Dropdown.Item onClick={() => { this.setAsDefault(cat, home, true) }}>Set As Default</Dropdown.Item>
                                    <Dropdown.Item onClick={() => { this.delete(home.contactId) }}>Delete</Dropdown.Item>
                                  </Dropdown.Menu>
                                </Dropdown>
                                <input type="checkbox" checked={home.default} onChange={(e) => { this.setAsDefault(cat, home, e.target.checked) }} />
                                <div className="inspector-card-body">
                                  <h4>{home.firstname} {home.lastname}</h4>
                                  <h5>{_.find(this.props.roles, val => val._id == home.roles).name}</h5>
                                  <p>{home.email}</p>
                                  <p>{home.mobileNumber}</p>
                                  <div className="check">
                                    <Check />
                                  </div>
                                </div>
                              </Col>
                            })}


                          </Row>
                        </Collapse>
                      </div>
                    })}

                  </div>

                </div>}
              </>}</>}
        <Modal
          show={this.state.show}
          onHide={() => this.setModalOpen()}
          dialogClassName="modal-90w"
          aria-labelledby="example-custom-modal-styling-title"
          className="modal-med"
          backdrop={false}
        >
          <Modal.Header closeButton>
            <Modal.Title id="example-custom-modal-styling-title">
              Create New Contact
              </Modal.Title>
          </Modal.Header>
          <AddContact onClose={() => { this.handleOnClose() }} />
        </Modal>


        <Modal
          show={this.state.showEdit}
          onHide={() => this.closeEdit()}
          dialogClassName="modal-90w"
          aria-labelledby="example-custom-modal-styling-title"
          className="modal-med"
          backdrop={false}
        >
          <Modal.Header closeButton>
            <Modal.Title id="example-custom-modal-styling-title">
              Edit Contact
              </Modal.Title>
          </Modal.Header>
          <EditContact contactData={this.state.contactData} onClose={() => { this.closeEdit() }} />
        </Modal>
      </div>
    );
  }

}

const mapStateToProps = state => {
  return {
    contacts: state.reducer.contacts,
    roles: state.reducer.roles,
  };
};

export default connect(mapStateToProps, { getContactsActions, deleteContactAction, editSetAsDefaultContactAction, getUserRolesAction })(Contacts);