class TransactionValuesWidget extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      viewMode:'month',
      responseData: {},
      chartOptions: {
        chart: {
          height: '315'
        },
        title: {
          text: ''
        },
        series: [
          {
            data: [],
            name: 'Open Transactions Value',
          },
          // {
          //   data: [100, 120, 230, 290, 180, 320, 220, 380, 280, 250, 320, 200],
          //   name: 'Closed Transactions Value',
          // }
        ],
        xAxis: {
          categories: [
            'Jan',
            'Feb',
            'Mar',
            'Apr',
            'May',
            'Jun',
            'Jul',
            'Aug',
            'Sep',
            'Oct',
            'Nov',
            'Dec'
          ],
          title: false
        },
      
        yAxis: {
          labels: {
            format: '{value}k'
          },
          title: false
        },
      }
    }
  }

  componentWillMount(){
    this.props.getTransactionValuesWidgetDataAction().then(res=>{
      this.setState({
        responseData: res
      },()=>{
        this.changeView('month')
      })
    });
  }

  changeView(view){
    var months = (this.state.responseData.monthTransaction || []).map(e=>{ return e.total})
    var monthsLabels = (this.state.responseData.monthTransaction || []).map(e=>{ return e._id.month})
    var weeks = (this.state.responseData.weekTransaction || [])
      .map(f=>{ 
        return {label: 'Week'+(f._id.week+1) , value: f.total} 
      })
      .sort((a,b)=>{
        return a.value > b.value ? 1 : -1
      });
    if(view == 'month'){
      this.setState({
        viewMode:'month',
        chartOptions: {
          chart: {
            height: '315'
          },
          title: {
            text: ''
          },
          series: [
            {
              data: months,
              name: 'Open Transactions Value',
            }
          ],
          xAxis: {
            categories: monthsLabels,
            title: false
          },
        
          yAxis: {
            labels: {
              format: '{value}k'
            },
            title: false
          },
        }
      })
    } else{
      this.setState({
        viewMode:'week',
        chartOptions: {
          chart: {
            height: '315'
          },
          title: {
            text: ''
          },
          series: [
            {
              data: weeks.map((e)=> {return e.value}),
              name: 'Open Transactions Value',
            }
          ],
          xAxis: {
            categories: weeks.map((f)=> {return f.label}),
            title: false
          },
        
          yAxis: {
            labels: {
              format: '{value}k'
            },
            title: false
          },
        }
      })
    }
    
  }

  render() {
    return (
      <div>
        {/* <div className="dash-row">
          <div className="dash-col dash-col-big">
            <div className="dash-col-inner"> */}
              <Row>
                <Col>
                  <h4 className="color-blue-3 font-16 font-600">
                    Transactions Value
                  </h4>
                </Col>
                <Col align="right" className="col-auto">
                  <div className="switch-btns">
                    <Button variant="primary" className={this.state.viewMode=='week' && 'active'} onClick={()=>{this.changeView('week')}}>Week</Button>
                    <Button variant="primary" className={this.state.viewMode=='month' && 'active'} onClick={()=>{this.changeView('month')}}>Month</Button>
                  </div>
                </Col>
              </Row>
              {/* <div className="high-line">
                <HighchartsReact highcharts={Highcharts} options={optionsWeek} />
              </div> */}
              {this.state.chartOptions.series[0].data.length == 0 ?  
              <div className="dash-no-records">
                No Records Found
              </div>:
              <div className="high-line">
                <HighchartsReact highcharts={Highcharts} options={this.state.chartOptions} />
              </div>}
            {/* </div>
          </div>
        </div> */}
      </div>
    )
  }
}

const mapStateToProps = state => {
  return {
    valuesData: state.reducer.valuesData || {}
  };
};

export default connect(mapStateToProps, { getTransactionValuesWidgetDataAction })(TransactionValuesWidget);
