import React, { Component } from 'react';
import { createBrowserHistory } from "history";
import { Route, Switch, Redirect, HashRouter } from 'react-router-dom';
import { PrivateRoute } from './Helper/PrivateRoute';

// components
import Login from "components/Login/Login.jsx";
import Register from "components/Register/Register.jsx";
import ForgotPassword from "components/ForgotPassword/ForgetPassword.jsx";
import ResetPassword from "components/ResetPassword/ResetPassword.jsx";

// Dashboard components
import Dashboard from "components/Dashboard/Dashboard.jsx";

// transaction components
import TransactionList from "components/Transactions/List/TransactionList.jsx";
import TransactionsCreate from "components/Transactions/Create/Create.jsx";
import TransactionsDetail from "components/Transactions/Detail/Detail.jsx";

// Calendar component
import CalendarModule from "components/Calendar/Calendar.jsx";

// Contact component
import Contacts from "components/Contacts/Contacts.jsx";

// Document component
import Documents from "components/Documents/Documents.jsx";

// admin pages
import RegistrationRequests from "components/RegistrationRequests/RegistrationRequests.jsx";
import BuyerAgent from "components/BuyerAgent/BuyerAgent.jsx";
import Vendor from "components/Vendors/Vendors.jsx";
import Buyers from "components/HomeBuyers/HomeBuyers.jsx";
import BuyerAgentCreate from "components/BuyerAgent/InviteAgent.jsx";

// user profile
import UserProfile from "components/UserProfile/UserProfile.jsx";

// Admin Template
import MasterTemplate from "components/MasterTemplate/MasterTemplate.jsx";

import BuyerAgentMasterTemplate from "components/MasterTemplate/BuyerAgentMasterTemplate.jsx";
// Admin Template
import ThankYou from "components/ThankYou/ThankYou.jsx";

// Admin Template
import AccessDenied from "components/ErrorPage/AccessDenied.jsx";

import './App.css';
// import Header from './layout/Header';

// Importing the Bootstrap CSS
import "bootstrap/dist/css/bootstrap.min.css";

import { Provider } from "react-redux";
import store from "./store/store";

const hist = createBrowserHistory();

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      miniActive: "",
      sidebar: ""
    };
  }

  render() {
    return (
      <Provider store={store}>
        <div>

          <HashRouter history={hist}>


            <Switch>

              {/* Dashboard */}
              <PrivateRoute exact path="/dashboard" miniActive={this.state.miniActive} component={Dashboard} onUpdate={(miniActive) => this.setState({ miniActive: miniActive })} />

              {/* transactions */}
              <PrivateRoute exact path="/transactions" miniActive={this.state.miniActive} component={TransactionList} onUpdate={(miniActive) => this.setState({ miniActive: miniActive })} />

              {/* create transaction */}
              <PrivateRoute exact path="/transactions/create" miniActive={this.state.miniActive} component={TransactionsCreate} onUpdate={(miniActive) => this.setState({ miniActive: miniActive })} />

              {/* detail transaction */}
              <PrivateRoute exact path="/transactions/detail/:transId" miniActive={this.state.miniActive} component={TransactionsDetail} onUpdate={(miniActive) => this.setState({ miniActive: miniActive })} />

              {/* Calendar */}
              <PrivateRoute exact path="/calendar" miniActive={this.state.miniActive} component={CalendarModule} onUpdate={(miniActive) => this.setState({ miniActive: miniActive })} />

              {/* Contacts */}
              <PrivateRoute exact path="/contacts" miniActive={this.state.miniActive} component={Contacts} onUpdate={(miniActive) => this.setState({ miniActive: miniActive })} />

              {/* Documents */}
              <PrivateRoute exact path="/documents" miniActive={this.state.miniActive} component={Documents} onUpdate={(miniActive) => this.setState({ miniActive: miniActive })} />


              {/****** admin pages ******/}

              {/* RegistrationRequests */}
              <PrivateRoute exact path="/RegistrationRequests" miniActive={this.state.miniActive} component={RegistrationRequests} onUpdate={(miniActive) => this.setState({ miniActive: miniActive })} />

              {/* BuyerAgent */}
              <PrivateRoute exact path="/BuyerAgent" miniActive={this.state.miniActive} component={BuyerAgent} onUpdate={(miniActive) => this.setState({ miniActive: miniActive })} />
              <PrivateRoute exact path="/Vendors" miniActive={this.state.miniActive} component={Vendor} onUpdate={(miniActive) => this.setState({ miniActive: miniActive })} />
              <PrivateRoute exact path="/Buyers" miniActive={this.state.miniActive} component={Buyers} onUpdate={(miniActive) => this.setState({ miniActive: miniActive })} />

              {/* Create BuyerAgent */}
              <PrivateRoute exact path="/BuyerAgent/create" miniActive={this.state.miniActive} component={BuyerAgentCreate} onUpdate={(miniActive) => this.setState({ miniActive: miniActive })} />

              {/* User Profile */}
              <PrivateRoute exact path="/UserProfile" miniActive={this.state.miniActive} component={UserProfile} onUpdate={(miniActive) => this.setState({ miniActive: miniActive })} />
              <PrivateRoute exact path="/UserProfile/:id" onEnter={this.requireAuth}  miniActive={this.state.miniActive} component={UserProfile} onUpdate={(miniActive) => this.setState({ miniActive: miniActive })} />

              {/* Admin Template */}
              { (localStorage.getItem('usertype') != null && localStorage.getItem('usertype').toUpperCase() == 'BUYERAGENT') ?
              
              <PrivateRoute exact path="/MasterTemplate" miniActive={this.state.miniActive} component={BuyerAgentMasterTemplate} onUpdate={(miniActive) => this.setState({ miniActive: miniActive })} />
              : 
              <PrivateRoute exact path="/MasterTemplate" miniActive={this.state.miniActive} component={MasterTemplate} onUpdate={(miniActive) => this.setState({ miniActive: miniActive })} />
            }
              

              {/* AccessDenied */}
              <PrivateRoute exact path="/AccessDenied" miniActive={this.state.miniActive} component={AccessDenied} onUpdate={(miniActive) => this.setState({ miniActive: miniActive })} />

              {/* Login */}
              <Route path="/ThankYou" component={ThankYou} />

              {/* Login */}
              <Route path="/login" component={Login} />

              {/* Register */}
              <Route path="/register" component={Register} />

              {/* Forgot Password */}
              <Route path="/forgotpassword" component={ForgotPassword} />

              {/* Reset Password */}
              <Route path="/resetpassword/:token?" component={ResetPassword} />

              : <Redirect to={{ pathname: '/login' }} />

            </Switch>
          </HashRouter>
        </div>
      </Provider>
    );
  }
}

export default App;