import { CropOriginal, DeleteOutline } from '@material-ui/icons';
import Joi from 'joi';
import strategy from 'joi-validation-strategy';
import PropTypes from 'prop-types';
import React from 'react';
// bootstrap components
import { Button, Col, Form, Row } from "react-bootstrap";
import Dropzone from 'react-dropzone';
import validation from 'react-validation-mixin';
import { StatesList } from './../../../Helper/StatesList'; //'.../../../Helper/StatesList'
import { isGenericTypeAnnotation } from '@babel/types';
import InputMask from 'react-input-mask';
var _ = require('lodash');

class PropertyInfo extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      "condo": props.getTransactionData().condo,
      "builtBefore1978" : props.getTransactionData().builtBefore1978,
      "hoa" : props.getTransactionData().hoa,

      "draft": props.getTransactionData().draft,
      "leadSource": props.getTransactionData().leadSource,
      "emd": props.getTransactionData().emd,
      "agreedOpenPrice": props.getTransactionData().agreedOpenPrice,
      "transactionType": props.getTransactionData().transactionType,

      "property_image": props.getTransactionData().property.image,
    //  "property_name": props.getTransactionData().property.name,
      "property_street": props.getTransactionData().property.street,
      // "property_street1": props.getTransactionData().property.street1,
      "property_city": props.getTransactionData().property.city,
      "property_state": props.getTransactionData().property.state,
      "property_zipcode": props.getTransactionData().property.zipcode,  

      "buyer_email": props.getTransactionData().buyer.email,
      "buyer_firstname": props.getTransactionData().buyer.firstname,
      "buyer_lastName": props.getTransactionData().buyer.lastName,
      "buyer_mobileNumber": props.getTransactionData().buyer.mobileNumber,
      // acceptedFiles:[],
      acceptedFiles:props.getTransactionData().property.image,
    };

    this.validatorTypes = function() {
      var base = Joi.object().keys({         
          
          "emd": Joi.string().required(),
          "agreedOpenPrice": Joi.string().required(),
          "transactionType": Joi.string().required(),

        //  "property_image": Joi.array().items(),
          // "property_image": Joi.array().min(1),
          "property_street": Joi.string().required(),
          // "property_street1": Joi.string().required(),
          "property_city": Joi.string().required(),
          "property_state": Joi.string().required(),
          "property_zipcode": Joi.string().regex(new RegExp("^[0-9]{5}(?:-[0-9]{4})?$")).required(),

          // "property_zipcode": Joi.string().min(5).max(9).required(),

          "buyer_email": Joi.string().email().required(),
          "buyer_firstname": Joi.string().required(),
          "buyer_lastName": Joi.string().required(),
          "buyer_mobileNumber": Joi.string().regex(new RegExp(/(?:1?|\+1|\+\s1|\(?)(?:\(?|\s\(|[\.\-\s])\d{3}(?:\)?|\)\s|[\.\-\s])\d{3}[\.\-\s]\d{4}/)).required()

      });
      return base;
    };

  this.getValidatorData = this.getValidatorData.bind(this);
  this.renderHelpText = this.renderHelpText.bind(this);
  this.isValidated = this.isValidated.bind(this);
    
  }


  getValidatorData() {
      return {

        "emd": this.props.getTransactionData().emd,
        "agreedOpenPrice": this.props.getTransactionData().agreedOpenPrice,
        "transactionType": this.props.getTransactionData().transactionType,

      //  "property_image": this.props.getTransactionData().property.image,
        "property_street": this.props.getTransactionData().property.street,
        // "property_street1": this.props.getTransactionData().property.street1,
        "property_city": this.props.getTransactionData().property.city,
        "property_state": this.props.getTransactionData().property.state,
        "property_zipcode": this.props.getTransactionData().property.zipcode,  

        "buyer_email": this.props.getTransactionData().buyer.email,
        "buyer_firstname": this.props.getTransactionData().buyer.firstname,
        "buyer_lastName": this.props.getTransactionData().buyer.lastName,
        "buyer_mobileNumber": this.props.getTransactionData().buyer.mobileNumber
      }
  }

  getErrorMsg(field, errsList){
    var err = '';

    if(field === "emd" || field === 'agreedOpenPrice' || 
    field === 'property_street' ||
    field === "property_city" || field === 'agreedOpenPrice' ||
    field === 'buyer_firstname' || field === "property_state" ||
    field === 'buyer_lastName' || field === "transactionType" || field === "property_image"
    ) {
        if(errsList[field] !== undefined && errsList[field] !== null && errsList[field] !== "") {
            if(field === "emd") {
                err = 'Please enter EMD Deposit'; 
            }
            if(field === "property_street") {
                err = 'Please enter Address 1';
            }
           
            if(field === "property_city") {
                err = 'Please enter City';
            }
            if(field === "buyer_firstname") {
                err = 'Please enter Buyers First Name';
            }
            if(field === "buyer_lastName") {
                err = 'Please enter Buyers Last Name';
            }
            if(field === "agreedOpenPrice") {
                err = 'Please enter Agreed Open Price';
            }
            if(field === "property_state") {
                err = 'Please select State';
            }
            if(field === "transactionType") {
              err = 'Please select Transaction Type';
            }
            if(field === "property_image") {
              if(this.state.acceptedFiles.length <= 0 ){
                err = 'Please select Property Image';
              }
            }
        }
    }
    if(field === 'buyer_email') {            
        if(errsList[field] !== undefined && errsList[field] !== null && errsList[field] !== "") {
            if(errsList[field].length === 2){
                err = 'Please enter E-mail Address*';
            }
            if(errsList[field].length === 1){
                err = 'Please enter valid E-mail Address*';
            }
        }
    }
    if(field === 'property_zipcode') {            
      if(errsList[field] !== undefined && errsList[field] !== null && errsList[field] !== "") {
          if(errsList[field].length === 2){
              err = 'Please enter Zip code';
          }
          if(errsList[field].length === 1){
              err = 'Please enter valid Zip code';
          }
      }
    }

    if(field === 'buyer_mobileNumber') {            
      if(errsList[field] !== undefined && errsList[field] !== null && errsList[field] !== "") {
          if(errsList[field].length === 2){
              err = 'Please enter Mobile Number';
          }
          if(errsList[field].length === 1){
              err = 'Mobile Number Must be of 10 digits';
          }
      }
  }
   
        return err;
  }

  isValidated() {    
      return new Promise((resolve, reject) => {
        this.props.validate((error) => {
          if (error) {
            reject(); // form contains errors
            return false;
          }
          resolve();
          return true;
        });
      });
  }

  renderHelpText(message, id) {
    return (<div className="val-err-tooltip" key={id}><span>{message}</span></div>);
  }

  onChange (event) {
   
    if(event.target.name === "condo" || event.target.name === "hoa" || event.target.name === "builtBefore1978") {
      this.setState({[event.target.name] : event.target.checked});
      this.props.updateTransactionData(event.target.name, event.target.checked );
      
    } else {
      this.setState({[event.target.name] : event.target.value});
      this.props.updateTransactionData(event.target.name, event.target.value );
    }
    
  }

  uploadImage(files){
    var acceptedFiles = this.state.acceptedFiles;
    acceptedFiles = _.uniqBy(_.concat(acceptedFiles, files), 'name');
    
    this.setState({
      acceptedFiles,
      property_image:acceptedFiles
    });
    this.props.handleValidation('property_image')
    this.props.updateTransactionData('property_image', acceptedFiles);
  }

  deleteImage(i){
    var acceptedFiles = this.state.acceptedFiles;
    acceptedFiles.splice(i, 1);
    this.setState({acceptedFiles
    },
      this.props.updateTransactionData('property_image', this.state.acceptedFiles)
    );
  }
  
  render() {

    var stateDropDownValue = [];
    for (var key in StatesList) {        
        var opt = <option key={key} value={key}>{StatesList[key]}</option>;
        stateDropDownValue.push(opt);
    } 

    return (
      <div className="admin-steps admin-content-area">
      <div className="container-custom">
        <Form className="form-sm form-admin">
          <Row>
            <Col xs={12}><h4 className="title-small font-600 mt-2 mb-3 pt-3">Property Information</h4></Col>
          </Row>
          <Row>
            <Col xs={12} sm={7}>
            <Row>
              <Col xs={12} sm={12}>
                <Row>
                  <Col xs={12}>
                      <Form.Group controlId="">
                          <Form.Label>Address 1<span className="text-danger">*</span></Form.Label>
                          <Form.Control type="text" name="property_street"
                           defaultValue = {this.state.property_street}
                           onChange={this.onChange.bind(this)} 
                          onBlur={this.props.handleValidation('property_street')} 
                           />
                          <Form.Text className="text-danger">
                          {this.renderHelpText(this.getErrorMsg('property_street', this.props.errors))}
                        </Form.Text>
                      </Form.Group>
                  </Col>
                </Row>
                <Row>
                  <Col xs={12}>
                      <Form.Group controlId="">
                          <Form.Label>Address 2</Form.Label>
                          <Form.Control type="text" name="property_street1"
                           defaultValue = {this.state.property_street1}
                           onChange={this.onChange.bind(this)} 
                            />
                         
                      </Form.Group>
                  </Col>
                </Row>
                <Row>
                  <Col xs={12} sm={4}>
                      <Form.Group controlId="">
                          <Form.Label>Zip Code<span className="text-danger">*</span></Form.Label>
                          <Form.Control type="text" name="property_zipcode" 
                           defaultValue = {this.state.property_zipcode}
                           onChange={this.onChange.bind(this)} 
                          onBlur={this.props.handleValidation('property_zipcode')} />

                        <Form.Text className="text-danger">
                          {this.renderHelpText(this.getErrorMsg('property_zipcode', this.props.errors))}
                        </Form.Text>
                      </Form.Group>
                  </Col>
                  <Col xs={12} sm={4}>
                      <Form.Group controlId="">
                          <Form.Label>City<span className="text-danger">*</span></Form.Label>
                          <Form.Control type="text"  name="property_city"
                           defaultValue = {this.state.property_city}
                           onChange={this.onChange.bind(this)} 
                          onBlur={this.props.handleValidation('property_city')} />
                          <Form.Text className="text-danger">
                          {this.renderHelpText(this.getErrorMsg('property_city', this.props.errors))}
                        </Form.Text>
                      </Form.Group>
                  </Col>
                  <Col xs={12} sm={4}>
                      <Form.Group controlId="">
                          <Form.Label>State<span className="text-danger">*</span></Form.Label>
                          <div className="c-select">
                            <select name="property_state" className="form-control"
                            defaultValue = {this.state.property_state}
                            onChange={this.onChange.bind(this)} 
                           >
                              <option value="">Select</option>
                              {stateDropDownValue}
                            </select>
                          </div>
                          <Form.Text className="text-danger">
                          {this.renderHelpText(this.getErrorMsg('property_state', this.props.errors))}
                        </Form.Text>
                      </Form.Group>
                  </Col>
                </Row>
                <hr className="mt-1"/>
              </Col>
          </Row>
          <Row>
            <Col xs={12} sm={12}>
              <Row>
                  <Col xs={12} sm={6}>
                      <Form.Group controlId="">
                          <Form.Label>Buyers First Name<span className="text-danger">*</span></Form.Label>
                          <Form.Control type="text" name="buyer_firstname"
                           defaultValue = {this.state.buyer_firstname}
                           onChange={this.onChange.bind(this)} 
                          onBlur={this.props.handleValidation('buyer_firstname')}  />
                          <Form.Text className="text-danger">
                          {this.renderHelpText(this.getErrorMsg('buyer_firstname', this.props.errors))}
                        </Form.Text>
                      </Form.Group>
                  </Col>
                  <Col xs={12} sm={6}>
                      <Form.Group controlId="">
                          <Form.Label>Buyers Last Name<span className="text-danger">*</span></Form.Label>
                          <Form.Control type="text"  name="buyer_lastName"
                           defaultValue = {this.state.buyer_lastName}
                           onChange={this.onChange.bind(this)} 
                          onBlur={this.props.handleValidation('buyer_lastName')} />
                          <Form.Text className="text-danger">
                          {this.renderHelpText(this.getErrorMsg('buyer_lastName', this.props.errors))}
                        </Form.Text>
                      </Form.Group>
                  </Col>
              </Row>

              <Row>
                  <Col xs={12} sm={6}>
                      <Form.Group controlId="">
                          <Form.Label>E-mail Address<span className="text-danger">*</span></Form.Label>
                          <Form.Control type="email" name="buyer_email"
                           defaultValue = {this.state.buyer_email}
                           onChange={this.onChange.bind(this)} 
                          onBlur={this.props.handleValidation('buyer_email')} />
                          <Form.Text className="text-danger">
                          {this.renderHelpText(this.getErrorMsg('buyer_email', this.props.errors))}
                        </Form.Text>
                      </Form.Group>
                  </Col>
                  <Col xs={12} sm={6}>
                      <Form.Group controlId="">
                      <Form.Label>Mobile Number<span className="text-danger">*</span></Form.Label>
                        <InputMask 
                              mask="999-999-9999" 
                              maskChar=''
                              // className={`${classes.textField} c-textfield`}
                              formControlProps={{
                              fullWidth: true,
                            }}
                              onChange={this.onChange.bind(this)}
                              onBlur={this.props.handleValidation('buyer_mobileNumber')}
                              value={this.state.zipcode}
                              name="buyer_mobileNumber"
                              // beforeMaskedValueChange={this.beforeMaskedValueChange} 
                              // error={ Boolean(this.props.errors.primaryAddress && this.props.errors.primaryAddress[0].postalcode)}
                            >
                            {(inputProps) =>
                            <Form.Control   {...inputProps}/>
                            // <TextField {...inputProps}/>
                            }
                        </InputMask>
                          
                          {/* <Form.Control type="number" name="buyer_mobileNumber"
                           defaultValue = {this.state.buyer_mobileNumber}
                           onChange={this.onChange.bind(this)} 
                          onBlur={this.props.handleValidation('buyer_mobileNumber')} /> */}
                          <Form.Text className="text-danger">
                          {this.renderHelpText(this.getErrorMsg('buyer_mobileNumber', this.props.errors))}
                        </Form.Text>
                      </Form.Group>
                  </Col>
              </Row>

              <Row>
                  <Col xs={12} sm={6}>
                      <Form.Group controlId="">
                          <Form.Label>Sales Price<span className="text-danger">*</span></Form.Label>
                          <Form.Control type="number" name="agreedOpenPrice"
                           defaultValue = {this.state.agreedOpenPrice}
                           onChange={this.onChange.bind(this)} 
                          onBlur={this.props.handleValidation('agreedOpenPrice')} />
                          <Form.Text className="text-danger">
                          {this.renderHelpText(this.getErrorMsg('agreedOpenPrice', this.props.errors))}
                        </Form.Text>
                      </Form.Group>
                  </Col>
                  <Col xs={12} sm={6}>
                      <Form.Group controlId="">
                          <Form.Label>Earnest Money Deposit<span className="text-danger">*</span></Form.Label>
                          <Form.Control type="number" name="emd"
                           defaultValue = {this.state.emd}
                           onChange={this.onChange.bind(this)} 
                          onBlur={this.props.handleValidation('emd')} />
                          <Form.Text className="text-danger">
                          {this.renderHelpText(this.getErrorMsg('emd', this.props.errors))}
                        </Form.Text>
                      </Form.Group>
                  </Col>
              </Row>

              <Row>
                  <Col xs={12} sm={6}>
                      <Form.Group controlId="">
                          <Form.Label>Transaction Type<span className="text-danger">*</span></Form.Label>
                          <div className="c-select">
                            <select name="transactionType" className="form-control"
                              defaultValue = {this.state.transactionType}
                              onChange={this.onChange.bind(this)} 
                              onBlur={this.props.handleValidation('transactionType')}>
                              <option value="">Select</option>
                              <option value="Purchase">Purchase</option>
                            </select>
                          </div>
                          <Form.Text className="text-danger">
                          {this.renderHelpText(this.getErrorMsg('transactionType', this.props.errors))}
                        </Form.Text>
                      </Form.Group>
                  </Col>
              </Row>

              <Row>
                  <Col xs={12}>
                      <Form.Group controlId="">
                          <Form.Label>Lead Source</Form.Label>
                          <Form.Control type="text" name="leadSource"
                          defaultValue = {this.state.leadSource}
                          onChange={this.onChange.bind(this)}  />
                      </Form.Group>
                  </Col>
              </Row>
              <Row>
                  <Col xs={12}>
                      <Form.Group controlId="">
                        <div className="custom-control custom-checkbox custom-control-inline font-600">
                        {(this.state.builtBefore1978) ? 
                          <input type="checkbox" className="custom-control-input" id="checkBuilt" 
                          name="builtBefore1978" onChange={this.onChange.bind(this)}
                          defaultChecked/>
                          
                          :
                        <input type="checkbox" className="custom-control-input" id="checkBuilt" 
                          name="builtBefore1978" onChange={this.onChange.bind(this)}
                          />
                        }

                          <label className="custom-control-label" htmlFor="checkBuilt">Built Before 1978</label>
                        </div>
                        <div className="custom-control custom-checkbox custom-control-inline font-600">
                          {(this.state.hoa) ? 
                              <input type="checkbox" className="custom-control-input"
                              id="checkHoa" name="hoa" onChange={this.onChange.bind(this)} 
                              defaultChecked/>
                          : 
                              <input type="checkbox" className="custom-control-input"
                                id="checkHoa" name="hoa" onChange={this.onChange.bind(this)} />
                           }
                          

                          <label className="custom-control-label" htmlFor="checkHoa">HOA</label>
                        </div>
                        <div className="custom-control custom-checkbox custom-control-inline font-600">
                          {(this.state.condo) ? 
                              <input type="checkbox" className="custom-control-input" 
                              id="checkCondo" name="condo" onChange={this.onChange.bind(this)} 
                              defaultChecked
                               />
                           :
                            <input type="checkbox" className="custom-control-input" 
                            id="checkCondo" name="condo" onChange={this.onChange.bind(this)} 
                            />
                          }
                          <label className="custom-control-label" htmlFor="checkCondo">Condo</label>
                        </div>
                      </Form.Group>
                  </Col>
              </Row>
            </Col>
          </Row>
          
            </Col>
            <Col xs={12} sm={5}>
                <Form.Group controlId="" className="dropzone-transaction-wrapper">
                  <div className="mt-4">
                  <Dropzone accept='image/*' maxSize={100000000} onDrop={acceptedFiles => {
                    this.uploadImage(acceptedFiles)
                  }}>
                    {({getRootProps, getInputProps}) => (
                      <section>
                        <div className="dropzone-transaction" {...getRootProps()}>
                          <input {...getInputProps()} />
                          <CropOriginal/>
                          <p><strong>Chose a file</strong> or drag here</p>
                        </div>
                      </section>
                    )}
                  </Dropzone>
                    {/* <input type="file" onChange={(e)=>{this.uploadImage(e)}}/> */}
                  </div>
                  <div className="dropzone-list">
                    <ul>
                      { this.state.acceptedFiles.map((file, i)=>{
                        return <li key={i}>
                           {file.name}
                           <Button variant="transparent" onClick={()=>{this.deleteImage(i)}}>
                              <DeleteOutline/>
                          </Button>
                        </li>
                      })
                        
                      }
                    </ul>
                  </div>
                  <Form.Text className="text-danger">
                          {this.renderHelpText(this.getErrorMsg('property_image', this.props.errors))}
                        </Form.Text>
                </Form.Group>
              </Col>
          </Row>
          
        </Form>
      </div>
      </div>
    );
  }

}


// export default PropertyInfo;

PropertyInfo.propTypes = {
  errors: PropTypes.object,
  validate: PropTypes.func,
  isValid: PropTypes.func,
  handleValidation: PropTypes.func,
  getValidationMessages: PropTypes.func,
  clearValidations: PropTypes.func,
  getUserData: PropTypes.func,
  updateUserData: PropTypes.func
};


