import React from "react";

// bootstrap components
import { Row, Col, Button, Form, Dropdown, Modal, Accordion, Card } from "react-bootstrap";

// icons
import { Search, MoreVert, AddCircle, Category , HelpOutline} from '@material-ui/icons';

import TaskDetail from './task-detail';
import TaskAdd from './task-add';
import { getAllTasksAction, getTaskDetailsAction, deleteTaskAction } from './../../../actions/TransactionActions'
//vendor
import { getVendorTasksAction } from './../../../actions/VendorTransactionActions';
import { getBuyerTasksAction } from './../../../actions/BuyerTransactionActions';

import { connect } from 'react-redux';
import moment from 'moment';
import { throwStatement } from "@babel/types";
import noRecord from './../../../assets/img/no-record.png';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';


var _ = require('lodash');



class TransactionTask extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            addTask: "",
            editTask: "",
            show: false,
            OpenTaskDetail: false,
            OpenTaskAdd: false,
            home: true,
            radon: false,
            hoa: false,
            tasks: [],
            originalTasks: [],
            filteredTasks: [],
            searchedTasks: [],
            taskData: {},
            taskId: '',
            activeAccordion: [],
            taskFilter: <><span className="status-mark status-mark-grey"></span>All Tasks</>,
            openTask: '',
            showLoader: false,
            searchParam: '',
            filterParam: 'all',
            userRole: localStorage.getItem('usertype').toUpperCase(),
            deleteId: '',
            deleteTaskModal: false, 
        };

    }

    // show add task row
    addTask = (i) => {
        if (window.innerWidth > 991) {
            this.setState({ editTask: "", addTask: "open", taskId: '' });
        }
        else {
            this.setState({ OpenTaskAdd: !this.state.OpenTaskAdd });
        }
    }

    // show task detail
    editTask(id) {

        this.setState({ taskId: id, editTask: "" }, () => {
            if (window.innerWidth > 991) {
                this.setState({ editTask: "open", addTask: "" });
            }
            else {
                this.setState({ OpenTaskDetail: !this.state.OpenTaskDetail });
            }
        })

    }

    // open task accordion
    setOpen(i) {
        if (i == 1) {
            this.setState({ home: !this.state.home });
        }
        if (i == 2) {
            this.setState({ radon: !this.state.radon });
        }
        if (i == 3) {
            this.setState({ hoa: !this.state.hoa });
        }
    }

    formatTasks(unFormatedTasks) {
        var tasks = _.groupBy(unFormatedTasks, (e) => {
            return e.contingency || 'General'
        })
        var newTasks = [];
        var activeAccordion = [];
        Object.keys(tasks).forEach((key) => {
            var data = { name: key, data: tasks[key], open: false }
            newTasks.push(data)
            activeAccordion.push(true)
        })
        newTasks.forEach((e) => {
            e.data = _.sortBy(e.data, 'eventEndDate');
            // e.data.sort((a,b)=>{
            //     console.log(  a.eventEndDate , b.eventEndDate, a.eventEndDate > b.eventEndDate)
            //     return a.eventEndDate > b.eventEndDate ?  1 : -1 ;
            // })
        })
        newTasks.sort((a, b) => {
            return b.name == 'General' ? 1 : -1;
        });
        this.setState({ activeAccordion })
        return newTasks;
    }

    componentWillMount() {
        this.getAllTasks()
        if(this.props.history.location.taskId != undefined){
            this.editTask(this.props.history.location.taskId)
        }
    }

    getAllTasks(type) {
        this.setState({ showLoader: true, addTask: "" })

        if (localStorage.getItem('usertype').toUpperCase() === "VENDOR") {
            if (type == 'delete') {

                if (window.innerWidth > 991) {
                    this.setState({ editTask: "", addTask: "" });
                }
                else {
                    this.setState({ OpenTaskDetail: false });
                }

                this.props.getVendorTasksAction(this.props.transactionId).then(res => {
                    this.setState({
                        tasks: this.formatTasks(this.props.vendorTasks.data),
                        originalTasks: this.props.vendorTasks.data,
                        filteredTasks: this.props.vendorTasks.data,
                        showLoader: false
                    })

                })
            } else {
                this.props.getVendorTasksAction(this.props.transactionId).then(res => {
                    this.setState({
                        tasks: this.formatTasks(this.props.vendorTasks.data),
                        originalTasks: this.props.vendorTasks.data,
                        filteredTasks: this.props.vendorTasks.data,
                        showLoader: false
                    })

                })
            }

        }
        else if (localStorage.getItem('usertype').toUpperCase() === "BUYER") {


            this.props.getBuyerTasksAction(this.props.transactionId).then(res => {
                this.setState({
                    tasks: this.formatTasks(this.props.buyerTasks.data),
                    originalTasks: this.props.buyerTasks.data,
                    filteredTasks: this.props.buyerTasks.data,
                    showLoader: false
                })

            })

        }
        else {
            if (type == 'delete') {

                if (window.innerWidth > 991) {
                    this.setState({ editTask: "", addTask: "" });
                }
                else {
                    this.setState({ OpenTaskDetail: false });
                }

                this.props.getAllTasksAction(this.props.transactionId).then(res => {
                    this.setState({
                        tasks: this.formatTasks(this.props.tasks.data),
                        originalTasks: this.props.tasks.data,
                        filteredTasks: this.props.tasks.data,
                        showLoader: false
                    })

                })
            } else {
                this.props.getAllTasksAction(this.props.transactionId).then(res => {
                    this.setState({
                        tasks: this.formatTasks(this.props.tasks.data),
                        originalTasks: this.props.tasks.data,
                        filteredTasks: this.props.tasks.data,
                        showLoader: false
                    })

                })
            }

        }


    }

    toggleAccordion(i) {
        var activeAccordion = this.state.activeAccordion
        activeAccordion[i] = activeAccordion[i] == true ? false : true
        this.setState({
            activeAccordion
        })
    }

    renderCollapse(tasks, name) {
        return <Accordion.Collapse eventKey={name}>
            {/* Loop though tasks */}
            <div>
                {tasks.map((task, i) => {
                    return <Row key={'ts' + i} className={this.state.taskId == task._id ? "mx-0 align-items-center task-row active" : "mx-0 align-items-center task-row"}>
                        {/* active */}
                        <Col className="col-auto task-status-col">

                            {task.overDue ? <span className={"task-status task-status-orange"}></span> : <>
                                {task.status == 'SCHEDULED' && <span className={"task-status task-status-blue"}></span>}
                                {task.status == 'COMPLETED' && <span className={"task-status task-status-green"}></span>}
                            </>}

                            {/* {task.status == 'SCHEDULED' && <span className={"task-status task-status-blue"}></span>}
                            {task.status == 'COMPLETED' && <span className={"task-status task-status-green"}></span>}
                            {task.status == '' || task.status == undefined && <span className={"task-status task-status-orange"}></span>} */}

                            {/* <span className={"task-status task-status-" + (task.status == 'SCHEDULED' ? 'blue' : 'green')}></span>
                            <span className={"task-status task-status-" + (task.status == 'SCHEDULED' ? 'blue' : 'green')}></span> */}
                        </Col>
                        <Col className="task-name-col" onClick={() => this.editTask(task._id)}>
                            <div className="task-name">{task.title}</div>
                            <div className="task-date"><em>Due date: {moment(task.eventEndDate).format('MM/DD/YYYY hh:mm a')}</em></div>
                        </Col>
                        {/* <Col className="col-auto pr-0 task-date-col">
                            <div className="task-date">Due date: {moment(task.eventEndDate).format('MM/DD/YYYY')}</div>
                        </Col> */}

                        {(this.state.userRole === "VENDOR" || this.state.userRole === "BUYER")
                            ? ""
                            :
                            <Col className="col-auto px-0 task-action-col">
                                <Dropdown alignRight className="task-action">
                                    <Dropdown.Toggle variant="transparent" id="dropdown-basic" className="p-0">
                                        <MoreVert />
                                    </Dropdown.Toggle>

                                    <Dropdown.Menu>
                                        {/* <Dropdown.Item>Set Dependency</Dropdown.Item>
                                        <Dropdown.Item >
                                            Mark as Completed
                                    </Dropdown.Item>
                                        <Dropdown.Item >
                                            Mark as Overdue
                                    </Dropdown.Item> */}
                                        {/* <Dropdown.Item>
                                        Duplicate task
                                    </Dropdown.Item> */}
                                        <Dropdown.Item onClick={() => { this.deleteModal(task._id) }}>
                                            Delete task
                                    </Dropdown.Item>
                                    </Dropdown.Menu>
                                </Dropdown>
                            </Col>
                        }

                    </Row>
                })}
            </div>
        </Accordion.Collapse>
    }

    changeTaskFilter(param) {
        var tasks = this.state.originalTasks;
        var taskFilter = <><span className="status-mark status-mark-grey"></span> All Tasks</>
        if (param == 'scheduled') {
            taskFilter = <><span className="status-mark status-mark-blue"></span>Scheduled Tasks</>;
            tasks = tasks.filter((e) => {
                return (e.status.toLowerCase() == param) && e.overDue == false
            });
        }
        if (param == 'overdue') {
            taskFilter = <><span className="status-mark status-mark-orange"></span>Overdue Tasks</>;
            tasks = tasks.filter((e) => {
                return e.overDue
            });
        }
        if (param == 'completed') {
            taskFilter = <> <span className="status-mark status-mark-green"></span>Completed Tasks</>;
            tasks = tasks.filter((e) => {
                return ((e.status + '').toLowerCase() == param) && e.overDue == false
            });
        }
        if (param == 'all') {
            tasks = this.state.originalTasks
        }
        tasks = tasks.filter((e) => {
            return e.title.toLowerCase().includes(this.state.searchParam.trim().toLowerCase())
        });
        this.setState({ taskFilter, tasks: this.formatTasks(tasks), filteredTasks: tasks, filterParam: param })
    }

    searchTasks(param) {
        var tasks = this.state.originalTasks;
        var filteredTasks = [];
        if (this.state.filterParam == 'scheduled') {
            filteredTasks = tasks.filter((e) => {
                return (e.status.toLowerCase() == this.state.filterParam) && e.overDue == false
            });
        }
        if (this.state.filterParam == 'overdue') {
            filteredTasks = tasks.filter((e) => {
                return e.overDue
            });
        }
        if (this.state.filterParam == 'completed') {
            filteredTasks = tasks.filter((e) => {
                return ((e.status + '').toLowerCase() == this.state.filterParam) && e.overDue == false
            });
        }
        if (this.state.filterParam == 'all') {
            filteredTasks = this.state.originalTasks
        }
        tasks = filteredTasks.filter((e) => {
            return e.title.toLowerCase().includes(param.trim().toLowerCase())
        });
        this.setState({ searchParam: param, tasks: this.formatTasks(tasks), searchedTasks: tasks })
    }

    deleteTask(id) {
        this.setState({deleteTaskModal: false})
        this.props.deleteTaskAction(id).then(res => {
            this.getAllTasks('delete')

        })
    }

    deleteModal(id){
        this.setState({deleteId: id, deleteTaskModal: true});
    }

    updateTasks(name) {
        if (name == 'update') {
            this.getAllTasks();
        } else {
            this.setState({ addTask: '', editTask: "", taskId: '' })
        }
    }

    render() {
        return (
            <> {this.state.showLoader ?
                <div className="page-loader">
                    <div className="page-loader-inner">
                        <div className="spinner-border" role="status">
                            <span className="sr-only">Loading...</span>
                        </div>
                    </div>
                </div>
                : <>
                    <div className="task-page">

                        {(this.state.userRole === "VENDOR" || this.state.userRole === "BUYER")
                            ? ""
                            :
                            <Row className="mx-0 px-0 page-head-row page-head-row-inner align-items-center mt-10">
                                <Col>
                                    <Button variant="primary" className="btn-blue btn-head" onClick={() => this.addTask()}> + Add Task</Button>
                                </Col>
                            </Row>
                        }

                        <div className={(this.state.userRole === "VENDOR" || this.state.userRole === "BUYER") ? "task-block task-block-vendor" : "task-block"}>
                            <Row className="mx-0">
                                <Col className="px-0">
                                    <Row className="mx-0 py-1 align-items-center page-head-row-small task-search">
                                        <Col>
                                            <div className='icon-wrapper'>
                                                <Form.Control type="text" className="form-control-head" placeholder="Search by Name" onChange={(e) => { this.searchTasks(e.target.value) }} />
                                                <span className='form-control-icn'>
                                                    <Search />
                                                </span>
                                            </div>
                                        </Col>
                                        <Col className="col-auto" align="right">
                                            <Dropdown alignRight>
                                                <Dropdown.Toggle variant="transparent" id="dropdown-basic" className="p-0 d-flex align-items-center">
                                                    {this.state.taskFilter}
                                                </Dropdown.Toggle>

                                                <Dropdown.Menu>
                                                    <Dropdown.Item onClick={() => { this.changeTaskFilter('all') }}>
                                                        <span className="status-mark status-mark-grey"></span>
                                                        All Tasks
                                            </Dropdown.Item>
                                                    <Dropdown.Item onClick={() => { this.changeTaskFilter('scheduled') }}>
                                                        <span className="status-mark status-mark-blue"></span>
                                                        Scheduled Tasks
                                        </Dropdown.Item>
                                                    <Dropdown.Item onClick={() => { this.changeTaskFilter('overdue') }}>
                                                        <span className="status-mark status-mark-orange"></span>
                                                        Overdue Tasks
                                        </Dropdown.Item>
                                                    <Dropdown.Item onClick={() => { this.changeTaskFilter('completed') }}>
                                                        <span className="status-mark status-mark-green"></span>
                                                        Completed Tasks
                                        </Dropdown.Item>
                                                </Dropdown.Menu>
                                            </Dropdown>
                                        </Col>
                                    </Row>
                                    <div className="task-content">
                                        {/* <div className="task-row-group">
                                { this.state.addTask ? 
                                    <Row className="mx-0 align-items-center task-row default-row">
                                        <Col className="col-auto">
                                            <span className="task-status task-status-grey"></span>
                                        </Col>
                                        <Col>
                                            <div className="new-task-input-wrapper">
                                                <Form.Control type="text" className="new-task-input" placeholder="Write task name and press enter" />
                                                <Button variant="transparent" className="new-task-input-clear">
                                                    <AddCircle/>
                                                </Button>
                                            </div>
                                        </Col>
                                    </Row>
                                : null }
                            </div> */}
                                        {/* Loop through categories */}

                                        {(this.state.tasks.length <= 0 && this.state.addTask == '') &&
                                            <div className="no-transaction no-record">
                                                <div className="no-transaction-inner">
                                                    <img src={noRecord} alt="no record" />
                                                    <p>No task found</p>
                                                </div>
                                            </div>
                                        }

                                        {this.state.tasks.map((t, i) => {
                                            return <ExpansionPanel defaultExpanded={true} className="task-combo">
                                                <ExpansionPanelSummary
                                                    expandIcon={<ExpandMoreIcon />}
                                                    aria-controls="panel1a-content"
                                                    className="task-row task-accordion card-header"
                                                    id="panel1a-header"
                                                >
                                                    <span></span>
                                                    {t.name}
                                                </ExpansionPanelSummary>
                                                <ExpansionPanelDetails className="task-row-detail">
                                                    <div className="task-row-group">
                                                        {t.data.map((task, i) => {
                                                            return <Row key={'ts' + i} className={this.state.taskId == task._id ? "mx-0 align-items-center task-row active" : "mx-0 align-items-center task-row"}>
                                                                {/* active */}
                                                                <Col className="col-auto task-status-col">
                                                                    {task.status == 'COMPLETED' ? <span className={"task-status task-status-green"}></span> :
                                                                    <>{task.overDue ? <span className={"task-status task-status-orange"}></span> : <>
                                                                        {task.status == 'SCHEDULED' && <span className={"task-status task-status-blue"}></span>}
                                                                       
                                                                    </>}</>
                                                                    }

                                                                    {/* <span className={"task-status task-status-" + (task.status == 'SCHEDULED' ? 'blue' : 'green')}></span>
                            <span className={"task-status task-status-" + (task.status == 'SCHEDULED' ? 'blue' : 'green')}></span> */}
                                                                </Col>
                                                                <Col className="task-name-col" onClick={() => this.editTask(task._id)}>
                                                                    <div className="task-name">{task.title}</div>
                                                                    <div className="task-date"><em>Due date: {moment(task.eventEndDate).format('MM/DD/YYYY hh:mm a')}</em></div>
                                                                </Col>
                                                                {/* <Col className="col-auto pr-0 task-date-col">
                            <div className="task-date">Due date: {moment(task.eventEndDate).format('MM/DD/YYYY')}</div>
                        </Col> */}

                                                                {(this.state.userRole === "VENDOR")
                                                                    ? ""
                                                                    :
                                                                    <Col className="col-auto px-0 task-action-col">
                                                                        <Dropdown alignRight className="task-action">
                                                                            <Dropdown.Toggle variant="transparent" id="dropdown-basic" className="p-0">
                                                                                <MoreVert />
                                                                            </Dropdown.Toggle>

                                                                            <Dropdown.Menu>
                                                                                {/* <Dropdown.Item>Set Dependency</Dropdown.Item>
                                                                                <Dropdown.Item >
                                                                                    Mark as Completed
                                                                                </Dropdown.Item>
                                                                                <Dropdown.Item >
                                                                                    Mark as Overdue
                                                                                </Dropdown.Item> */}
                                                                                {/* <Dropdown.Item>
                                                                                    Duplicate task
                                                                                </Dropdown.Item> */}
                                                                                {(this.state.userRole === "VENDOR" || this.state.userRole === "BUYER") ?'':
                                                                                <Dropdown.Item onClick={() => { this.deleteModal(task._id) }}>
                                                                                    Delete task
                                                                                </Dropdown.Item>}
                                                                            </Dropdown.Menu>
                                                                        </Dropdown>
                                                                    </Col>
                                                                }

                                                            </Row>
                                                        })}
                                                    </div>
                                                </ExpansionPanelDetails>
                                            </ExpansionPanel>
                                            // return <Accordion defaultActiveKey={t.name} key={'Category' + i} ><div className="task-row-group" >
                                            //     <Row className="mx-0 align-items-center ">
                                            //         <Col>
                                            //             <Accordion.Toggle as={Card.Header} className={this.state.activeAccordion[i] ? 'collapsed task-row task-accordion' : 'task-row task-accordion'} variant="link" eventKey={t.name} onClick={() => { this.toggleAccordion(i) }}>
                                            //                 {t.name} <span></span>
                                            //             </Accordion.Toggle>
                                            //         </Col>
                                            //     </Row>
                                            //     {this.renderCollapse(t.data, t.name)}
                                            // </div></Accordion>
                                        })}

                                    </div>
                                </Col>
                                <Col className="px-0 col-auto">
                                    <div className={this.state.editTask + " task-single calendar-add-task"}>
                                        {this.state.editTask == 'open' && <TaskDetail taskId={this.state.taskId} transactionData={this.props.transactionData}
                                            deleteTask={(id) => { this.deleteModal(id)}}
                                            onUpdate={(e) => this.updateTasks(e)}
                                        />}
                                    </div>

                                    <div className={this.state.addTask + " task-single calendar-add-task"}>
                                        {this.state.addTask == 'open' &&
                                            <TaskAdd onUpdate={(e) => this.updateTasks(e)} taskId={this.state.taskId} transactionData={this.props.transactionData} />
                                        }
                                    </div>
                                </Col>
                            </Row>

                            {/* Task Detail in Mobile */}
                            <Modal
                                show={this.state.OpenTaskDetail}
                                onHide={() => this.editTask()}
                                dialogClassName="modal-90w"
                                aria-labelledby="example-custom-modal-styling-title"
                                className="modal-med modal-view-page task-mobile-modal"
                            >
                                <Modal.Header closeButton></Modal.Header>
                                <Modal.Body>
                                    <TaskDetail taskId={this.state.taskId} transactionData={this.props.transactionData}
                                        deleteTask={(id) => { this.deleteModal(id)}}
                                        onUpdate={(e) => this.updateTasks(e)}
                                    />
                                </Modal.Body>
                            </Modal>


                            {/* Task Add in Mobile */}
                            <Modal
                                show={this.state.OpenTaskAdd}
                                onHide={() => this.addTask()}
                                dialogClassName="modal-90w"
                                aria-labelledby="example-custom-modal-styling-title"
                                className="modal-med modal-view-page task-mobile-modal"
                            >
                                <Modal.Header closeButton></Modal.Header>
                                <Modal.Body>
                                    <TaskAdd onUpdate={(e) => this.updateTasks(e)} taskId={this.state.taskId} transactionData={this.props.transactionData} />
                                </Modal.Body>
                            </Modal>

                            {/* Delete Task Modal */}
                            <Modal
                                show={this.state.deleteTaskModal}
                                // onHide={() => this.deletePrompt()}
                                dialogClassName="modal-90w"
                                aria-labelledby="example-custom-modal-styling-title"
                                className="modal-med modal-400 modal-delete"
                                centered
                                backdrop={false}
                            >
                                <Modal.Body className="pb-0 text-center">
                                    <HelpOutline />
                                    <p>Are you sure you want to delete?</p>
                                </Modal.Body>
                                <Modal.Footer>
                                    <Button variant='primary' className="btn-blue btn-outlined w-140" onClick={() => { this.setState({ deleteTaskModal: false }) }}>Cancel</Button>
                                    <Button variant='primary' className="btn-blue w-140" onClick={() => { this.deleteTask(this.state.deleteId) }}>Yes</Button>
                                </Modal.Footer>
                            </Modal>
                        </div>
                    </div>

                    {/* {(this.state.tasks.length <= 0 && this.state.addTask == '') &&
                        <div className="no-transaction no-record">
                            <div className="no-transaction-inner">
                                <img src={noRecord} alt="no record" />
                                <p>No task found</p>
                            </div>
                        </div>} */}
                </>}
            </>
        );
    }

}


const mapStateToProps = state => {
    return {
        tasks: state.reducer.tasks,
        vendorTasks: state.reducer.vendorTasks,
        buyerTasks: state.reducer.buyerTasks
    };
};

export default connect(mapStateToProps, { getAllTasksAction, getTaskDetailsAction, deleteTaskAction, getVendorTasksAction, getBuyerTasksAction })(TransactionTask);
