import React from 'react';

// Bootstrap components
import { Row, Col, Form, Button, Collapse, Modal, Dropdown, Accordion, Card, Alert } from "react-bootstrap";
import Toast from 'react-bootstrap/Toast'


// icons
import { Search, KeyboardArrowDown, KeyboardArrowRight, MoreHoriz, Delete, ControlCamera } from '@material-ui/icons';

import DataTable from 'react-data-table-component';
import { connect } from 'react-redux'
import {
  registrationRequestsAction, activeUserAction, removeRegistartionAction, rejectRegistartionAction,
  rejectRegistartionsListAction
} from './../../actions/UserActions'

import noRecord from './../../assets/img/no-record.png';


class RegistrationRequests extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      showArchieve: false,
      show: false,
      status: null,
      UserMessage: '',
      searchParam: '',
      toggleActive: '',
      showDelete: 'Registration Requests',
      users: [],
      showLoader: true,
      modalUserData: {},
      modalCmpnyData: {},
      recordPerPage: 100,
      lastSyncTimestamp: "",
      totalRows: 6,
      showRejections: false,
      filterData: [],
      rejectedUsers: [],
      searchedRequests: [],
      originalRequestsUsers: [],
      filteredRequestsUsers: [],
      btnName: 'Rejected Requests',
      isHideSts: true
    };
  }


  columns = [
    {
      name: 'Name',
      selector: 'name',
      sortable: true,
      ignoreRowClick: true,
      // cell: () => <Button raised primary >Action</Button>,
    },
    {
      name: 'Licence No',
      selector: 'licence',
      sortable: true,
      width: '140px',
    },
    {
      name: 'Company Name',
      selector: 'company',
      sortable: true,
    },
    {
      name: 'Mobile No.',
      selector: 'mobile',
      sortable: true,
      width: '140px',
    },
    {
      name: 'Email',
      selector: 'email',
      sortable: true,
      width: '25%',
    },
    {
      name: '',
      selector: 'active',
      sortable: true,
      width: '105px',
      ignoreRowClick: true,
    },
    {
      name: '',
      selector: 'reject',
      sortable: true,
      width: '105px',
      ignoreRowClick: true,
    },
    {
      name: '',
      selector: 'action',
      width: '60px',
      right: true,
      ignoreRowClick: true,
    },
  ];

  // Registration Requests modal
  setModalOpen(userData) {
    var user = {}
    user._id = userData._id;
    user.brokerLicenseNo = userData.brokerLicenseNo;
    user.licenseExpirationDate = userData.licenseExpirationDate;
    user.firstname = userData.firstname;
    user.lastname = userData.lastname;
    user.mobileNumber = userData.mobileNumber;
    user.otherNumber = (userData.otherNumber != "") ? userData.otherNumber : "-";
    user.emailId = userData.email.address;
    user.showRejections = this.state.showRejections;

    var cmpny = {}
    cmpny = userData.companyInformation;

    if (cmpny.website === "" || cmpny.website === undefined) {
      cmpny.website = "-";
    }

    if (cmpny.companySize === "" || cmpny.companySize === undefined) {
      cmpny.companySize = "-";
    }

    if (cmpny.typeOfBusiness === "" || cmpny.typeOfBusiness === undefined) {
      cmpny.typeOfBusiness = "-";
    }

    if (cmpny.faxNumber === "" || cmpny.faxNumber === undefined) {
      cmpny.faxNumber = "-";
    }

    this.setUserDataModal(user, cmpny);
    this.setState({ show: !this.state.show });
  }

  setUserDataModal(user, cmpny) {
    this.setState({ modalUserData: user, modalCmpnyData: cmpny });
  }

  colseModal() {
    this.setState({ show: !this.state.show });
  }

  toggleActive() {
    if (this.state.toggleActive == 'active') {
      this.setState({ toggleActive: "" });
    } else {
      this.setState({ toggleActive: "active" });
    }

  }

  componentWillMount() {
    this.props.registrationRequestsAction(this.state.recordPerPage, this.state.lastSyncTimestamp).then(res => {
      this.setState({
        users: this.props.users || [],
        originalRequestsUsers: this.props.users || [],
        filteredRequestsUsers: this.props.users || [],
      });
    })
  }

  getRejectedUsers(flag) {
    this.setState({ status: false, showLoader: true })
    if (!this.state.showRejections || flag) {
      this.props.rejectRegistartionsListAction().then(res => {
        setTimeout(
          function () {

            this.setState({
              status: null,
              UserMessage: null
            });
          }
            .bind(this),
          3000
        );
        this.setState({
          searchParam: '',
          showRejections: true,
          showLoader: false,
          showDelete: 'Rejected Requests',
          users: res.data.data,
          originalRequestsUsers: res.data.data || [],
          filteredRequestsUsers: res.data.data || [],
          btnName: 'Registration Requests',
          status: null,
          UserMessage: null,
        })

      });
    } else {
      this.props.registrationRequestsAction(this.state.recordPerPage, this.state.lastSyncTimestamp)
      this.setState({
        showRejections: false,
        showLoader: false,
        showDelete: 'Registration Requests',
        btnName: 'Rejected Requests',
      })
    }


  }

  search(param) {
    param = param.trim()
    var requests = [];
    requests = this.state.filteredRequestsUsers.filter((o) => {
      var found = false;
      Object.keys(o).forEach((v) => {
        if (v == 'firstname' || v == 'lastName' || v == 'mobileNumber' || v == 'brokerLicenseNo') {
          var item = o[v] + ''
          if (item.toLowerCase().includes(param.toLowerCase())) {
            found = true
          }
        } else if (v == 'email') {
          var item = o[v].address + ''
          if (item.toLowerCase().includes(param.toLowerCase())) {
            found = true
          }

        }
      })
      return found
    })
    this.setState({
      users: requests,
      searchParam: param,
      isSearched: param.trim() == '' ? false : true,
    })
  }

  componentWillReceiveProps(nextProps) {
    this.setState(nextProps);
    this.setState({ showLoader: false })
  }

  activeUserAction(userId) {
    this.setState({ showLoader: true })
    this.props.activeUserAction(userId).then(res => {
      if (this.state.showRejections) {
        this.getRejectedUsers(this.state.showRejections);
        this.setState({ isHideSts: false })
      } else {
        this.props.registrationRequestsAction(this.state.recordPerPage, this.state.lastSyncTimestamp).then(res => {
          this.setState({ isHideSts: false })
        })
      }

      this.setState({ showLoader: false })
    });
    setTimeout(
      function () {
        this.setState({
          status: null,
          UserMessage: null
        });
      }
        .bind(this),
      3000
    );

  }


  //   getSelectedRows = (state) => {
  //     if(state.selectedRows.length > 1 ){
  //         this.setState({
  //             showDelete: <Button variant="transparent" className="color-grey-1 px-0"><Delete/></Button>
  //         })
  //     }
  //     else{
  //         this.setState({
  //             showDelete: 'Registration Requests'
  //         })
  //     }
  //   };

  changePage = async page => {

    if (page !== 1) {
      let lastRecordData = this.props.users[this.props.users.length - 1];
      let lastSyncTimestamp = lastRecordData.modifiedDate;
      if (lastRecordData !== undefined && lastRecordData !== null && lastRecordData !== "") {

        //await this.props.registrationRequestsAction(this.state.recordPerPage,lastSyncTimestamp)
      }

    }
    //console.log("lastRecordData",lastRecordData);

    //
  }
  removeRegistration(uId) {
    this.setState({ showLoader: true })
    this.props.removeRegistartionAction(uId).then(res => {
      if (this.props.status) {
        setTimeout(
          function () {
            this.setState({
              status: null,
              UserMessage: null
            });
          }
            .bind(this),
          3000
        );
        this.setState({ isHideSts: false })
        if (this.state.showRejections) {
          this.getRejectedUsers(true)
        } else {
          this.props.registrationRequestsAction(this.state.recordPerPage, this.state.lastSyncTimestamp).then(res => {
            this.setState({ isHideSts: false })
          })
        }
        this.setState({ showLoader: false })
      }

    })
  }
  rejectRegistration(uId) {
    this.setState({ showLoader: true })
    this.props.rejectRegistartionAction(uId).then(res => {
      if (this.props.status) {
        setTimeout(
          function () {
            this.setState({
              status: null,
              UserMessage: null
            });
          }
            .bind(this),
          3000
        );
        this.props.registrationRequestsAction(this.state.recordPerPage, this.state.lastSyncTimestamp).then(res => {
          this.setState({ isHideSts: false })
        })
      }
      this.setState({ showLoader: false })
    });
  }


  render() {

    console.log("this.state.status",this.state.status)
    console.log("this.props.UserMessage",this.props.UserMessage)

    // if(this.props.status === true) {
    //   setTimeout(() => {
    //     window.location.reload();
    //   }, 1000);

    // } 

    let rows = [];

    for (var i = 0, len = this.state.users.length; i < len; i++) {
      let user = this.state.users[i];

      let data = {
        id: user._id,
        name: <Button variant="link" className="p-0" onClick={() => this.setModalOpen(user)}>{user.firstname} {user.lastname}</Button>,
        licence: user.brokerLicenseNo,
        company: user.companyInformation ? user.companyInformation.name : "",
        mobile: user.mobileNumber,
        email: <div className="email-case">{user.email.address}</div>,
        active: <Button variant="transparent" className={"btn-blue btn-outlined btn-grid "} onClick={() => this.activeUserAction(user._id)} >Activate</Button>,
        reject: <Button variant="transparent" className={"btn-blue btn-outlined btn-grid "} onClick={() => this.rejectRegistration(user._id)} >Reject</Button>,
        action:
          <Dropdown alignRight className="dropdown-action">
            <Dropdown.Toggle variant="transparent" id="dropdown-folder1" className="p-0">
              <MoreHoriz />
            </Dropdown.Toggle>

            <Dropdown.Menu>
              <Dropdown.Item className="color-dark-orange-3 font-14" onClick={() => { this.removeRegistration(user._id) }}>Remove</Dropdown.Item>
            </Dropdown.Menu>
          </Dropdown>

      }
      if (this.state.showRejections) {
        // delete data['active'];
        delete data['reject'];
        // delete data['action'];
      }
      rows.push(data);
    }

    return (

      <div>
        <Row className="mx-0 page-head-row page-head-row-big align-items-center">
          <Col>
            <h4 className="title-med mt-0 pt-1">
              {this.state.showDelete}
            </h4>
          </Col>
          <Col xs="4" align="right">
            <div className='icon-wrapper ml-2'>
              <Form.Control type="text" className="form-control-head" placeholder="Search" value={this.state.searchParam} onChange={(e) => { this.search(e.target.value) }} />
              <span className='form-control-icn'>
                <Search />
              </span>
            </div>
          </Col>
          <Col className="col-auto pl-0">
            <Button
              variant="primary"
              className={'btn-blue btn-blue btn-head w-140'}
              onClick={() => this.getRejectedUsers()}
            >
              {this.state.btnName}
            </Button>
          </Col>
        </Row>

        {(this.props.UserMessage !== null && !this.state.isHideSts && this.state.status !== null && this.state.status) ?
          <Alert variant="success">{this.state.UserMessage}</Alert>
          : ''
        }

        {(this.props.UserMessage !== null && !this.state.isHideSts && this.state.status !== null && !this.state.status) ?
          <Alert variant="danger">{this.state.UserMessage}</Alert> : ''
        }

        <div className="dt-content-area">
          <DataTable
            // title="Arnold Movies"
            columns={this.columns}
            data={rows}
            selectableRows
            sortIcon={<KeyboardArrowDown className="color-grey-1" />}
            highlightOnHover={true}
            className="rdt-requests"
            //                progressPending={true}
            onRowSelected={this.getSelectedRows}
            // pagination
            // selectableRowsComponent={Checkbox} // Pass the function only
            // selectableRowsComponentProps={{ uncheckedIcon: handleIndeterminate  }}
            noDataComponent={
              <div className="no-transaction no-record no-datatable">
                <div className="no-transaction-inner">
                  <img src={noRecord} alt="no record" />
                  <p>No Requests Found</p>
                </div>
              </div>
            }
            pagination={true}
            paginationServer={true}
            paginationTotalRows={this.state.totalRows}
            paginationPerPage={this.state.recordPerPage}
            onChangePage={(page) => this.changePage(page)}
            paginationRowsPerPageOptions={[this.state.recordPerPage]}
          />

          {this.state.showLoader === true &&
            <div className="page-loader">
              <div className="page-loader-inner">
                <div className="spinner-border" role="status">
                  <span className="sr-only">Loading...</span>
                </div>
              </div>
            </div>
          }
          <Modal
            show={this.state.show}
            onHide={() => this.colseModal()}
            dialogClassName="modal-90w"
            aria-labelledby="example-custom-modal-styling-title"
            className="modal-small"
          >
            <Modal.Header className="modal-header-none" closeButton></Modal.Header>
            <Modal.Body>
              <h2 className="font-24 mb-0 mt-2">{this.state.modalUserData.firstname} {this.state.modalUserData.lastname}</h2>
              <div>
                <span>Broker License: </span>
                <label className="font-600 mb-0">{this.state.modalUserData.brokerLicenseNo}</label>
              </div>
              <div className="font-14" style={{ color: '#0052d6' }}>
                <span>Expire on: </span>
                <span>{this.state.modalUserData.licenseExpirationDate}</span>
              </div>
              <div className="font-14 mt-2">
                <label className="mb-0 font-600 mr-2">Mobile: </label>
                <span>{this.state.modalUserData.mobileNumber}</span>
              </div>
              <div className="font-14">
                <label className="mb-0 font-600 mr-2">Phone: </label>
                <span>{this.state.modalUserData.otherNumber}</span>
              </div>
              <div className="font-14">
                <label className="mb-0 font-600 mr-2">Email: </label>
                <span>{this.state.modalUserData.emailId}</span>
              </div>
              <hr />

              <h5 className="text-uppercase font-14 mb-0" style={{ color: '#206ec5' }}>Company Information</h5>
              <h2 className="font-24 mb-0">{this.state.modalCmpnyData.name}</h2>
              <p className="font-14 mb-2">{this.state.modalCmpnyData.address} <br />{this.state.modalCmpnyData.city}, {this.state.modalCmpnyData.state} {this.state.modalCmpnyData.zipcode}</p>

              <div className="font-14">
                <label className="mb-0 font-600 mr-2">Phone: </label>
                <span>{this.state.modalCmpnyData.phoneNumber}</span>
              </div>
              <div className="font-14">
                <label className="mb-0 font-600 mr-2">Website: </label>
                <span>{this.state.modalCmpnyData.website}</span>
              </div>
              <div className="font-14">
                <label className="mb-0 font-600 mr-2">Type of Business: </label>
                <span>{this.state.modalCmpnyData.typeOfBusiness}</span>
              </div>
              <div className="font-14">
                <label className="mb-0 font-600 mr-2">Fax: </label>
                <span>{this.state.modalCmpnyData.faxNumber}</span>
              </div>
              <div className="font-14">
                <label className="mb-0 font-600 mr-2">Company Size: </label>
                <span>{this.state.modalCmpnyData.companySize}</span>
              </div>
              <hr className="mb-0" />

            </Modal.Body>
            <Modal.Footer>
              {(this.state.showRejections) ?
                <Button variant="primary" size="sm" onClick={() => this.activeUserAction(this.state.modalUserData._id)} className="btn-blue btn-grid btn-outlined">Activate</Button>
                :
                <>
                  <Button variant="primary" size="sm" onClick={() => this.activeUserAction(this.state.modalUserData._id)} className="btn-blue btn-grid btn-outlined">Activate</Button>
                  <Button variant="primary" size="sm" onClick={() => this.activeUserAction(this.state.modalUserData._id)} className="btn-blue btn-grid btn-outlined">Reject</Button>
                </>
              }

            </Modal.Footer>
          </Modal>
        </div>
      </div>
    );
  }

}


//export default RegistrationRequests;


const mapStateToProps = state => {
  return {
    users: state.reducer.users,
    status: state.reducer.status,
    UserMessage: state.reducer.UserMessage,
  };
};

export default connect(mapStateToProps, {
  registrationRequestsAction, activeUserAction, removeRegistartionAction,
  rejectRegistartionAction, rejectRegistartionsListAction
})(RegistrationRequests)
