import { KeyboardArrowLeft, KeyboardArrowRight, Search } from '@material-ui/icons';
import 'bootstrap-daterangepicker/daterangepicker.css';
import moment from 'moment';
import React from 'react';
// Calendar
import BigCalendar from 'react-big-calendar';
// Bootstrap components
import { Col, Form, Row } from "react-bootstrap";
import { connect } from 'react-redux';
import { getAllCalendarEventsAction, getTransactionDataAction, deleteTaskAction } from './../../actions/TransactionActions';
import './../../assets/scss/plugins/_plugin-react-big-calendar.scss';
import TaskDetail from './../Transactions/Detail/task-detail';

const localizer = BigCalendar.momentLocalizer(moment);



class CalendarModule extends React.Component {
  constructor(props) {
    super(props);

    this.setStartDate = this.setStartDate.bind(this);
    this.setEndDate = this.setEndDate.bind(this);
    this.state = {
      events: [],
      alert: null,
      addTask:"",
      startDate: moment(),
      closeDate: moment().subtract(-29, 'days'),
      viewTask: false,
      viewState: 'month',
      taskId: '',
      transactionData: {},
      showLoader: false,
      
    };
    this.hideAlert = this.hideAlert.bind(this);
    this.formats = {
      eventTimeRangeFormat: () => ""
    };
  }

  

   // set start datepicker
   setStartDate(event, picker) {
        this.setState({
            startDate: picker.startDate,
        });
    }

    // set start endpicker
    setEndDate(event, picker) {
        this.setState({
            closeDate: picker.startDate,
        });
    }

    // toggle task
    toggleTask = (i) =>{
        if(i == 0) { 
            this.setState({ addTask: "open" });
        } else {
          this.setState({ addTask: "" });
        }
    }

  // view task modal
  
  selectedEvent(event){
    console.log(event)
    this.props.getTransactionDataAction(event.transactionId).then(res => {
			if (res) {
        this.setState({viewTask:!this.state.viewTask, transactionData: res, taskId: event._id});
			}
		});
  }

  loadCalendarEvents(){
    this.setState({ showLoader: true})
    this.props.getAllCalendarEventsAction().then(res=>{
      var events = [];
      if(res){
        res.forEach((tr)=>{
          tr.forEach((e)=>{
            var color = 'blue';
            if(e.status == 'SCHEDULED'){
              color = 'blue'
            } else if(e.status == 'COMPLETED'){
              color = 'green'
            }

            if(moment(e.eventEndDate) < moment()){
              color = 'orange'
            }
            events.push({
              title: moment(e.eventEndDate).format('h:mm A, ') +e.title,
              allDay: false,
              start: new Date(moment(e.eventEndDate).get('year'), moment(e.eventEndDate).get('month'), moment(e.eventEndDate).get('date'), moment(e.eventEndDate).get('hour'), moment(e.eventEndDate).get('minutes')),//'2019-08-02',
              end: new Date(moment(e.eventEndDate).get('year'), moment(e.eventEndDate).get('month'), moment(e.eventEndDate).get('date'), moment(e.eventEndDate).get('hour'), moment(e.eventEndDate).get('minutes')),
              color: color,
              _id : e._id,
              transactionId: e.transactionId,
            });
          });
        });
        this.setState({
          events,
          showLoader: false
        })
      }
    }, error=>{
      this.setState({
        showLoader: false
      })
    });
  }

  addNewEventAlert(slotInfo){
    console.log(slotInfo)
    this.toggleTask(0);
    // this.setState({
      
    //   alert: ()
    // });
  }

  addNewEvent(e,slotInfo){
    var newEvents = this.state.events;
    newEvents.push({
      'title': e,
      'start':slotInfo.start,
      'end':slotInfo.end
    })
    this.setState({
      alert: null,
      events: newEvents
    })
  }
  hideAlert(){
    this.setState({
      alert: null
    });
  }

  deleteTask(id) {
    this.setState({ viewTask: false}); 
    this.props.deleteTaskAction(id).then(res => {
      this.loadCalendarEvents()
    })
  }

  eventColors(event, start, end, isSelected) {
    var backgroundColor = "event-";
    event.color ? (backgroundColor=backgroundColor+event.color):(backgroundColor=backgroundColor+"default");
    return {
      className: backgroundColor
    };
  }

  componentWillMount(){
    this.loadCalendarEvents()
  }

  changeView(view, toolbar){
    toolbar.onView(view);
    this.setState({ viewState: view });
  }

  render() {
    const startDate = this.state.startDate.format('MM/DD/YYYY');
    const closeDate = this.state.closeDate.format('MM/DD/YYYY');

    const CustomToolbar = (toolbar) => {
      
      const goToBack = () => { toolbar.onNavigate('PREV'); };

      const goToNext = () => { toolbar.onNavigate('NEXT'); };

      const goToCurrent = () => { toolbar.onNavigate('TODAY'); };      

      const label = () => {
        const date = moment(toolbar.date);
        console.log(toolbar)
        return (
          <span>
            {toolbar.label}
          </span>
        );
      };
    
      return (
        <div className="rbc-calendar">
          <div className="rbc-toolbar d-block">
            <Row className="justify-content-between align-items-center">
              <Col xs={12} sm={4}>
                <span className="rbc-btn-group d-flex">
                    <button type="button" onClick={goToCurrent}>Today</button>
                    <div className='icon-wrapper ml-2'>
                        <Form.Control type="text" className="form-control-head" placeholder="Transaction list" />
                        <span className='form-control-icn'>
                            <Search/>
                        </span>
                    </div>
                </span>
              </Col>
              <Col xs={12} sm={4} className="px-0">
                <span className="rbc-toolbar-label">
                    <button type="button" onClick={goToBack}>
                        <KeyboardArrowLeft/>
                    </button>
                    {label()}
                    <button type="button" onClick={goToNext}>
                        <KeyboardArrowRight/>
                    </button>
                </span>
              </Col>
              <Col xs={12} sm={4} align="right">
                <span className="rbc-btn-group cal-view">
                    <button type="button" className={this.state.viewState == 'day' && "rbc-active"} onClick={()=> this.changeView('day', toolbar)}>Day</button>
                    <button type="button" className={this.state.viewState == 'week' && "rbc-active"} onClick={()=>this.changeView('week', toolbar)}>Week</button>
                    <button type="button" className={this.state.viewState == 'month' && "rbc-active"} onClick={()=>this.changeView('month', toolbar)}>Month</button>
                </span>
              </Col>
            </Row>
              
          </div>
        </div>
      );
    };
    return (<>
      {this.state.showLoader &&
        <div className="page-loader">
            <div className="page-loader-inner">
                <div className="spinner-border" role="status">
                    <span className="sr-only">Loading...</span>
                </div>
            </div>
        </div>
      }
      <div className="calendar-page">
        <Row className="ml-0 mr-0 page-head-row page-head-row-big align-items-center">
          <Col>
            <h4 class="title-med mt-0 pt-1">Calendar</h4>
          </Col>
          <Col align="right">
            <ul className="list-inline status-mark-list mb-0 font-12">
              <li className="status-mark-text d-inline-flex align-items-center mr-2">
                <span className="status-mark status-mark-blue"></span>
                Scheduled Tasks
              </li>
              <li className="status-mark-text d-inline-flex align-items-center mr-2">
                <span className="status-mark status-mark-orange"></span>
                Overdue Tasks
              </li>
              <li className="status-mark-text d-inline-flex align-items-center mr-2">
                <span className="status-mark status-mark-green"></span>
                Completed Tasks
              </li>
            </ul>
          </Col>
        </Row>
        <div className="admin-content-area mt-10">

          {/* add task slide */}
          {/* <div className={this.state.addTask + " calendar-add-task auto-task"}>
            {this.state.addTask == 'open' &&
              <TaskAdd onUpdate={(e) => this.updateTasks(e)} transactionData={this.props.transactionData} />
            }
          </div> */}

          {/* calendar */}
          <BigCalendar
            views={['day','week','month']}
            localizer={localizer}
            selectable
            components={{
              toolbar: CustomToolbar
            }}
            events={this.state.events}
            defaultView='month'
            scrollToTime={new Date(1970, 1, 1, 6)}
            defaultDate={new Date()}
            onSelectEvent={event => this.selectedEvent(event)}
            onSelectSlot={(slotInfo) => this.addNewEventAlert(slotInfo)}
            // formats={this.formats}
            tooltipAccessor={(e)=> {return e.title}}
            eventPropGetter={this.eventColors}
          />

          {/* View task slide */}
          <div className={(this.state.viewTask && 'open') + " calendar-add-task task-single cal-task-slide"}>
            {this.state.viewTask &&
              <TaskDetail 
                taskId={this.state.taskId}
                deleteTask={(id) => { this.deleteTask(id); }}
                onUpdate={(e) => { this.loadCalendarEvents();}}
                transactionData={this.state.transactionData}
                // reloadPage={() => this.getAllTasks()}
                fromTimeline={true}
                closeDetailsModal={() => {this.setState({ viewTask: false})}}
              />
            }
          </div>

        </div>
      </div>
      </>
    );
  }

}

const mapStateToProps = state => {
  return {
      taskData: state.reducer.taskData,
  };
};


export default connect(mapStateToProps, { getAllCalendarEventsAction, getTransactionDataAction, deleteTaskAction })(CalendarModule);
