    //Set contingecies for selected campaign
    setContingencyForCamp(selectedcamp) {
        //set contingencies
        //let selectedcamp = e.target.value;
        let campContingencies = [];
        let camDays = 0;
        for (var i = 0, len = this.state.campaignStyle.length; i < len; i++) {
            let camp = this.state.campaignStyle[i];
            if (camp._id === selectedcamp) {
                campContingencies = camp.contingencies;
                camDays = camp.campaignDays;
            }
        }

        let sdate = moment()
        let edate = moment().subtract(-camDays, 'days');

        let contingencies = [];
        for (var key in campContingencies) {
            if (campContingencies.hasOwnProperty(key)) {
                contingencies.push({
                    "name": key, 
                    "startDate": sdate.format('YYYY-MM-DD'),
                    'endDate': edate.format('YYYY-MM-DD')
                });
            }
        }

        // this.state.transactionDataInfo
        this.setState({ newcampContingencies: contingencies });
    }
