    openConfirmPopup(sts) {

        let msg = "";
        if (sts === "start") {
            msg = "Please confirm the new start date & closing date";
            this.setState({ showStartDate: true, showCloseDate: true, chanedSts: "INPROGRESS" })
        }

        if (sts === "resume") {
            msg = "Please select the new closing date";
            this.setState({ showStartDate: false, showCloseDate: true, chanedSts: "INPROGRESS" })
        }

        if (sts === "pause") {
            msg = "All the pending tasks will be stopped and the transaction will be marked as Paused, Are you sure want to continue?"
            this.setState({ showStartDate: false, showCloseDate: false, chanedSts: "PAUSED" })
        }

        if (sts === "cancel") {
            msg = "All the pending tasks will be marked as cancled. Are you sure want to continue?"
            this.setState({ showStartDate: false, showCloseDate: false, chanedSts: "CANCELED" })
        }

        if (msg !== "") {
            this.setState({ promptMsg: msg, startTransaction: true })
        }

    }

