    //Upload images
    uploadImage(e) {
        var files = e.target.files;
        var fileNames = [];
        var formData = new FormData();

        for (let i = 0; i < files.length; i++) {
            formData.append('file', files[i]);
            fileNames.push(files[i].name);
        }
        APIUtil.postFormDataMethod(UrlConstants.uploadPropertyImage, formData, true).then(res => {
            this.setState({ updatedKeys: ['image'] })
            this.updateAPI(fileNames, 'images');
        });
    }
