    //Call api for update transaction
    //@param : data updated data 
    //@param : model - which model data should be updated
    updateAPI(data, model) {

        let buyer = this.state.buyerDataInfo;
        let property = this.state.propertyDataInfo;
        let price = this.state.priceDataInfo;
        let camp = this.state.campDataInfo;
        let trans = this.state.transactionDataInfo;
        let campAll = this.state.campContingencies;

        // console.log("campAll",data)
        // console.log("transactionDataInfo",trans)
        
        for (let i = 0; i < campAll.length; i++) {
            campAll[i].startDate = trans.startDate;
            campAll[i].endDate = (campAll[i].endDate > trans.closeDate) ? trans.closeDate : campAll[i].endDate;
        }

        if (model === "buyer") {
            buyer = data;
        }
        if (model === "property") {
            //zipcode
            // data.zipcode = data.zipcode.replace("-","")
            property = data;
        }
        if (model === "price") {
            price = data;
        }
        if (model === "camp") {
            camp = data;
        }
        if (model === "trans") {
            trans = data;
        }
        let transactData = {
            "property": property,
            "buyer": buyer,
            "emd": price.emd,
            "agreedOpenPrice": price.agreedOpenPrice,
            "draft": false,
            "hoa": camp.hoa,
            "condo": camp.condo,
            "builtBefore1978": camp.builtBefore1978,
            "leadSource": camp.leadSource,
            "transactionType": trans.transactionType,
            "startDate": trans.startDate,
            "endDate": trans.closeDate,
            "campaign": trans.campaign,
            "contingencies": campAll,
            "updatedKeys": this.state.updatedKeys
        }

        if (model === 'trans') {

            if(trans.campaign !== data.campaign) {
                let campAllNew = this.state.newcampContingencies;
                for (let i = 0; i < campAllNew.length; i++) {
                    campAllNew[i].startDate = trans.startDate;
                    campAllNew[i].endDate = (campAllNew[i].endDate > trans.closeDate) ? trans.closeDate : campAllNew[i].endDate;
                }

                transactData.contingencies = campAllNew
            }

        }

       

        if (model === "status") {
            transactData.status = data.status;
        }

        if (model === "images") {
            for (let i = 0; i < data.length; i++) {
                property.image.push(data[i]);
            }
            transactData.property = property;
        }
        if (model === "delImage") {
            var imagesAr = [];

            imagesAr = property.image.filter((e) => {
                return !(e == data)
            })
            // for(let i=0;i<property.image.length;i++) {
            //     if(property.image[i] != data) {
            //         imagesAr.push(property.image[i]);
            //     }
            // } 
            property.image = imagesAr
            transactData.property = property;
        }

        if (model === "conti-up") {
            transactData.contingencies = data;
        }

        if (transactData.hoa) {
            //if hoa checked then update conti
            let isexi = false;
            for (let i = 0; i < transactData.contingencies.length; i++) {
                if (transactData.contingencies[i].name === "HOA Docs") {
                    isexi = true;
                }
            }
            if (!isexi) {
                transactData.contingencies.push({
                    endDate: transactData.startDate,
                    name: "HOA Docs",
                    startDate: transactData.closeDate,
                })
            }
        } else {
            //if hoa removed
            let updatedCont = [];
            for (let i = 0; i < transactData.contingencies.length; i++) {
                if (transactData.contingencies[i].name !== "HOA Docs") {
                    updatedCont.push(transactData.contingencies[i])
                }
            }
            transactData.contingencies = updatedCont
        }

        if(transactData.campaign == "") {
            delete transactData.campaign
        }

        // if(transactData.property !== undefined) {
        //     transactData.property.zipcode = transactData.property.zipcode.replace(/\-/g, "")
        // }

        if(transactData.property) {
            let zip = transactData.property.zipcode.toString();                    
           
            if(zip.length>5) {
                let zipcode = transactData.property.zipcode.replace(/\-/g, "")
               
                transactData.property.zipcode = zipcode;
            }                    
        }


        // console.log("final post",transactData)

        this.props.updateTransactionDataAction(this.state.transactionId, transactData).then(res => {
            // console.log(res)
            if (res !== undefined && res !== null) {

                if(res.property) {
                    let zip = res.property.zipcode.toString();                    
                   
                    if(zip.length>5) {
                        let a = zip.slice(0, 5)
                        let b = zip.slice(5, 9)
                        let zipcode = a +"-" + b
                        res.property.zipcode = zipcode;
                    }                    
                }

                let data = res;
                this.setState({
                    propertyInfo: false,
                    buyerInfo: false,
                    agreedInfo: false,
                    transactionInfo: false,
                    campaignInfo: false,
                    allTransdata: data,
                    buyerDataInfo: res.buyer,
                    propertyDataInfo: res.property,
                    priceDataInfo: {
                        agreedOpenPrice: res.agreedOpenPrice,
                        emd: res.emd
                    },
                    transactionDataInfo: {
                        transactionType: res.transactionType,
                        campaign: res.campaign,
                        startDate: res.startDate,
                        closeDate: res.endDate
                    },
                    campDataInfo: {
                        leadSource: res.leadSource,
                        condo: res.condo,
                        builtBefore1978: res.builtBefore1978,
                        hoa: res.hoa
                    },
                    campContingencies: res.contingencies
                });

                //update data to parent component -Details.jsx
                this.props.isDataUpdate(this.props.updatedTransData.property)
                this.props.isTransactionUpdate(this.props.updatedTransData)
            }
        });

    }


    formatNumber(num) {
        let newFor = num.toFixed(2);
        return (newFor.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,'));
    }
      


