// icons
import { Archive, KeyboardArrowDown, KeyboardArrowRight, MoreVert, Search } from '@material-ui/icons';
import React from 'react';
// Bootstrap components
import { Button, Col, Collapse, Dropdown, Form, Modal, Row } from 'react-bootstrap';
import { connect } from 'react-redux';
import { addDocAction, listDocAction, updateDocAction, uploadDocumentAction, removeDocAction } from './../../actions/DocActions';
import folderIcon from './../../assets/img/folder.svg';
import noRecord from './../../assets/img/no-record.png';
// import pages
import DocumentsDetail from './DocumentsDetail';
var _ = require('lodash');

class Documents extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			showArchieve: true,
			show: false,
			OpenDocumentDetail: false,
			folders: {},
			originalFolders: {},
			folderName: '',
			selectedFolderName: '',
			showLoader: false,
			renameFolder: false,
			documents: [],
			searchParam: '',
			userRole: localStorage.getItem('usertype').toUpperCase()
		};
	}

	// archieve accordion
	showArchieve() {
		this.setState({ showArchieve: !this.state.showArchieve });
	}

	// add folder modal
	setModalOpen(rename) {
		if (rename) {
			this.setState({ folderName: rename, renameFolder: true, searchParam: '' });
		} else {
			this.setState({ folderName: '' })
		}
		this.setState({ show: !this.state.show });
	}

	// Document Detail modal
	OpenDocumentDetail() {
		if (window.innerWidth < 768) {
			this.setState({
				OpenDocumentDetail: !this.state.OpenDocumentDetail
			});
		}
	}

	componentWillMount() {
		this.getAllFolders(0, 'first');
		// Object.keys(this.state.folders).forEach((e)=>{
		// 	if(e == 'Closing Documents' ){

		// 	}
		// })

	}

	showLoader(visible) {
		this.setState({ showLoader: visible });
	}

	getAllFolders(index, type) {
		this.showLoader(true);
		this.props.listDocAction(this.props.transactionId).then(res => {
			var folders = [];

			Object.keys(res).sort().forEach(function (key) {
				folders[key] = res[key];
			});
			this.setState({ folders, originalFolders: folders }, () => {
				this.showLoader(false);				
				if (type == 'archive' || type == 'add') {
					this.selectFolder(index)
				} else if (type == 'first') {
					if (Object.keys(this.state.folders).length > 0) {
						this.selectFolder(Object.keys(this.state.folders)[index], false, true);
					} else{
						this.selectFolder('Closing Documents', false, true);
					}
				}
			});
		});
	}

	createNewFolder(name) {

		this.showLoader(true);
		var dataToPost = {
			transactionId: this.props.transactionId,
			location: name,
			document: ''
		};
		if (this.state.renameFolder) {
			var data = {
				TransactionId: this.props.transactionId,
				location: this.state.selectedFolderName,
				newLocation: name,
				isDelete: false,
			};

			this.props.removeDocAction(data).then(res => {
				this.getAllFolders(this.state.selectedFolderName, 'add');
				this.setModalOpen();
				this.showLoader(false);
			});
			this.setState({ renameFolder: false }, () => {
				this.selectFolder(name)
			});
		} else {
			this.props.addDocAction(dataToPost).then(res => {
				this.getAllFolders(name, 'add');
				this.setModalOpen();
				this.showLoader(false);
			});
		}

	}

	archiveFolder(name) {
		var dataToPost = {
			TransactionId: this.props.transactionId,
			location: name,
			newLocation: name,
			isDelete: true,
		};
		this.props.removeDocAction(dataToPost).then(res => {
			this.getAllFolders(name, 'archive');
			this.showLoader(false);
		});
	}

	selectFolder(name, hideNewDocument, isFirst) {
		var documents = [];
		var id = '';
		if (hideNewDocument) {
			if (name == 'Archived Docs') {
				Object.keys(this.state.folders).forEach(e => {
					console.log(this.state.folders[e])
					documents = _.concat(documents, (this.state.folders[e] || []).filter(item => {
						return item.isDelete == false && item.isArchive == true;
					}));
				});
			} else {
				Object.keys(this.state.folders).forEach(e => {
					if (e == name) {
						documents = (this.state.folders[e] || []).filter(item => {
							return item.isDelete == true && item.isArchive == false;
						});
					}
				});
			}

		} else {
			Object.keys(this.state.folders).forEach(e => {
				if (e == name) {
					documents = (this.state.folders[e] || []).filter(item => {
						return item.isArchive == true ? false : true;
					});
				}
			});
		}
		_.remove(documents, e => {
			return e.document == undefined || e.document == '';
		});

		if(!isFirst){
			this.OpenDocumentDetail()
		}
		this.setState({ selectedFolderName: name, folderId: id || '', documents, hideNewDocument });
	}

	uploadNewDocument(e) {
		var documents = [];
		// Object.keys(this.state.folders).forEach(e => {
		// 	if (e == this.state.selectedFolderName) {
		// 		documents = this.state.folders[e][0].document;
		// 	}
		// });


		this.showLoader(true);
		if (e.length > 0) {
			var formData = new FormData();
			for (var i = 0; i < e.length; i++) {
				documents.push(e[i].name)
				formData.append('file', e[i])
			}

			this.props.uploadDocumentAction(formData, this.props.transactionId).then(res => {
				this.showLoader(false);
				documents.forEach((doc) => {
					var dataToPost = {
						transactionId: this.props.transactionId,
						location: this.state.selectedFolderName,
						document: doc
					};
					let id = '';
					Object.keys(this.state.folders).forEach(e => {
						if (e == this.state.selectedFolderName) {
							id = this.state.folders[e][0]._id;
							// dataToPost.document = this.state.folders[e][0].document;
						}
					});
					this.showLoader(true);
					this.props.addDocAction(dataToPost).then(res => {
						this.showLoader(false);
						this.getAllFolders(this.state.selectedFolderName, 'add');
					});
				});
			});



		} else {
			this.showLoader(false);
			alert('No files Selected');
		}
	}

	searchDocumentByName(name) {
		var folders = this.state.originalFolders;
		var filteredFolders = {};
		folders = Object.keys(folders).filter((e) => {
			var documents = [];

			documents = folders[e].filter((doc) => {
				if (doc.document) {
					return (doc.document + '').toLowerCase().includes((name + '').trim().toLowerCase())
				}
				return false;
			})


			// if (documents.length > 0 || (e.toLowerCase()).includes((name + '').trim().toLowerCase())) {
				// console.log(documents)
				// var f = {}
				// Object.keys(folders[e]).forEach(fd=>{
				// 	f[fd]= folders[e][0][fd]
				// })
				// f.document = documents
				filteredFolders[e] = documents;
			// }

			return false
		})
		console.log(filteredFolders);
		this.setState({ folders: filteredFolders, searchParam: name }, () => {
			console.log('folders', this.state.folders, this.state.originalFolders)
			var documents = []
			// Object.keys(this.state.folders).forEach(e => {
			// 	this.state.folders[e].forEach(item=>{
			// 		documents = _.concat(documents, (item.document || []).filter(item=>{
			// 			return item.isArchive;
			// 		}));
			// 	})
			// })	
			if (Object.keys(this.state.folders).length > 0) {
				this.selectFolder(documents.length > 0 ? 'Archived Docs' : Object.keys(this.state.folders)[0]);
			}
			else {
				this.selectFolder('');
			}
		})
	}

	refreshDocuments(res) {
		this.getAllFolders(this.state.selectedFolderName, 'archive');
	}

	render() {
		return (
			<>
				{this.state.showLoader && (
					<div className="page-loader">
						<div className="page-loader-inner">
							<div className="spinner-border" role="status">
								<span className="sr-only">Loading...</span>
							</div>
						</div>
					</div>
				)}
				<div className="document-page">
					<Row className="ml-0 mr-0 page-head-row page-head-row-inner align-items-center mt-10">

						<Col className="doc-name">
							<h4 className="font-18">Documents</h4>
						</Col>
						<Col className="doc-search">
							<div className="icon-wrapper">
								<Form.Control
									type="text"
									className="form-control-head"
									placeholder="Search by document/folder name"
									value={this.state.searchParam}
									onChange={(e) => {
										this.searchDocumentByName(e.target.value)
									}}
								/>
								<span className="form-control-icn">
									<Search />
								</span>
							</div>
						</Col>
						<Col align="right" className="doc-black"></Col>
					</Row>

					<div className="container-custom admin-content-area">
						<div className="doc-row pt-2">
							<div className="doc-col">
								<Row className="mx-0 py-1 align-items-center">
									<Col>
										<h4 className="font-16 m-0">
											Document Folder
										</h4>
									</Col>
									<Col
										align="right"
										className="col-auto pl-0 pr-1"
									>
										{(this.state.userRole === "VENDOR")
											? "" :

											<Button
												variant="primary"
												size="xs"
												className="btn-outlined btn-blue-light h-auto px-2 font-14"
												onClick={() => this.setModalOpen()}
											>
												+ New Folder
										</Button>
										}
									</Col>
								</Row>

								{/* folder listing */}
								<div className="folder-list-wrapper">
									<div className="folder-list folder">
										{/* {(Object.keys(this.state.folders).filter((e)=>{ return e == "Closing Documents"}).length > 0) && */}
										<Row className={'mx-0 align-items-center folder-single' + (this.state.selectedFolderName == 'Closing Documents' ? ' active' : '')} onClick={() => { this.selectFolder('Closing Documents'); }} key={'Closing Documents'}>
											<Col className="col-auto pr-0">
												<img src={folderIcon} alt="folder" style={{ width: '30px' }}
												/>
											</Col>
											<Col>
												<h4 className="color-blue-3 font-16 mb-0">
													Closing Documents <em>(default folder)</em>
												</h4>
											</Col>
											{/* <Col className="col-auto px-0">
												<Dropdown
													alignRight
													className="dropdown-action"
												>
													<Dropdown.Toggle
														variant="transparent"
														id="dropdown-folder2"
														className="p-0"
													>
														<MoreVert />
													</Dropdown.Toggle>

													<Dropdown.Menu>
														<Dropdown.Item onClick={()=>{this.setModalOpen('Closing Documents')}}>
															Rename
														</Dropdown.Item>
														<Dropdown.Item onClick={()=>{this.archiveFolder('Closing Documents')}}
															className="color-dark-orange-3"
														>
															Archive
														</Dropdown.Item>
													</Dropdown.Menu>
												</Dropdown>
											</Col> */}
										</Row>
										{/* } */}

										{Object.keys(this.state.folders).filter((e) => {
											var found = false;
											this.state.folders[e].forEach((f) => {
												if (!(f.isDelete)) {
													found = true;
												}
											})
											return found
										}).map(
											name => {
												if (name != 'Closing Documents') {
													return (
														<Row
															className={
																'mx-0 align-items-center folder-single' +
																(this.state.selectedFolderName == name ? ' active' : '')
															}
															onClick={() => {
																this.selectFolder(
																	name
																);
															}}
															key={name}
														>
															<Col className="col-auto pr-0">
																<img
																	src={folderIcon}
																	alt="folder"
																	style={{
																		width:
																			'30px'
																	}}
																/>
															</Col>
															<Col>
																<h4 className="color-blue-3 font-16 mb-0">
																	{name}
																</h4>
															</Col>
															<Col className="col-auto px-0">
																<Dropdown
																	alignRight
																	className="dropdown-action"
																>
																	<Dropdown.Toggle
																		variant="transparent"
																		id="dropdown-folder2"
																		className="p-0"
																	>
																		<MoreVert />
																	</Dropdown.Toggle>

																	<Dropdown.Menu>
																		<Dropdown.Item onClick={() => { this.setModalOpen(name) }}>
																			Rename
																	</Dropdown.Item>
																		{localStorage.getItem('usertype') != 'VENDOR' &&
																			<Dropdown.Item onClick={() => { this.archiveFolder(name) }}
																				className="color-dark-orange-3"
																			>
																				Archive
																		</Dropdown.Item>
																		}
																	</Dropdown.Menu>
																</Dropdown>
															</Col>
														</Row>
													);
												}
												else {
													return ''
												}
											}
										)}
									</div>

									{(this.state.userRole === "VENDOR" || this.state.userRole === "BUYER" )
										? "" :

										<div className="folder-list archieve">
											<Row
												className="mx-0 align-items-center folder-single archieve-acc-title"
												onClick={() => this.showArchieve()}
												aria-controls="example-collapse-text"
												aria-expanded={
													this.state.showArchieve
												}
											>
												<Col className="col-auto pr-0">
													<Archive className="color-grey-1" />
												</Col>
												<Col>
													<h4 className="font-16 mb-0 font-400">
														Archive
													 {/* <em>({Object.keys(this.state.folders).filter((e)=>{return this.state.folders[e][0].isArchive || false}).length + 1})</em> */}
													</h4>
												</Col>
												<Col className="col-auto px-0">
													<KeyboardArrowRight className="KeyboardArrowRight" />
													<KeyboardArrowDown className="KeyboardArrowDown" />
												</Col>
											</Row>

											{/* archieve folders toggle */}
											<Collapse in={this.state.showArchieve}>
												<div className="folder-list folder">
													<Row className={
														'mx-0 align-items-center folder-single' +
														(this.state.selectedFolderName == 'Archived Docs' ? ' active' : '')
													}
														onClick={() => { this.selectFolder('Archived Docs', true); }}
														key={'Archived Docs'}>
														<Col className="col-auto pr-0">
															<img
																src={folderIcon}
																alt="folder"
																style={{
																	width:
																		'30px'
																}}
															/>
														</Col>
														<Col>
															<h4 className="color-blue-3 font-16 mb-0">
																Archived Docs
													</h4>
														</Col>
													</Row>
													{Object.keys(this.state.folders).filter((e) => {
														var found = false;
														this.state.folders[e].forEach((f) => {
															if (f.isDelete) {
																found = true;
															}
														})
														return found
													}).map(name => {
														if (name != 'Closing Documents') {
															return (
																<Row
																	className={
																		'mx-0 align-items-center folder-single' +
																		(this.state.selectedFolderName == name ? ' active' : '')
																	}
																	onClick={() => { this.selectFolder(name, true); }}
																	key={name}
																>
																	<Col className="col-auto pr-0">
																		<img
																			src={folderIcon}
																			alt="folder"
																			style={{
																				width:
																					'30px'
																			}}
																		/>
																	</Col>
																	<Col>
																		<h4 className="color-blue-3 font-16 mb-0">
																			{name}
																		</h4>
																	</Col>
																	{/* <Col className="col-auto px-0">
																		<Dropdown alignRight className="dropdown-action" >
																			<Dropdown.Toggle
																				variant="transparent"
																				id="dropdown-folder2"
																				className="p-0"
																			>
																				<MoreVert />
																			</Dropdown.Toggle>

																			<Dropdown.Menu>
																				<Dropdown.Item onClick={() => { this.setModalOpen(name) }}>
																					Rename
																			</Dropdown.Item>
																			<Dropdown.Item onClick={()=>{this.archiveFolder(name)}} className="color-dark-orange-3" >
																				Archive
																			</Dropdown.Item>
																			</Dropdown.Menu>
																		</Dropdown>
																	</Col> */}
																</Row>
															);
														}
														else {
															return ''
														}
													}
													)}
												</div>
											</Collapse>

										</div>
									}
								</div>
							</div>

							{/* folder detail */}
							<div className="doc-col col-full">
								{/* {Object.keys(this.state.folders).length <= 0 ?
									<div className="no-transaction no-record">
										<div className="no-transaction-inner">
											<img src={noRecord} alt="no record" />
											<p>No Folders...</p>
										</div>
									</div>
								: */}
								<DocumentsDetail
									folderName={this.state.selectedFolderName}
									transactionId={
										this.props.transactionId || ''
									}
									documents={this.state.documents}
									uploadNewDocument={files => {
										this.uploadNewDocument(files);
									}}
									archiveDocument={(res) => { this.refreshDocuments(res); }}
									folderId={this.state.folderId}
									hideNewDocument={this.state.hideNewDocument}
								/>
								{/* } */}
							</div>
						</div>
					</div>

					<Modal
						show={this.state.show}
						onHide={() => this.setModalOpen()}
						dialogClassName="modal-90w"
						aria-labelledby="example-custom-modal-styling-title"
						className="modal-med"
					>
						<Modal.Header closeButton>
							<Modal.Title id="example-custom-modal-styling-title">
								Create Folder
							</Modal.Title>
						</Modal.Header>
						<Modal.Body>
							<Form className="form-sm form-admin">
								<Form.Group controlId="" className="mb-3">
									<Form.Control
										type="text"
										value={this.state.folderName}
										onChange={e => {
											this.setState({
												folderName: e.target.value
											});
										}}
									/>
								</Form.Group>
								<Button
									type="submit"
									variant="primary"
									size="sm"
									disabled={this.state.folderName == '' ? true : false}
									className="btn-blue w-140"
									onClick={() => {
										this.createNewFolder(this.state.folderName);
									}}
								>
									{this.state.renameFolder ? 'Rename' : 'Create'}
								</Button>
							</Form>
						</Modal.Body>
					</Modal>

					<Modal
						show={this.state.OpenDocumentDetail}
						onHide={() => this.OpenDocumentDetail()}
						dialogClassName="modal-90w"
						aria-labelledby="example-custom-modal-styling-title"
						className="modal-med modal-view-page"
					>
						<Modal.Header closeButton></Modal.Header>
						<Modal.Body>
							<DocumentsDetail
								folderName={this.state.selectedFolderName}
								folderId={this.state.folderId}
								transactionId={this.props.transactionId || ''}
								documents={this.state.documents}
								uploadNewDocument={files => {
									this.uploadNewDocument(files);
								}}
								archiveDocument={(res) => { this.refreshDocuments(res); }}
								hideNewDocument={this.state.hideNewDocument}
							/>
						</Modal.Body>
					</Modal>
				</div>
			</>
		);
	}
}

const mapStateToProps = state => {
	return {
		docs: state.reducer.docs,
		totalDocs: state.reducer.totalDocs
	};
};

export default connect(mapStateToProps, { listDocAction, addDocAction, updateDocAction, uploadDocumentAction, removeDocAction })(Documents);
