    handleSubmit(values, taskId) {
        var val = values;
        val.communicationTo = [val.communicationTo ? (val.communicationTo[0] + '').replace(/\{\(/g, "").replace(/\)\}/g, "") : '']
        val.communicationSubject = [val.communicationSubject ? (val.communicationSubject[0] + '').replace(/\{\(/g, "").replace(/\)\}/g, "") : '']
        this.setState({ showLoader: true })
        this.props.updateTaskAction(val, taskId).then(res => {
            this.setState({ showLoader: false })
        })
    }

