    handleDateChange(date, type, setFieldValue, values) {
        var taskData = this.state.taskData;
        var dateStr = moment(date).format('YYYY-MM-DD');
        var timeStr = moment(date).format('HH:mm');


        if (type == 'time') {
            dateStr = moment(values.eventEndDate).format('YYYY-MM-DD')
        }

        var finalDate = moment(dateStr + ' ' + timeStr);

        setFieldValue('eventEndDate', new Date(finalDate.format('YYYY-MM-DD HH:mm')))

        this.setState({
            taskData
        });
    }

    setFiles(files) {
        var acceptedFiles = this.state.acceptedFiles;
        acceptedFiles = _.uniqBy(_.concat(acceptedFiles, files), 'name');

        this.setState({
            acceptedFiles
        })
    }

    deleteDocument(i) {
        var acceptedFiles = this.state.acceptedFiles;
        acceptedFiles.splice(i, 1);
        this.setState({ acceptedFiles })
    }

