//All action about user defined 

import * as types from './actions';

import APIUtil from '../config/APIUtil';
import UrlConstants from '../config/UrlConstants';

/*
 * action creators
 */

/**
 *  
 * @desc List Buyer Agents API call
 */
export function adminGetMaterTemplateAction() {  
    return function (dispatch){
      return APIUtil.getMethod(UrlConstants.AdminGetMaterTemplateUrl,true).then(response => {

          if(response.status === 200) {
              dispatch( {
                    type: types.ADMIN_GET_MASTER_TEMPLATE_DATA,
                    adminMasterTemplates: response.data.data,
                    totalAdminTemplates : response.data.total
              });
          }
          else {
              dispatch( {
                  type: types.ADMIN_GET_MASTER_TEMPLATE_DATA,
                  adminMasterTemplates: {},
                  totalAdminTemplates : 0
              });
          }
      });
    }  
}
/**
 *  
 * @desc List dependency Tasks API call
 */
export function GetDependencytasksAction(campaign, day, event, module) {
    let url = '';
    return function (dispatch){
        if(module == 'ADMIN'){
            url = `${UrlConstants.setDependency}campaign=${campaign}&day=${day}&eventId=${event}&isTransaction=false`
        }
        if(module == 'USER'){
            url = `${UrlConstants.setDependency}campaign=${campaign}&day=${day}&eventId=${event}`
        }
        if(module == 'TRANSACTION'){
            url = `${UrlConstants.setDependency}day=${day}&transactionId=${campaign}&eventId=${event}`
        }
      return APIUtil.getMethod(url ,true).then(response => {
          if(response.status === 200) {
              dispatch( {
                    type: types.GET_DEPENDENCY_DATA,
                    dependencyTasks: response.data,
              });
          }
          else {
              dispatch( {
                  type: types.GET_DEPENDENCY_DATA,
                  dependencyTasks: {}
              });
          }
          return response;
      });
    }  
}
/**
 *  
 * @desc List dependency Tasks API call
 */
export function putDependencytasksAction(event, module, apidata) {
    let url = '';
    return function (dispatch){
        if(module == 'ADMIN'){
            url = `${UrlConstants.setDependency}eventId=${event}&isTransaction=false`
        }
        if(module == 'BUYERAGENT'){
            url = `${UrlConstants.setDependency}eventId=${event}`
        }
        if(module == 'TRANSACTION'){
            url = `${UrlConstants.setDependency}eventId=${event}&isTransaction=true`
        }
      return APIUtil.putMethod(url ,apidata, true).then(response => {
          if(response.status === 200) {
              dispatch( {
                    type: types.PUT_DEPENDENCY_DATA,
                    dependencyTasks: response,
                    status: true,
                    statusMessage: 'Dependencies Added Successfully',
              });
          }
          else {
              dispatch( {
                  type: types.PUT_DEPENDENCY_DATA,
                  dependencyTasks: {},
                  status: false,
                  statusMessage: 'Something went Wrong',
              });
          }
          return response;
      });
    }  
}
/**
 * 
 * @desc Update master template data API call
 */
export function adminUpdateMaterTemplateAction(data,model,campaignId, eventTempId, commuId, type) {  

    var url = UrlConstants.AdminUpdateBothMaterTemplateUrl;
    if(model === "campaign") {
        url = UrlConstants.AdminUpdateCampMaterTemplateUrl.replace("{model}",model);
        url = url.replace("{campaignId}",campaignId)
    }

    if(model === "both") {
        url = UrlConstants.AdminUpdateBothMaterTemplateUrl.replace("{eventTempId}",eventTempId);
        if(type === 0) {
            url = url.replace("{commuId}",commuId)
        } else {
            //remove commuid from url
            url = url.replace("&communicationtemplateId={commuId}",'')
        }
        
    }

    return function (dispatch){
      return APIUtil.putMethod(url,data,true).then(response => {
        
          if(response.status === 200) {
              dispatch( {
                    type: types.ADMIN_UPDATE_MASTER_TEMPLATE_DATA,
                    isMasterTemplateUpdated : true,
                    updatedEventData : response.data
              });
              return response.data;
          }
          else {
              dispatch( {
                  type: types.ADMIN_UPDATE_MASTER_TEMPLATE_DATA,
                  isMasterTemplateUpdated : false,
                  updatedEventData : []
              });
              return response.data;
          }
      });
    }  
}

