/**
 *  
 * @desc List dependency Tasks API call
 */
export function deleteDependencytasksAction(event, module, apidata) {
    let url = '';
    return function (dispatch){
        if(module == 'ADMIN'){
            url = `${UrlConstants.setDependency}id=${event}&isTransaction=false&removeId=${apidata}`
        }
        if(module == 'BUYERAGENT'){
            url = `${UrlConstants.setDependency}id=${event}&removeId=${apidata}`
        }
        if(module == 'TRANSACTION'){
            url = `${UrlConstants.setDependency}id=${event}&isTransaction=true&removeId=${apidata}`
        }
      return APIUtil.deleteMethod(url, true).then(response => {
          if(response.status === 200) {
              dispatch( {
                    type: types.PUT_DEPENDENCY_DATA,
                    dependencyTasks: response,
                    status: true,
                    statusMessage: 'Dependencies Added Successfully',
              });
          }
          else {
              dispatch( {
                  type: types.PUT_DEPENDENCY_DATA,
                  dependencyTasks: {},
                  status: false,
                  statusMessage: 'Something went Wrong',
              });
          }
          return response;
      });
    }  
}
