import React from "react";

// MultiStep Library
import StepZilla from "react-stepzilla";

// steps pages
import PropertyInfo from "./PropertyInfo";
import AssignVendor from "./AssignVendor";
// import LenderInfo from "./LenderInfo";

// bootstrap components
import { Button, Col, Modal, Row } from "react-bootstrap";
import { Add, Close, DateRange, Edit, Delete, HelpOutline,AlternateEmail } from '@material-ui/icons';

import moment from 'moment';

// icons

import { Link } from 'react-router-dom'
import { connect } from 'react-redux'
import { createTransactionAction } from './../../../actions/BuyerAgentMasterTemplateActions'

import $ from 'jquery';

import APIUtil from './../../../config/APIUtil'
import UrlConstants from './../../../config/UrlConstants';


class CreateTransaction extends React.Component {


  

defaultState ={
  "condo": false,
  "builtBefore1978" : false,
  "hoa" : false,
  "draft": false,
  "leadSource": "",
  "emd": "",
  "agreedOpenPrice": "",
  "transactionType": "Purchase",
  "property": {
      "image": [],
      "name": "",
      "street": "",
      "street1": "",
      "city": "",
      "zipcode": "",
      "state" : ""
  },
  "buyer": {
      "email": "",
      "firstname": "",
      "lastName": "",
      "mobileNumber": ""
  },
  "startDate": moment(),
    "endDate": moment(),
  "campaign": "",
  "contingencies": [],
  "isShowPrompt" : false,
  "promptMsg" : ""
};

// defaultState ={
//   "condo": false,
//   "builtBefore1978" : false,
//   "hoa" : false,
//   "draft": false,
//   "leadSource": "a",
//   "emd": "12.12",
//   "agreedOpenPrice": "5000.00",
//   "transactionType": "Purchase",
//   "property": {
//       "image": [],
//       "name": "",
//       "street": "test",
//       "street1": "aa",
//       "city": "aa",
//       "zipcode": "45641",
//       "state" : "AL"
//   },
//   "buyer": {
//       "email": "a@a.com",
//       "firstname": "a",
//       "lastName": "a",
//       "mobileNumber": "123-456-7890"
//   },
//   "startDate": moment(),
//   "endDate": moment(),
//   "campaign": "",
//   "contingencies": [],
//   "isCampErrMsg": false,
//   "isShowPrompt" : false,
//   "promptMsg" : ""
// };

  constructor(props) {
    super(props);
    this.state = this.defaultState
  }


  getTransactionData() {
    return this.state;
  }

  updateTransactionData(field,value) {
  
    if(field === "emd") {
      this.state.emd = value
    }
    if(field === "agreedOpenPrice") {
      this.state.agreedOpenPrice = value
    }
    if(field === "leadSource") {
      this.state.leadSource = value
    }
    if(field === "transactionType") {
      this.state.transactionType = value
    }

    if(field === "condo") {
      this.state.condo = value
    }
    if(field === "builtBefore1978") {
      this.state.builtBefore1978 = value
    }
    if(field === "hoa") {
      this.state.hoa = value
    }

    if(field === "campaign") {
      this.state.campaign = value
    }
    if(field === "startDate") {
      // this.state.startDate = value
      this.setState({startDate : value})
    }
    if(field === "endDate") {
      // this.state.endDate = value
      this.setState({endDate : value})
    }
    if(field === "contingencies") {
      // console.log("parent val",value)
      //this.state.contingencies = value
      this.setState({contingencies : value})
    }    

    //Property fields
    if(field === "property_street") {
       this.state.property.street = value
    }
    if(field === "property_street1") {
      this.state.property.street1 = value
    }
    if(field === "property_city") {
      this.state.property.city = value
    }
    if(field === "property_state") {
      this.state.property.state = value
    }
    if(field === "property_zipcode") {
      this.state.property.zipcode = value
    }
    if(field === "property_image") {
      this.state.property.image = value
    }

    //Buyer fields
    if(field === "buyer_firstname") {
      this.state.buyer.firstname = value
    }
    if(field === "buyer_lastName") {
      this.state.buyer.lastName = value
    }
    if(field === "buyer_email") {
      this.state.buyer.email = value
    }
    if(field === "buyer_mobileNumber") {
      this.state.buyer.mobileNumber = value
    }
    
  }

  getLastStep(step) {
    
    if(step === 0) {
      $(".saveBtn").html('Next')
    }

    if(step === 1) {
      $(".saveBtn").html('Confirm & Save')
    }
    
    if(step === 2) {         
      this.showPrompt();
    }
   }

   isShowPrompt() {
    this.setState({isShowPrompt:!this.state.isShowPrompt});
   }

   showPrompt() {
      if(this.state.campaign === "" && this.state.contingencies.length === 0) {
        //show warning
        this.setState({ promptMsg : "You are trying to create transaction without any template or contingency, hence you have to add all the tasks manually. Are you sure want to continue?"});
        this.setState({isShowPrompt:true});
      }
      if(this.state.campaign === "" && this.state.contingencies.length > 0) {
        //show warning
        this.setState({ promptMsg : "You are trying to create transaction without any template or contingency, hence you have to add all the tasks manually. Are you sure want to continue?"});
        this.setState({isShowPrompt:true});
      }
      if(this.state.campaign !== "" && this.state.contingencies.length === 0) {
        //show warning
        this.setState({ promptMsg : "You are trying to create transaction without any template or contingency, hence you have to add all the tasks manually. Are you sure want to continue?"});
        this.setState({isShowPrompt:true});
      }

      if(this.state.campaign !== "" && this.state.contingencies.length > 0){      
        this.setState({isShowPrompt:false,promptMsg : ""});
        this.submitAction()
      }
      

   }

   okPrompt() {
    this.setState({isShowPrompt:false,promptMsg : ""});
    this.submitAction()
   }

   submitAction() {
      let data = this.state;
      // if(this.state.campaign !== "") {
        this.setState({isCampErrMsg:false});
        delete data.isCampErrMsg;
        // contingency
          let c = this.state.contingencies;
          let finalInputCont = [];
          for(let i=0; i<c.length;i++) {

              if(c[i].assignee !== "" && c[i].assignee !== undefined && c[i].assignee != 'no') {
                finalInputCont.push( {
                  name: c[i].name,
                  startDate : new Date(c[i].startDate),
                  endDate : new Date(c[i].endDate),
                  assignee : c[i].assignee,
                })
              } else {
                finalInputCont.push( {
                  name: c[i].name,
                  startDate : new Date(c[i].startDate),
                  endDate : new Date(c[i].endDate),
                //  assignee : c[i].assignee,
                })
              }
              
          }
         
          data.contingencies = finalInputCont;

          data.startDate = new Date(data.startDate);
          data.endDate = new Date(data.endDate);
          
          data.buyer.mobileNumber = data.buyer.mobileNumber.replace(/\-/g, "")
          data.property.zipcode = data.property.zipcode.replace(/\-/g, "")

          if(data.campaign === "") {
            delete data.campaign;
          }

          //zipcode
          // data.property.zipcode = data.property.zipcode.replace("-","")

          if(data.property) {
            let zip = data.property.zipcode.toString();                    
            
              if(zip.length>5) {
                  let zipcode = data.property.zipcode.replace(/\-/g, "")                
                  data.property.zipcode = zipcode;
              }                    
          }

          var fileNames = [];
          var formData = new FormData();
          data.property.image.forEach(file=>{
            if(file){
              formData.append('file', file)
              fileNames.push(file.name)
            }
          });

          // console.log("adsf",data)
          if(data.property.image.length > 0){
            APIUtil.postFormDataMethod(UrlConstants.uploadPropertyImage, formData,true).then(res=>{
              data.property.image = fileNames;
              if(res.status == 200){
                this.props.createTransactionAction(data).then(res => {
                  if(this.props.isTrascationCreated === true) {
                    this.setState(this.defaultState);
                    this.props.history.push("/transactions")             
                  }
                });
              } else {
                data.property.image = []
                this.props.createTransactionAction(data).then(res => {
                  if(this.props.isTrascationCreated === true) {
                    this.setState(this.defaultState);
                    this.props.history.push("/transactions")             
                  }
                });
              }
              
            });
          } else{        
            data.property.image = []
            this.props.createTransactionAction(data).then(res => {
              if(this.props.isTrascationCreated === true) {
                this.setState(this.defaultState);
                this.props.history.push("/transactions")             
              }
            });
          }
         
          
        
      // } else {       
      //   this.setState({isCampErrMsg:true})
      //   return false;
      // }
  }

  updateErrMsg(i) {
    if(i == 0) {
      this.setState({isCampErrMsg:false})
    } else {
      this.setState({isCampErrMsg:true})
    }
   
  }

  render() {


    const steps =
      [
        {name: 'Property Information', component: <PropertyInfo  getTransactionData={() => (this.getTransactionData())} updateTransactionData={(k,v) => {this.updateTransactionData(k,v)}} />},
        {name: 'Select Campaign', component: <AssignVendor getTransactionData={() => (this.getTransactionData())} updateTransactionData={(k,v) => {this.updateTransactionData(k,v)}} updateErrMsg={(i)=>this.updateErrMsg(i)}/>},
        // {name: 'Lender Information', component: <LenderInfo />},
      ]


    return (
      <div className='step-progress transaction-create'>
          <Row className="ml-0 mr-0 page-head-row page-head-row-big align-items-center">
              <Col xs={12} sm={3}>
                  <h4 className="title-med mt-0 pt-1">Create Transactions</h4>
              </Col>
              
              <Col xs={12} sm={6}>
                  
              </Col>

              <Col xs={12} sm={3} align="right" className="transaction-close">
                  <Link to='/transactions' className="nav-link p-0 color-black">
                    <Close style={{ stroke: "#fff", height: "40px", width: "40px"}}/>
                  </Link>
              </Col>
          </Row>
          <div className="multi-step multi-step-admin">
            <StepZilla 
              showNavigation={true} 
              steps={steps}
              backButtonCls="btn btn-sm btn-primary btn-blue btn-outlined w-140 mr-3"
              nextButtonCls="btn btn-sm btn-primary btn-blue w-140 saveBtn"
              backButtonText= "Cancel"
             // nextButtonText= "Save & Next"
              onStepChange = {(step) => this.getLastStep(step)}
              hocValidationAppliedTo = {[0]}
              preventEnterSubmission= {true}
            />
          </div>

           {/* Popup Modal */}
           <Modal
                show={this.state.isShowPrompt}
                // onHide={() => this.isShowPrompt()}
                dialogClassName="modal-90w"
                aria-labelledby="example-custom-modal-styling-title"
                className="modal-med modal-400 modal-delete"
                centered
                backdrop={false}
            >
              <Modal.Body className="pb-0 text-center">
                <HelpOutline/>
                <p>{this.state.promptMsg}</p>
              </Modal.Body>
              <Modal.Footer>

                  <Button variant='primary' className="btn-blue btn-outlined w-140"  onClick={() => {this.isShowPrompt()}}>Cancel</Button>
                  <Button variant='primary' className="btn-blue w-140" onClick={()=>{this.okPrompt()}}>Yes</Button>
              </Modal.Footer>
          </Modal>
      </div>
    );
  }

}

const mapStateToProps = state => {
  return {  
    isTrascationCreated: state.reducer.isTrascationCreated,
    createTransactionData: state.reducer.createTransactionData
  };
};



export default connect(mapStateToProps, {createTransactionAction})(CreateTransaction)
