import React from 'react'

// bootstrap components
import { Row, Col, Button, Alert } from 'react-bootstrap'

import ReactSelect from 'react-select'
import { connect } from 'react-redux'

import { Field, Form, Formik, ErrorMessage } from 'formik'

import { putDependencytasksAction, deleteDependencytasksAction } from './../../../actions/MasterTemplateActions'
import moment from 'moment';

const colourOptions = [
  {
    value: 'Auto Text to Buyer’s 9 a.m.',
    label: 'Auto Text to Buyer’s 9 a.m.',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'Radon Inspector has to upload document',
    label: 'Radon Inspector has to upload document',
    color: '#0052CC'
  },
  {
    value: 'CLIENT Auto Text to Buyer’s 9 a.m.',
    label: 'CLIENT Auto Text to Buyer’s 9 a.m.',
    color: '#5243AA'
  },
  { value: 'TEXT TO BUYER:', label: 'TEXT TO BUYER:', color: '#FF5630' },
  { value: 'EMAIL (client)', label: 'EMAIL (client)', color: '#FF8B00' },
  {
    value: 'Auto Text to Septic:',
    label: 'Auto Text to Septic:',
    color: '#FFC400'
  },
  {
    value: 'TO DO for Agent, TC and/or Admin',
    label: 'TO DO for Agent, TC and/or Admin',
    color: '#36B37E'
  },
  {
    value: 'Auto email to Home Inspector:',
    label: 'Auto email to Home Inspector:',
    color: '#00875A'
  }
]

class SetDependency extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      optionlist: [],
      fromTransaction: false,
      showLoader: true,
      statusMessage: '',
      status: false
    }
  }

  handleChange = (values) => {
    this.setState({ values })
  }
  addDependencies() {
    this.setState({ showLoader: true })
    let selectedTasks = []
    // if(this.state.values){
    selectedTasks = this.state.values ? this.state.values.map((obj) => obj.value) : []
    let apidata = {
      predecessor: selectedTasks
    }
   
    let eventID = this.props.eventId
    localStorage.getItem('usertype')
    let ROLE = localStorage.getItem('usertype')
    if (!this.state.fromTransaction) {
      
      this.props.putDependencytasksAction(eventID, ROLE, apidata).then((res) => {
          // alert(this.props.statusMessage);
          setTimeout(
            function() {
              // this.setState({showLoader: false})
              this.props.dependencies(this.state.values)
            }.bind(this),
            1000
          )
        })

        if(selectedTasks.length < this.props.predecessors.length){

          let removeID = [];

          this.props.predecessors.forEach((e, i)=>{
            console.log(e)
            var found = false
            selectedTasks.forEach((f, i)=>{
              if( e._id == f){
                found = true
              }
              
            })
            if(!found){
              removeID.push(e._id)
            }
          })

          var data = '';
          removeID.forEach((fd, i)=>{
            if(i == 0){
              data += fd
            } else {
              data += ','+fd;
            }
          })

          this.props.deleteDependencytasksAction(eventID, ROLE, data);
        }
        


    } else {
      ROLE = 'TRANSACTION'
      if(selectedTasks.length < this.props.predecessors.length){

        let removeID = [];

        this.props.predecessors.forEach((e, i)=>{
          console.log(e)
          var found = false
          selectedTasks.forEach((f, i)=>{
            if( e._id == f){
              found = true
            }
            
          })
          if(!found){
            removeID.push(e._id)
          }
        })

        var data = '';
        removeID.forEach((fd, i)=>{
          if(i == 0){
            data += fd
          } else {
            data += ','+fd;
          }
        })

        this.props.deleteDependencytasksAction(eventID, ROLE, data);
      }
      this.props.putDependencytasksAction(eventID, ROLE, apidata).then((res) => {
          // alert(this.props.statusMessage);
          setTimeout(
            function() {
              // this.setState({showLoader: false})
              this.props.dependencies(this.state.values)
            }.bind(this),
            1000
          )
        })
    }

    //  }

    setTimeout(
      function() {
        this.setState({ status: false })
      }.bind(this),
      3000
    )
  }
  componentDidMount() {
    this.setState({ showLoader: true })
    if (this.props.actiontaken === 'UPDATE') {
      if (this.props.predecessors.length > 0) {
        let predecessorArray = this.props.predecessors

        let predecessorArrayList = predecessorArray.map((obj) => {
          if (obj !== null) {
            return {
              label: obj.hasOwnProperty('eventEndDate') ? `${moment(obj.eventEndDate).format('MM-DD-YYYY H:mm a')} -  ${obj.title}`  : `Day ${obj.day} - ${obj.title}`,
              value: obj._id,
              day: obj.day
            }
          }
        })
        this.setState({ values: predecessorArrayList, showLoader: false })
      }
    }
    let arrayss = this.props.options
    let resultarray = arrayss.map((obj) => {
        if (obj !== null) {
            return {
              label: obj.hasOwnProperty('eventEndDate') ? `${moment(obj.eventEndDate).format('MM-DD-YYYY H:mm a')} -  ${obj.title}`  : `Day ${obj.day} - ${obj.title}`,
              value: obj._id,
              day: obj.day
            }
        }
      
    })
    this.setState({
      optionlist: resultarray,
      fromTransaction: this.props.fromTransaction
        ? this.props.fromTransaction
        : false,
      btnName: this.props.actiontaken,
      showLoader: false
    })
  }
  componentWillReceiveProps(nextProps) {
    this.setState(nextProps)
    this.setState({ showLoader: false })
  }
  render() {
    return (
      <Form>
        {this.state.showLoader === true && (
          <div className="page-loader">
            <div className="page-loader-inner">
              <div className="spinner-border" role="status">
                <span className="sr-only">Loading...</span>
              </div>
            </div>
          </div>
        )}
        {this.state.statusMessage != null && this.state.status ? (
          <Alert variant="success">{this.state.statusMessage}</Alert>
        ) : (
          ''
        )}
        <div className="dependency-select">
          <ReactSelect
            isClearable={false}
            closeMenuOnSelect={false}
            placeholder=""
            // defaultValue={[colourOptions[0], colourOptions[1]]}
            isMulti
            options={this.state.optionlist}
            className="react-select-wrapper react-select-multi"
            onChange={this.handleChange}
            value={this.state.values}
            // styles={colourStyles}
          />
        </div>
        <div className="mt-2 text-right">
          <Button
            variant="primary"
            size="sm"
            className="btn-blue"
            onClick={() => this.addDependencies()}
            style={{ width: '85px' }}
          >
            {this.state.btnName}
          </Button>
          {/* <Button variant="primary" size="sm" className="btn-blue btn-outlined w-140 ml-3">Cancel</Button> */}
        </div>
      </Form>
    )
  }
}
const mapStateToProps = (state) => {
  return {
    statusMessage: state.reducer.statusMessage,
    status: state.reducer.status
  }
}
export default connect(
  mapStateToProps,
  {
    putDependencytasksAction,deleteDependencytasksAction
  }
)(SetDependency)
