//All action about user defined 

import * as types from './actions';

import APIUtil from '../config/APIUtil';
import UrlConstants from '../config/UrlConstants';

var _ = require('lodash');


/**
 * @desc Transactions - get All
 */
export function getBuyerTransactionsAction() {  

    let vendorId = localStorage.getItem("ID")
   
    let url = UrlConstants.buyerTransactionsList;
    return function (dispatch){
      return APIUtil.getMethod(url, true).then(response => {
          // dispatch
          if(response.status == 200) {
              dispatch( {
                  type: types.GET_ALL_BUYER_TRANSACTIONS,
                  buyerTransactions: response.data
              });
          }
          else {
              dispatch( {
                  type: types.GET_ALL_BUYER_TRANSACTIONS,
                  buyerTransactions: []
              });
          }
      });
    }  
}

export function getBuyerTasksAction(transactionId) {

    let url = UrlConstants.buyerTasksUrl + transactionId;

    return function (dispatch){
      return APIUtil.getMethod(url, true).then(response => {
          // dispatch
          if(response.status == 200) {
              dispatch( {
                  type: types.GET_ALL_BUYER_TASKS,
                  buyerTasks: response.data
              });
          }
          else {
              dispatch( {
                  type: types.GET_ALL_BUYER_TASKS,
                  buyerTasks: []
              });
          }
      });
    }  
}
