import React from "react";

// Bootstrap components 
import { Form, Row, Col, InputGroup } from "react-bootstrap";

// icons
import DateRange from '@material-ui/icons/DateRange';

// date picker
import DateRangePicker from 'react-bootstrap-daterangepicker';
import moment from 'moment';
import 'bootstrap-daterangepicker/daterangepicker.css'

import PropTypes from 'prop-types';
import validation from 'react-validation-mixin';
import strategy from 'joi-validation-strategy';
import Joi from 'joi';

import APIUtil from './../../../config/APIUtil'
import UrlConstants from './../../../config/UrlConstants'
import InputMask from 'react-input-mask';


var todayDate = new Date();
var minDate = (todayDate.getMonth()+1)+'/'+todayDate.getDate()+'/'+todayDate.getFullYear();

class PersonalInfo extends React.Component {

    constructor(props) {
        super(props);
    
        this.setStartDate = this.setStartDate.bind(this);
    
        this.state = {
          startDate: props.getUserData().licenseExpirationDate,// moment(),

          firstname : props.getUserData().firstname,
          lastname : props.getUserData().lastname,
          email: props.getUserData().email,
          recoveryEmail :  props.getUserData().recoveryEmail,
          password : props.getUserData().password,
          confirmPassword : props.getUserData().confirmPassword,
          mobileNumber : props.getUserData().mobileNumber,
          otherNumber : props.getUserData().otherNumber,
          brokerLicenseNo : props.getUserData().brokerLicenseNo,
          licenseExpirationDate : props.getUserData().licenseExpirationDate,
          knowAboutUs : props.getUserData().knowAboutUs,
          isEmailExist : false,
        };

        this.validatorTypes = function() {
            var base = Joi.object().keys({
                email: Joi.string().email().required().label("Email ID"),
                firstname : Joi.string().required().label("First Name"),
                lastname : Joi.string().required().label("Last Name"),
                recoveryEmail : Joi.string().email().required().label("Confirm Email ID"),
                recoveryEmail: Joi.ref('email'),
                password : Joi.string().min(6).max(30).required().label("Password"),
                confirmPassword : Joi.string().min(6).max(30).required(),
                confirmPassword: Joi.ref('password'),
                mobileNumber : Joi.string().regex(new RegExp("^[0-9]{3}-[0-9]{3}-[0-9]{4}?$")).required().label("Mobile No"),
                otherNumber : Joi.string().allow('').regex(new RegExp("^[0-9]{3}-[0-9]{3}-[0-9]{4}?$")).label("Phone No. (Office)"),
                brokerLicenseNo : Joi.string().required().label("Broker License No"),
               // licenseExpirationDate : Joi.date().required().label("License Expiration Date"),
                //knowAboutUs : Joi.string().required().label(""),
            });

            return base;
            
        };
      
        this.getValidatorData = this.getValidatorData.bind(this);
        this.renderHelpText = this.renderHelpText.bind(this);
        this.isValidated = this.isValidated.bind(this);
    }

    getValidatorData() {
        return {
            firstname : this.props.getUserData().firstname,
            lastname : this.props.getUserData().lastname,
            email: this.props.getUserData().email,
            recoveryEmail : this.props.getUserData().recoveryEmail,
            password : this.props.getUserData().password,
            confirmPassword : this.props.getUserData().confirmPassword,
            mobileNumber : this.props.getUserData().mobileNumber,
            otherNumber : this.props.getUserData().otherNumber,
            brokerLicenseNo : this.props.getUserData().brokerLicenseNo,
            licenseExpirationDate : this.props.getUserData().licenseExpirationDate,
            knowAboutUs : this.props.getUserData().knowAboutUs
        }
    };

    isValidated() {
        
        return new Promise((resolve, reject) => {
          this.props.validate((error) => {
             
            if (error || this.state.isEmailExist) {
              reject(); // form contains errors
              return;
            }
           
            resolve();
            
          });
        });
      }

    renderHelpText(message, id) {
        return (<div className="val-err-tooltip" key={id}><span>{message}</span></div>);
    }

      onChange (event) {
        let val = event.target.value;
       // console.log("event.target.name",event.target.name)
        // if(event.target.name === "mobileNumber" ) {
        //     if(/^[0-9\b]+$/.test(val)) {
        //         val = val;
        //        // console.log("val",val)
        //     } else {
        //         val = "";
        //     }
           
        // }
        if(event.target.name === "email" ) {
            this.checkEmailExist(val);
        }

        this.setState({[event.target.name] : val})

        this.props.updateUserData(event.target.name, val);        
      }

      setStartDate(event, picker) {
        this.setState({
          startDate: picker.startDate.format('MM/DD/YYYY'),
        });
        let selDate = picker.startDate.format('MM/DD/YYYY');
        this.props.updateUserData('licenseExpirationDate',selDate);
      }


      getErrorMsg(field, errsList){
        var err = '';

        // console.log(errsList)

        if(field === "firstname" || field === 'lastname' || field === 'brokerLicenseNo') {
            if(errsList[field] !== undefined && errsList[field] !== null && errsList[field] !== "") {
                if(field === "firstname") {
                    err = 'Please enter First Name';
                }
                if(field === "lastname") {
                    err = 'Please enter Last Name';
                }
                if(field === "brokerLicenseNo") {
                    err = 'Please enter Broker License No';
                }
            }
        }
        if(field === 'email') {            
            if(errsList[field] !== undefined && errsList[field] !== null && errsList[field] !== "") {
                if(errsList[field].length === 2){
                    err = 'Please enter Email';
                }
                if(errsList[field].length === 1){
                    err = 'Please enter valid Email address';
                }
            }
        }

        if(field === 'password') {            
            if(errsList[field] !== undefined && errsList[field] !== null && errsList[field] !== "") {
                if(errsList[field].length === 2){
                    err = 'Please enter Password';
                }
                if(errsList[field].length === 1){
                    err = 'Password should be of minimum 6 character';
                }
            }
        }
        if(field === 'confirmPassword') {            
            if(errsList[field] !== undefined && errsList[field] !== null && errsList[field] !== "") {
                if(errsList[field].length === 2){
                    err = 'Confirm Password required';
                }
                if(errsList[field].length === 1){
                    err = 'Confirm Password should be same as Password entered';
                }
            }
        }

        if(field === 'recoveryEmail') {            
            if(errsList[field] !== undefined && errsList[field] !== null && errsList[field] !== "") {
                if(errsList[field].length === 2){
                    err = 'Please enter Confirm email';
                }
                if(errsList[field].length === 1){
                    err = 'Confirm Email Id should be same as Email Id';
                }
            }
        }

        if(field === 'mobileNumber') {            
            if(errsList[field] !== undefined && errsList[field] !== null && errsList[field] !== "") {
                if(errsList[field].length === 2){
                    err = 'Please enter Mobile No';
                }
                if(errsList[field].length === 1){
                    err = 'Mobile No Must be of 10 digits';
                }
            }
        }

        if(field === 'otherNumber') {            
            if(errsList[field] !== undefined && errsList[field] !== null && errsList[field] !== "") {
                if(errsList[field].length === 2){
                    err = 'Please enter Phone No';
                }
                if(errsList[field].length === 1){
                    err = 'Phone No Must be of 10 digits';
                }
            }
        }
            return err;
      }


    checkEmailExist(email) {
        APIUtil.getMethod(UrlConstants.emailCheckUrl+"?email="+email, false).then(response => {

            if(response.data === true) {
                this.setState({isEmailExist : true});
            }
            else {
                this.setState({isEmailExist : false});
            }
            
        });
    }

    render() {

        // let startDate = (this.state.licenseExpirationDate !== undefined && this.state.licenseExpirationDate != '') ? this.state.licenseExpirationDate : this.state.startDate.format('MM/DD/YYYY');
        let startDate = this.state.startDate//.format('MM/DD/YYYY')

        return (
            <div className="step-content">
                <h4 className="title-blue mb-3">Personal Information</h4>
                <Form className="form-sm">

                    <Row>
                        <Col xs={12} sm={6}>
                            <Form.Group controlId="">
                                <Form.Label>First Name<span className="text-danger">*</span></Form.Label>
                                <Form.Control maxLength="50" defaultValue = {this.state.firstname}
                  onChange={this.onChange.bind(this)} name = "firstname" type="text" size='lg' onBlur={this.props.handleValidation('firstname')}/>


<Form.Text className="text-danger">
 {this.renderHelpText(this.getErrorMsg('firstname', this.props.errors))}

    </Form.Text>
                   

                            </Form.Group>
                        </Col>
                        <Col xs={12} sm={6}>
                            <Form.Group controlId="">
                                <Form.Label>Last Name<span className="text-danger">*</span></Form.Label>
                                <Form.Control maxLength="50" defaultValue = {this.state.lastname}
                  onChange={this.onChange.bind(this)} name="lastname" type="text" size='lg' onBlur={this.props.handleValidation('lastname')} />
<Form.Text className="text-danger">
 {this.renderHelpText(this.getErrorMsg('lastname', this.props.errors))}

</Form.Text>
                            
                            </Form.Group>
                        </Col>
                    </Row>

                    <Row>
                        <Col xs={12} sm={6}>
                            <Form.Group controlId="">
                                <Form.Label>Email ID<span className="text-danger">*</span></Form.Label>
                                <Form.Control maxLength="50" name = "email" defaultValue = {this.state.email}
                  onChange={this.onChange.bind(this)} type="email" size='lg' onBlur={this.props.handleValidation('email')} />
                               <Form.Text className="text-danger">                               
                               {this.renderHelpText(this.getErrorMsg('email', this.props.errors))}
                               </Form.Text> 
                               
                               {
                                   (this.state.isEmailExist) &&
                                    <Form.Text className="text-danger"> 
                                    {this.renderHelpText("Email address already exist.")}
                                    </Form.Text> 
                               }

                            </Form.Group>
                        </Col>
                        <Col xs={12} sm={6}>
                            <Form.Group controlId="">
                                <Form.Label>Confirm Email ID<span className="text-danger">*</span></Form.Label>
                                <Form.Control maxLength="50" name = "recoveryEmail" defaultValue = {this.state.recoveryEmail} 
                  onChange={this.onChange.bind(this)} type="email" size='lg' onBlur={this.props.handleValidation('recoveryEmail')}/>
                  <Form.Text className="text-danger">
                  {this.renderHelpText(this.getErrorMsg('recoveryEmail', this.props.errors))}
                  </Form.Text>
                  
                            </Form.Group>
                        </Col>
                    </Row>

                    <Row>
                        <Col xs={12} sm={6}>
                            <Form.Group controlId="">
                                <Form.Label>Create Password<span className="text-danger">*</span></Form.Label>
                                <Form.Control maxLength="50" defaultValue = {this.state.password}
                  onChange={this.onChange.bind(this)} name = "password" type="password" size='lg' onBlur={this.props.handleValidation('password')}/>
                  <Form.Text className="text-danger">
                  {this.renderHelpText(this.getErrorMsg('password', this.props.errors))}
                  </Form.Text>
                
                            </Form.Group>
                        </Col>
                        <Col xs={12} sm={6}>
                            <Form.Group controlId="">
                                <Form.Label>Confirm Password<span className="text-danger">*</span></Form.Label>
                                <Form.Control maxLength="50" defaultValue = {this.state.confirmPassword}
                  onChange={this.onChange.bind(this)} name = "confirmPassword" type="password" size='lg' onBlur={this.props.handleValidation('confirmPassword')} />
                  <Form.Text className="text-danger">
{this.renderHelpText(this.getErrorMsg('confirmPassword', this.props.errors))}                  </Form.Text>
                  
                            </Form.Group>
                        </Col>
                    </Row>

                    <Row>
                        <Col xs={12} sm={6}>
                            <Form.Group controlId="">
                                <Form.Label>Mobile No.<span className="text-danger">*</span></Form.Label>
                                <InputMask 
                                    mask="999-999-9999" 
                                    maskChar=''
                                    formControlProps={{
                                        fullWidth: true,
                                    }} 
                                    className="form-control" 
                                    value = {this.state.mobileNumber}
                                    name="mobileNumber" 
                                    onChange={this.onChange.bind(this)}
                                    // onChange={(e)=> { this.setMobile(e.target.value,'mobileNumber', setFieldValue)}}
                                    >
                                    {(inputProps) =>
                                    <Form.Control   {...inputProps}/>
                                    }
                                </InputMask> 
                                {/* <Form.Control maxLength="10" defaultValue = {this.state.mobileNumber}
                                onChange={this.onChange.bind(this)} name = "mobileNumber" type="number" size='lg' onBlur={this.props.handleValidation('mobileNumber')} /> */}
                                    <Form.Text className="text-danger">
                                    {this.renderHelpText(this.getErrorMsg('mobileNumber', this.props.errors))} 
                                    </Form.Text>
                            </Form.Group>
                        </Col>
                        <Col xs={12} sm={6}>
                            <Form.Group controlId="">
                                <Form.Label>Phone No. (Office)</Form.Label>
                                <InputMask 
                                    mask="999-999-9999" 
                                    maskChar=''
                                    formControlProps={{
                                        fullWidth: true,
                                    }} 
                                    className="form-control" 
                                    value = {this.state.otherNumber}
                                    name="otherNumber" 
                                    onChange={this.onChange.bind(this)}
                                    // onChange={(e)=> { this.setMobile(e.target.value,'mobileNumber', setFieldValue)}}
                                    >
                                    {(inputProps) =>
                                    <Form.Control   {...inputProps}/>
                                    }
                                </InputMask>  
                                {/* <Form.Control maxLength="10" defaultValue = {this.state.otherNumber}
                                    onChange={this.onChange.bind(this)} name="otherNumber" type="number" size='lg' /> */}
                                <Form.Text className="text-danger">
                                    {this.renderHelpText(this.getErrorMsg('otherNumber', this.props.errors))} 
                                </Form.Text>
                            </Form.Group>
                        </Col>
                    </Row>
                    
                    <Row>
                        <Col xs={12} sm={8}>
                            <Form.Group controlId="">
                                <Form.Label>Broker License No.<span className="text-danger">*</span></Form.Label>
                                <Form.Control maxLength="50" defaultValue = {this.state.brokerLicenseNo}
                  onChange={this.onChange.bind(this)} name="brokerLicenseNo" type="text" size='lg' onBlur={this.props.handleValidation('brokerLicenseNo')} />
                  <Form.Text className="text-danger">
                  {this.renderHelpText(this.getErrorMsg('brokerLicenseNo', this.props.errors))}
                  </Form.Text>
                  
                            </Form.Group>
                        </Col>
                        <Col xs={12} sm={4}>
                            <Form.Group controlId="">
                                <Form.Label>License Expiration Date<span className="text-danger">*</span></Form.Label>
                                <InputGroup className='icon-wrapper blue-wrapper'>
                                    <DateRangePicker
                                        singleDatePicker
                                        startDate={this.state.startDate}
                                        onEvent={this.setStartDate}    
                                        minDate={minDate}                                    
                                    >
                                        <Form.Control  name="licenseExpirationDate" type="text" size='lg' placeholder="MM/DD/YYYY" value={startDate} />
                                        <span className='form-control-icn'>
                                            <DateRange/>
                                        </span>
                                    </DateRangePicker>
                                </InputGroup>
                                <Form.Text className="text-danger">
                                
                                {this.renderHelpText(this.getErrorMsg('licenseExpirationDate', this.props.errors))} 
                                </Form.Text>
                                
                            </Form.Group>
                        </Col>
                    </Row>

                    <Row>
                        <Col xs={12} sm={8}>
                            <Form.Group controlId="">
                                <Form.Label>How you know about us?</Form.Label>
                                <div className="c-select">
                                    <select onChange={this.onChange.bind(this)} defaultValue={this.state.knowAboutUs} name="knowAboutUs" className="form-control form-control-lg">
                                        <option value="">Select</option>
                                        <option value="Advertisement" >Advertisement</option>
                                    </select>
                                </div>
                            </Form.Group>
                        </Col>
                    </Row>

          </Form>
      </div >
        );
    }

}


//export default PersonalInfo;


PersonalInfo.propTypes = {
    errors: PropTypes.object,
    validate: PropTypes.func,
    isValid: PropTypes.func,
    handleValidation: PropTypes.func,
    getValidationMessages: PropTypes.func,
    clearValidations: PropTypes.func,
    getUserData: PropTypes.func,
    updateUserData: PropTypes.func
  };
  
  export default validation(strategy)(PersonalInfo);
