
import React, { Component } from "react";

// Bootstrap components
import { Row, Col, Form, InputGroup } from "react-bootstrap";

// icons
import {DateRange } from '@material-ui/icons';

// date picker
import DateRangePicker from 'react-bootstrap-daterangepicker';
import moment from 'moment';
import 'bootstrap-daterangepicker/daterangepicker.css'

import UpdateContingencies from './../Detail/UpdateContingencies'

import Contingencies from './Contingencies'




import PropTypes from 'prop-types';
import validation from 'react-validation-mixin';
import strategy from 'joi-validation-strategy';


import { connect } from 'react-redux'
import { getBuyerAgentMasterTemplateAction } from './../../../actions/BuyerAgentMasterTemplateActions'

import $ from 'jquery'
import { th } from "date-fns/locale";
var _ = require('lodash');

var todayDate = new Date();
var minDate = (todayDate.getMonth()+1)+'/'+todayDate.getDate()+'/'+todayDate.getFullYear();

class AssignVendor extends Component {
  constructor(props) {
    super(props);

    this.setStartDate = this.setStartDate.bind(this);
    this.setEndDate = this.setEndDate.bind(this);
    this.contStartDate = this.contStartDate.bind(this);
    this.contDueDate = this.contDueDate.bind(this);

    this.state = {
      show:false,
      startDate: moment(),
      closeDate: moment().subtract(-29, 'days'),
      contStartDate: moment().subtract(-29, 'days'),
      contDueDate: moment().subtract(-29, 'days'),
      activeContigency: true,
      inactiveContiClass: '',
      addVendor: false,
      campaignStyle :[],
      vendors : [],
      campContingencies : [],   
      setVendors : [],  
    };
  }


  setStartDate(event, picker) {
    
    this.setState({
      startDate: picker.startDate,
    },() => {
      this.props.updateTransactionData("startDate", picker.startDate.format('YYYY-MM-DD'));   
    });

    let selectedcamp = this.props.getTransactionData().campaign;
    let camDays = 0;
    for (var i = 0, len = this.state.campaignStyle.length; i < len; i++) {
        let camp = this.state.campaignStyle[i];
        if (camp._id === selectedcamp) {
            camDays = camp.campaignDays;
        }
    }
   
    let edate = moment(this.state.startDate).subtract(-camDays, 'days');

    this.setState({
      closeDate : edate
    },() => {
      this.props.updateTransactionData("endDate", moment(edate).format('YYYY-MM-DD')); 
    });

  }

  setEndDate(event, picker) {
    this.setState({
      closeDate: picker.startDate,
    });

    this.props.updateTransactionData("endDate", picker.startDate.format('YYYY-MM-DD'));
  }

  setModalOpen(){
    this.setState({show:!this.state.show});
  }

  // contigency dates
  contStartDate(event, picker) {
    this.setState({
      contStartDate: picker.startDate,
    });
  }

  contDueDate(event, picker) {
    this.setState({
      contDueDate: picker.startDate,
    });
  }

  activeContigency(i){
    if(i == 1){
      this.setState({activeContigency:!this.state.activeContigency});
      this.setState({inactiveContiClass : 'inactive'});
    }
    if(i == 2){
      this.setState({activeContigency:!this.state.activeContigency});
      this.setState({inactiveContiClass : ''});
    }
  }

  addVendor(){
    this.setState({addVendor:!this.state.addVendor});
  }



  componentDidMount() {    

    this.props.getBuyerAgentMasterTemplateAction().then(res => {        
      this.setState({campaignStyle : this.props.buyerMasterTemplates});

        let selectedcamp = "";
        let campContingencies = [];
        let campDays = 0;
        for (var i = 0, len = this.state.campaignStyle.length; i < len; i++) {
          let camp = this.state.campaignStyle[i];      
          if(camp.campaignDays == 30) {
            campContingencies = camp.contingencies
            campDays =  camp.campaignDays -1
            selectedcamp = camp._id
          }
        }

        let vendors = this.state.setVendors;

        //set start date and end ddate
        let sdate =  moment()
        let edate = moment().subtract(-campDays, 'days');

        let contingencies = [];
        for (var key in campContingencies) {

            if (campContingencies.hasOwnProperty(key)) {

              let conti = key;
              let item = {"name":key, "startDate": sdate.format('YYYY-MM-DD'),
              'endDate' : edate.format('YYYY-MM-DD'), "assignee" : ""}

                if(conti === "Home Inspection" || conti === "Radon Inspection" || 
                conti === "Septic Inspection" || conti === "Wood Destroying Inspection") {

                  if(vendors[conti] !== undefined && vendors[conti] !== null) {      
                    let vendorsData = (vendors[conti].data !== undefined && vendors[conti].data !== null) ? vendors[conti].data : [];
                    for (var i = 0, len = vendorsData.length; i < len; i++) {
              
                      if(vendorsData[i].default) {
                      item.assignee = vendorsData[i].contactId
                      } 
                    }
                  }
                }

                contingencies.push(item);
            } 

        }
       
        this.setState({ startDate : sdate, closeDate : edate,campContingencies : contingencies});

        this.props.updateTransactionData("campaign", selectedcamp);

        this.props.updateTransactionData("contingencies", contingencies);

        this.props.updateTransactionData("startDate", moment(sdate).format('YYYY-MM-DD'));
        this.props.updateTransactionData("endDate", moment(edate).format('YYYY-MM-DD'));

    });

  }


  updateTranConData(data) {

      // console.log("datasss --------", data)
      this.setState({campContingencies :data});
      this.props.updateTransactionData("contingencies", data);
      // if(!this.state.isInitialState) {
      //     this.updateAPI(data,'conti-up');
      // }
  }


  setContingencyForCamp(e) {
    //set contingencies
    let selectedcamp = e.target.value;

    if(selectedcamp != "select") {
      let campContingencies = [];
      let campDays = 0;
      for (var i = 0, len = this.state.campaignStyle.length; i < len; i++) {
        let camp = this.state.campaignStyle[i];      
        if(camp._id === selectedcamp) {
          campContingencies = camp.contingencies
          campDays =  camp.campaignDays -1
        }
      }


      let transcationAllData = this.props.getTransactionData();
      let vendors = this.state.setVendors;

      //set start date and end ddate
      let sdate =  moment()
      let edate = moment().subtract(-campDays, 'days');

      // console.log("sdate",sdate)
      // console.log("edate",edate)

      let contingencies = [];
      for (var key in campContingencies) {

          if (campContingencies.hasOwnProperty(key)) {

            let conti = key;
            let item = {"name":key, "startDate": sdate.format('YYYY-MM-DD'),
            'endDate' : edate.format('YYYY-MM-DD'), "assignee" : ""}

              if(conti === "Home Inspection" || conti === "Radon Inspection" || 
              conti === "Septic Inspection" || conti === "Wood Destroying Inspection") {

                if(vendors[conti] !== undefined && vendors[conti] !== null) {      
                  let vendorsData = (vendors[conti].data !== undefined && vendors[conti].data !== null) ? vendors[conti].data : [];
                  for (var i = 0, len = vendorsData.length; i < len; i++) {
            
                    if(vendorsData[i].default) {
                    item.assignee = vendorsData[i].contactId
                    } 
                  }
                }
              }

              contingencies.push(item);
          } 

      }
      this.setState({ startDate : sdate, closeDate : edate,campContingencies : contingencies});

      this.props.updateTransactionData("campaign", selectedcamp);

      this.props.updateTransactionData("contingencies", contingencies);

      this.props.updateTransactionData("startDate", moment(sdate).format('YYYY-MM-DD'));
      this.props.updateTransactionData("endDate", moment(edate).format('YYYY-MM-DD'));
    }
    else {
      this.setState({ campContingencies : []});
      this.props.updateTransactionData("campaign", "");
      this.props.updateTransactionData("contingencies", []);

    }
    

   // console.log("selectedcamp",selectedcamp)
    // if(selectedcamp == "") {
    //   this.props.updateErrMsg(1)
    // } else {
    //   this.props.updateErrMsg(0)
    // }

  }


  renderHelpText(message, id) {
    return (<div className="val-err-tooltip" key={id}><span>{message}</span></div>);
  }

  render() {

    let styleMsg = {"display" : "none"};
    if(this.props.getTransactionData().isCampErrMsg) {
      styleMsg = {"display" : "block"};
    }

    let startDate = this.state.startDate.format('MM/DD/YYYY');
    let closeDate = this.state.closeDate.format('MM/DD/YYYY');

    let campaignList = [];

		for (var i = 0, len = this.state.campaignStyle.length; i < len; i++) {
			let camp = this.state.campaignStyle[i];

			let data = <option key={camp._id} value={camp._id}>{camp.name}</option>
			campaignList.push(data);
    }
   
    return (
     <div className="admin-steps last-step admin-content-area campaign-step">
        <h2 className="color-blue-3 font-20">Select Campaign</h2>
        <div className="campaign-div">
          <Row>
            <Col xs={12} sm={10}>
              <form>
                <Row>
                  <Col xs={12} sm={4}>
                    <Form.Group controlId="">
                        <Form.Label className="font-500 color-grey-1">Campaign Style</Form.Label>
                        <div className="c-select campaign-dropdown">
                          <select name="campaign" className="form-control" 
                          onChange={(e)=> this.setContingencyForCamp(e)}
                          value = {this.props.getTransactionData().campaign} >
                            <option value="select">Select</option>
                            {campaignList}
                          </select>
                        </div>
                        {/* <Form.Text className="text-danger" style={styleMsg}>
                          {this.renderHelpText("Please select Campaign Style")}
                        </Form.Text> */}
                    </Form.Group>
                  </Col>
                  <Col xs={12} sm={4}>
                      <Form.Group controlId="">
                          <Form.Label className="font-500 color-grey-1">Contract/Escrow Date<span className="text-danger">*</span></Form.Label>
                          <InputGroup className='icon-wrapper'>
                              <DateRangePicker 
                                singleDatePicker
                                startDate={this.state.startDate}
                                onEvent={this.setStartDate}
                                minDate={minDate}   
                              >
                                  <Form.Control type="text" placeholder="MM/DD/YYYY" 
                                  value={this.state.startDate.format('MM/DD/YYYY')} />
                                  <span className='form-control-icn form-control-icn-sm'>
                                      <DateRange/>
                                  </span>
                              </DateRangePicker>
                          </InputGroup>
                      </Form.Group>
                  </Col>
                  <Col xs={12} sm={4}>
                      <Form.Group controlId="">
                          <Form.Label className="font-500 color-grey-1">Expected Closing Date​​​​​​​<span className="text-danger">*</span></Form.Label>
                          <InputGroup className='icon-wrapper'>
                              <DateRangePicker
                                singleDatePicker
                                startDate={this.state.closeDate}
                                onEvent={this.setEndDate}
                                minDate={this.state.startDate} 
                              >
                                  <Form.Control type="text" placeholder="MM/DD/YYYY" value={this.state.closeDate.format('MM/DD/YYYY')}/>
                                  <span className='form-control-icn form-control-icn-sm'>
                                      <DateRange/>
                                  </span>
                              </DateRangePicker>
                          </InputGroup>
                      </Form.Group>
                  </Col>
                </Row>
              </form>
            </Col>
          </Row>
        </div>

        <hr className="mt-1"/>

        <UpdateContingencies isMasterTemplate={false} 
        updateTransactionData={(k,v,isUp) => this.props.updateTransactionData(k,v,isUp)} 
        campContingencies={this.state.campContingencies} 
        getTransactionData={() => this.props.getTransactionData()}
        contiCamp ={(data) => this.updateTranConData(data)}
        setVendorsFun = {(data) => {this.setState({setVendors : data})}}
        />
      </div>
      
    );
  }
}

// export default AssignVendor;

AssignVendor.propTypes = {
  errors: PropTypes.object,
  validate: PropTypes.func,
  isValid: PropTypes.func,
  handleValidation: PropTypes.func,
  getValidationMessages: PropTypes.func,
  clearValidations: PropTypes.func,
  getTransactionData: PropTypes.func,
  updateTransactionData: PropTypes.func
};

// var AssignVendor1 = validation(strategy)(AssignVendor);


const mapStateToProps = state => {
	return {
		buyerMasterTemplates: state.reducer.buyerMasterTemplates,
    totalBuyerTemplates: state.reducer.totalBuyerTemplates,    
	};
};



export default validation(strategy) (connect(mapStateToProps, {
  getBuyerAgentMasterTemplateAction  
})(AssignVendor) )



