import React from "react";

// MultiStep Library
import StepZilla from "react-stepzilla";

// steps pages
import PersonalInfo from "./../Register/Steps/PersonalInfo";
import CompanyInfo from "./../Register/Steps/CompanyInfo";
import ReviewStep from "./../Register/Steps/Review";
//import CommissionStep from "./Steps/Commission";
// import LenderInfo from "./LenderInfo";

// icons
import { Close } from '@material-ui/icons';

import { Link } from 'react-router-dom'
import $ from 'jquery'

import { connect } from 'react-redux'
import { inviteAgentAction } from './../../actions/UserActions'

var today = new Date();
var date = (today.getMonth()+1)+'/'+today.getDate()+'/'+today.getFullYear();

class InviteAgent extends React.Component {


  defaultState = {
   usertype:"BUYERAGENT",
    firstname : '',
    lastname : '',
    email: '',
    recoveryEmail: '',
    password : '',
    confirmPassword : '',
    mobileNumber : '',
    otherNumber : '',
    brokerLicenseNo : '',
    licenseExpirationDate : date,
    knowAboutUs : '',
    companyInformation : {
      name : '',
      website : '',
      address : '',
      zipcode : '',
      city : '',
      state : '',
      phoneNumber : '',
      faxNumber : '',
      typeOfBusiness : '',
      companySize : ''
    },
    address: "",
    bio: "-",
    education: " ",
    certifications: "",
    awards: "",
    jobTitle: "",
    quote: "",
    avatar: "-",
    socialMediaHandles: {
        facebook: "",
        twitter: "",
        instagram: ""
    },
    education : [],
    certifications : [],
    awards : [],  
  };

  constructor(props) {
    super(props);
    this.state = this.defaultState
  }

  getUserData() {
    return this.state;
  }

  getUserData() {
    return this.state;
  }

  updateUserData(key,value) {
    this.setState({
        [key] :value
    })
  }

  updateUserCmpnyData(key,value) {
    this.state.companyInformation[key] = value;
  }

  componentWillReceiveProps(nextProps) {
    // console.log('componentWillReceiveProps', nextProps);
     this.setState(nextProps);
   }

   getLastStep(step) {

    if(step === 0 || step === 1) {
      $(".saveBtn").html('Next')
    }

    if(step === 2) {
      $(".saveBtn").html('Confirm & Save')
    }

    if(step === 3) {
      this.submitAction();
    }
   }

   submitAction() {
      let data = this.state;
      delete data.recoveryEmail;
      delete data.confirmPassword;

      data.mobileNumber = data.mobileNumber.replace(/\-/g, "")
      data.otherNumber = data.otherNumber.replace(/\-/g, "")
      data.companyInformation.phoneNumber = data.companyInformation.phoneNumber.replace(/\-/g, "")

      this.props.inviteAgentAction(data).then(res => {
        this.setState(this.defaultState);
      });
      
  }

  render() {

    const steps =
    [
      {name: 'Personal Info', component: <PersonalInfo getUserData={() => (this.getUserData())} updateUserData={(k,v) => {this.updateUserData(k,v)}} />},
      {name: 'Company Info', component: <CompanyInfo  getUserData={() => (this.getUserData())} updateUserData={(k,v) => {this.updateUserCmpnyData(k,v)}} />},
      {name: 'Review', component: <ReviewStep getUserData={() => (this.getUserData())} />}
    ]

    if(this.props.checkStatus) {
      this.props.history.push("/buyeragent")
    }

    return (
      <div className="reg-page agent-create">
        <div className='loginBg'>
          <div className='step-progress'>
              <div className="reg-title-wrapper">
                <h4 className="title-big mt-0 pt-2">Create Buyer Agent Account</h4>
                <Link to='/BuyerAgent' className="nav-link p-0 color-black close-link">
                  <Close style={{ stroke: "#fff", height: "40px", width: "40px"}}/>
                </Link>
              </div>
              <div className="multi-step">
                <StepZilla 
                  steps={steps} 
                  showNavigation={true}
                  backButtonCls="btn btn-sm btn-primary btn-blue btn-outlined w-140 mr-3"
                  nextButtonCls="btn btn-sm btn-primary btn-blue w-140 saveBtn"
                  onStepChange = {(step) => this.getLastStep(step)}
                  hocValidationAppliedTo = {[0,1]}
                />
              </div>
          </div>
        </div>
      </div>
    );
  }

}

const mapStateToProps = state => {
  return {  
    checkStatus: state.reducer.checkStatus,
    invieAgentMsg: state.reducer.invieAgentMsg
  };
};

export default connect(mapStateToProps, {inviteAgentAction})(InviteAgent)